import { PaginationInput } from 'src/API';

export enum EChatsActions {
  GetUserChatList = '[Chats] Get User ChatList',
  GetChatMessageList = '[Chats] Get Chat Message List',
  GetChatMessageListByUserId = '[Chats] Get Chat Message List By User Id',
  AddNextUserChatList = '[Chats] Add Next User Chat List',
  AddNextChatMessageList = '[Chats] Add Next Chat Message List',
  CreateMessage = '[Chats] Create Message',
  SetChatMessagesAsRead = '[Chats] Set Chat Messages As Read',
  ResetChatsState = '[Chats] Reset Chats State',
}

export class GetUserChatList {
  static readonly type = EChatsActions.GetUserChatList;
  constructor(public payload: PaginationInput) {}
}

export class GetChatMessageList {
  static readonly type = EChatsActions.GetChatMessageList;
  constructor(public payload: { id: string; input: PaginationInput }) {}
}

export class GetChatMessageListByUserId {
  static readonly type = EChatsActions.GetChatMessageListByUserId;
  constructor(public payload: { id: string; input: PaginationInput }) {}
}

export class AddNextUserChatList {
  static readonly type = EChatsActions.AddNextUserChatList;
  constructor(public payload: PaginationInput) {}
}

export class AddNextChatMessageList {
  static readonly type = EChatsActions.AddNextChatMessageList;
  constructor(public payload: { id: string; input: PaginationInput }) {}
}

export class CreateMessage {
  static readonly type = EChatsActions.CreateMessage;
  constructor(public payload: { receiverId: string; message: string }) {}
}

export class SetChatMessagesAsRead {
  static readonly type = EChatsActions.SetChatMessagesAsRead;
  constructor(public payload: string) {}
}

export class ResetChatsState {
  static readonly type = EChatsActions.ResetChatsState;
  constructor() {}
}
