import { Injectable } from '@angular/core';
import { API, graphqlOperation } from 'aws-amplify';

import {
  getPublicationById,
  getCommentsOfPublication,
  getLikesOfPublication,
  getPublisherPublications,
  getTimeline,
} from '../../graphql/queries';
import { PaginationInput } from '../../API';
import { createCommentToPublication, deletePublication } from '../../graphql/mutations';
import { changeLikeStatus, changeLikeCommentStatus } from 'src/graphql/mutations';

@Injectable({
  providedIn: 'root',
})
export class PublicationService {
  constructor() {}

  public getPublisherPublications(id: string, input: PaginationInput) {
    return API.graphql(graphqlOperation(getPublisherPublications, { id, input }));
  }

  public getTimeline(input: PaginationInput) {
    return API.graphql(graphqlOperation(getTimeline, { input }));
  }

  public createComment(id, comment) {
    const input = {
      content: comment,
    };
    return API.graphql(graphqlOperation(createCommentToPublication, { id, input }));
  }

  public changeLikeStatus(id: string, like: boolean) {
    return API.graphql(graphqlOperation(changeLikeStatus, { id, like }));
  }
  public changeLikeCommentStatus(id: string, like: boolean) {
    return API.graphql(graphqlOperation(changeLikeCommentStatus, { id, like }));
  }

  public getPublicationLikes(id: string) {
    return API.graphql(graphqlOperation(getLikesOfPublication, { id }));
  }

  public getCommentsOfPublication(id: string) {
    return API.graphql(graphqlOperation(getCommentsOfPublication, { id }));
  }

  public getPublication(id: string) {
    return API.graphql(graphqlOperation(getPublicationById, { id }));
  }

  public deletePublication(id: string) {
    return API.graphql(graphqlOperation(deletePublication, { id }));
  }
}
