import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { Constants } from '../../../@global/constants';

@Component({
  selector: 'app-image-chooser',
  templateUrl: './image-chooser.component.html',
  styleUrls: ['./image-chooser.component.scss'],
})
export class ImageChooserComponent implements OnInit {
  @Input() imagePath: string;
  @Output() actionButtonClick = new EventEmitter<void>();
  public iconName: string;

  constructor() {}

  ngOnInit() {
    this.iconName = this.imagePath === Constants.NO_PROFILE_IMAGE_PATH ? 'camera' : 'pencil';
  }

  public onActionButton(): void {
    this.actionButtonClick.emit();
  }
}
