import { Action, Selector, State, StateContext } from '@ngxs/store';
import { Injectable } from '@angular/core';
import {
  CloseSheetAction,
  OpenSheetAction,
  SetCurrentPublicationIdAction,
  SetSheetTypeAction,
} from './ion-sheet.actions';
import { IonSheetModel } from './ion-sheet.model';

@State({
  name: 'ionsheet',
  defaults: {
    open: false,
    likeItems: [],
    commentItems: [],
    type: 0,
    currentPublicationId: '',
  },
})
@Injectable()
export class IonSheetState {
  constructor() {}

  @Selector()
  static getIonSheetStatus(state: IonSheetModel): boolean {
    return state.open;
  }

  @Selector()
  static getSheetType(state: IonSheetModel): number {
    return state.type;
  }

  @Selector()
  static getCurrentPublicationId(state: IonSheetModel): string {
    return state.currentPublicationId;
  }

  @Action(OpenSheetAction)
  openSheet({ getState, patchState }: StateContext<IonSheetModel>): void {
    const state = getState();

    patchState({
      ...state,
      open: true,
    });
  }

  @Action(CloseSheetAction)
  closeSheet({ getState, patchState }: StateContext<IonSheetModel>): void {
    const state = getState();

    patchState({
      ...state,
      open: false,
    });
  }

  @Action(SetSheetTypeAction)
  setSheetType({ getState, patchState }: StateContext<IonSheetModel>, { payload }: SetSheetTypeAction): void {
    const state = getState();

    patchState({
      ...state,
      type: payload,
    });
  }

  @Action(SetCurrentPublicationIdAction)
  setCurrentPublicationId(
    { getState, patchState }: StateContext<IonSheetModel>,
    { payload }: SetCurrentPublicationIdAction
  ): void {
    const state = getState();

    patchState({
      ...state,
      currentPublicationId: payload,
    });
  }
}
