import { Action, Selector, State, StateContext } from '@ngxs/store';
import { NetworkStateModel } from './network.model';
import {
  DeleteConnectionAction,
  GetConnectionsAction,
  GetPendingRequestsAction,
  GetRequestsSentAction,
  GetSuggestionsAction,
  GetUsersAction,
  RequestConnectionAction,
  ResponseRequestAction,
  GetProfileVisitsAction,
} from './network.actions';
import { AsyncCallFail, AsyncCallStart, AsyncCallSuccess } from '../common/common.actions';
import { from, throwError } from 'rxjs';
import { catchError, take, tap } from 'rxjs/operators';
import { NetworkingService } from '../../@core/networking.service';
import { UserPublicInfo } from '../../networking/models/user-public-info.model';
import { Injectable } from '@angular/core';
import { ConnectionRequestModel } from '../../networking/models/connection-request.model';
import { ResetNetworkState } from './network.actions';
import { IOrganizationInfo } from 'src/app/@shared/interfaces/organization-info';
import { Utils } from '../../@global/utils';
import { shuffle, uniqBy } from 'lodash';
import { GraphqlError } from '../../@core/errors/graphql-error';

const initialState: NetworkStateModel = {
  contacts: [],
  suggestions: [],
  requested: [],
  pending: [],
  visits: [],
};

@State({
  name: 'network',
  defaults: initialState,
})
@Injectable()
export class NetworkState {
  constructor(private networkingService: NetworkingService) {}

  @Selector()
  public static getSuggestions(state: NetworkStateModel): (UserPublicInfo | IOrganizationInfo)[] {
    return state.suggestions;
  }

  @Selector()
  public static getContacts(state: NetworkStateModel): UserPublicInfo[] {
    return state.contacts;
  }

  @Selector()
  public static getRequested(state: NetworkStateModel): ConnectionRequestModel[] {
    return state.requested;
  }

  @Selector()
  public static getPending(state: NetworkStateModel): ConnectionRequestModel[] {
    return state.pending;
  }

  @Selector()
  public static getVisits(state: NetworkStateModel): UserPublicInfo[] {
    return state.visits;
  }

  @Action(GetSuggestionsAction)
  public getSuggestions({ getState, patchState, dispatch }: StateContext<NetworkStateModel>) {
    dispatch(new AsyncCallStart());

    return from(this.networkingService.getSuggestedToConnect()).pipe(
      take(1),
      tap((response) => {
        dispatch(new AsyncCallSuccess());

        const state = getState();

        const list = [
          ...response['data'].getSuggestedToConnect?.organizations,
          ...response['data'].getSuggestedToConnect?.users,
        ];

        const suggestions = Utils.mapObjectPhotoList(shuffle(list));

        patchState({
          ...state,
          suggestions,
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new GraphqlError(err));
      })
    );
  }

  @Action(GetUsersAction)
  public getUsers({ getState, patchState, dispatch }: StateContext<NetworkStateModel>) {
    dispatch(new AsyncCallStart());

    return from(this.networkingService.getUsers()).pipe(
      take(1),
      tap((response) => {
        dispatch(new AsyncCallSuccess());

        const suggestions = Utils.mapObjectPhotoList(response['data'].getUserSuggestedToConnect);

        const state = getState();

        patchState({
          ...state,
          suggestions,
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new GraphqlError(err));
      })
    );
  }

  @Action(RequestConnectionAction)
  public createConnectionRequest(
    { getState, patchState, dispatch }: StateContext<NetworkStateModel>,
    { payload }: RequestConnectionAction
  ) {
    dispatch(new AsyncCallStart());

    return from(this.networkingService.createConnectionRequest(payload.id)).pipe(
      take(1),
      tap(() => {
        dispatch(new AsyncCallSuccess());

        const state = getState();

        patchState({
          ...state,
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new GraphqlError(err));
      })
    );
  }

  @Action(GetPendingRequestsAction)
  public getPendingRequests({ getState, patchState, dispatch }: StateContext<NetworkStateModel>) {
    dispatch(new AsyncCallStart());

    return from(this.networkingService.getPendingRequests()).pipe(
      take(1),
      tap((response) => {
        dispatch(new AsyncCallSuccess());

        const pending = Utils.mapObjectPhotoList(response['data'].pendingRequests);

        const state = getState();

        patchState({
          ...state,
          pending,
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new GraphqlError(err));
      })
    );
  }

  @Action(ResponseRequestAction)
  public responseConnectionRequest(
    { getState, patchState, dispatch }: StateContext<NetworkStateModel>,
    { payload }: ResponseRequestAction
  ) {
    dispatch(new AsyncCallStart());

    return from(this.networkingService.answerConnectionRequest(payload.id, payload.accept)).pipe(
      take(1),
      tap(() => {
        dispatch(new AsyncCallSuccess());
        const state = getState();

        // update pending
        // find the index of object from array that you want to update
        const objIndex = state.pending.findIndex((user) => user.id === payload.id);

        // make new object of updated object.
        const updatedObj: ConnectionRequestModel = { ...state.pending[objIndex], accept: payload.accept };

        // make final new array of objects by combining updated object.
        const updatedObjects = [...state.pending.slice(0, objIndex), updatedObj, ...state.pending.slice(objIndex + 1)];

        // update suggestions
        const objIndexSuggestions = state.suggestions.findIndex((item: UserPublicInfo) => item.id === payload.id);
        // delete user from suggestions if exists
        let updatedSuggestionsObjects = state.suggestions;
        if (objIndexSuggestions !== -1) {
          updatedSuggestionsObjects = [
            ...state.suggestions.slice(0, objIndexSuggestions),
            ...state.suggestions.slice(objIndexSuggestions + 1),
          ];
        }

        patchState({
          ...state,
          pending: updatedObjects,
          suggestions: updatedSuggestionsObjects,
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new GraphqlError(err));
      })
    );
  }

  @Action(GetConnectionsAction)
  public getUsersConnected(
    { getState, patchState, dispatch }: StateContext<NetworkStateModel>,
    { payload }: GetConnectionsAction
  ) {
    dispatch(new AsyncCallStart());
    return from(this.networkingService.getConnectedUsers(payload.id)).pipe(
      take(1),
      tap((response) => {
        dispatch(new AsyncCallSuccess());

        const contacts = Utils.mapObjectPhotoList(response['data'].getUserConnections);

        const state = getState();

        patchState({
          ...state,
          contacts,
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new GraphqlError(err));
      })
    );
  }

  @Action(GetRequestsSentAction)
  public getRequestsSent({ getState, patchState, dispatch }: StateContext<NetworkStateModel>) {
    dispatch(new AsyncCallStart());

    return from(this.networkingService.getRequestSent()).pipe(
      take(1),
      tap((response) => {
        dispatch(new AsyncCallSuccess());

        const requested = Utils.mapObjectPhotoList(response['data'].sendedPendingRequests);

        const state = getState();

        patchState({
          ...state,
          requested,
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new GraphqlError(err));
      })
    );
  }

  @Action(DeleteConnectionAction)
  public deleteConnection(
    { getState, patchState, dispatch }: StateContext<NetworkStateModel>,
    { payload }: DeleteConnectionAction
  ) {
    dispatch(new AsyncCallStart());

    return from(this.networkingService.removeConnection(payload.id)).pipe(
      take(1),
      tap(() => {
        dispatch(new AsyncCallSuccess());

        const state = getState();

        const objIndex = state.contacts.findIndex((item: UserPublicInfo) => item.id === payload.id);

        const updatedObjects = [...state.contacts.slice(0, objIndex), ...state.contacts.slice(objIndex + 1)];

        patchState({
          ...state,
          contacts: updatedObjects,
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new GraphqlError(err));
      })
    );
  }

  @Action(ResetNetworkState)
  resetNetworkState({ setState }: StateContext<NetworkStateModel>): void {
    setState({
      ...initialState,
    });
  }

  @Action(GetProfileVisitsAction)
  public getProfileVisitsAction(
    { getState, patchState, dispatch }: StateContext<NetworkStateModel>,
    { payload }: GetProfileVisitsAction
  ) {
    dispatch(new AsyncCallStart());

    return from(this.networkingService.getVisitsProfileLastDays(payload.days)).pipe(
      take(1),
      tap((response) => {
        dispatch(new AsyncCallSuccess());

        // mapping to show photo and remove repeated user visits
        const visits = Utils.mapObjectPhotoList(uniqBy(response['data'].getVisitsProfileLastDays, 'id'));

        const state = getState();

        patchState({
          ...state,
          visits,
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new GraphqlError(err));
      })
    );
  }
}
