import { Injectable } from '@angular/core';
import { CanActivate, Router } from '@angular/router';
import { Store } from '@ngxs/store';
import { NavigationState } from '../../@store/navigation/navigation.state';
import { Constants } from '../../@global/constants';

@Injectable({
  providedIn: 'root',
})
export class CreatePageGuard implements CanActivate {
  constructor(private store: Store, private router: Router) {}

  canActivate() {
    return (
      this.store.selectSnapshot(NavigationState.getCanEnterCreatePageFlow) ||
      this.router.parseUrl(Constants.FIRST_PAGE_AFTER_LOGIN)
    );
  }
}
