import { Directive, ElementRef, Output } from '@angular/core';
import { Platform } from '@ionic/angular';
import { Constants } from '../../@global/constants';

@Directive({
  selector: '[appPlatformMode]',
})
export class PlatformLabelModeDirective {
  @Output() mode: string;

  constructor(el: ElementRef, platform: Platform) {
    let position = Constants.ANDROID_FORM_LABEL_POSITION;

    // Determine if the device platform is ios or not
    if (platform.is('ios')) {
      position = Constants.IOS_FORM_LABEL_POSITION;
    }
    // el.nativeElement.setAttribute('position', position);
    // set all labels with same style
    el.nativeElement.setAttribute('position', Constants.ANDROID_FORM_LABEL_POSITION);
  }
}
