import { enableProdMode } from '@angular/core';
import { platformBrowserDynamic } from '@angular/platform-browser-dynamic';

import { AppModule } from './app/app.module';
import { environment } from '@env/environment';

import { Amplify } from 'aws-amplify';
import * as awsconfig from './aws-exports';

// TODO change by switch
if (environment.name === 'PRO') {
  Amplify.configure(awsconfig.awsmobilePRO);
} else if (environment.name === 'PRE') {
  Amplify.configure(awsconfig.awsmobilePRE);
} else {
  Amplify.configure(awsconfig.awsmobileDEV);
}
if (environment.production) {
  enableProdMode();
}

platformBrowserDynamic()
  .bootstrapModule(AppModule)
  .catch((err) => console.log(err));
