import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { Observable, Subscription } from 'rxjs';
import { ION_SHEET_TYPE } from '../../enum/ion-sheet-type';
import { Constants } from '../../../@global/constants';
import { IComment } from '../../../@shared/interfaces/comment';
import { IUserPublicInfo } from '../../../@shared/interfaces/user-public-info';
import { Select, Store } from '@ngxs/store';
import { UserState } from 'src/app/@store/user/user.state';
import { PUBLISHER_TYPE } from '../../../@shared/enums/publisher-type';
import { UtilsService } from '../../../@core/utils.service';
import { NetworkingControllersService } from 'src/app/networking/services/networking-controllers.service';
import { NetworkState } from 'src/app/@store/network/network.state';
import { ConnectionRequestModel } from 'src/app/networking/models/connection-request.model';
import { Utils } from '../../../@global/utils';
import { GetPendingRequestsAction, GetRequestsSentAction } from '../../../@store/network/network.actions';
import { TimelineState } from '../../../@store/timeline/timeline.state';
import { IonSheetState } from '../../../@store/ion-sheet/ion-sheet.state';
import { AddComment, ChangeCommentLikeStatus } from '../../../@store/timeline/timeline.actions';
import { CloseSheetAction } from '../../../@store/ion-sheet/ion-sheet.actions';
import { InformService } from '../../../@core/inform.service';

@Component({
  selector: 'app-ion-sheet',
  templateUrl: './ion-sheet.component.html',
  styleUrls: ['./ion-sheet.component.scss'],
})
export class IonSheetComponent implements OnInit, OnDestroy {
  @Input() userProfileImage: string;
  @Input() events: Observable<void>;
  @Select(NetworkState.getRequested) userSentList$: Observable<ConnectionRequestModel[]>;
  @Select(NetworkState.getPending) userPendingList$: Observable<ConnectionRequestModel[]>;
  public commentItems: IComment[] = [];
  public likeItems: IUserPublicInfo[] = [];
  public itemsRequests: ConnectionRequestModel[];
  public itemsPending: ConnectionRequestModel[];
  private subscriptionRequested$: Subscription;
  private subscriptionPending$: Subscription;
  private commentsSubscription: Subscription;
  private likesSubscription: Subscription;
  private ionSheetSubscription: Subscription;

  private type: ION_SHEET_TYPE;

  private shadowId = 'ion-sheet-bg';
  private ionSheetContainerId = 'ion-sheet';
  public comment = '';
  public maxLenghtMessage = Constants.MAX_COMMENT_PUBLICATION_LENGTH;

  public userId = '';
  public currentPublicationId = '';

  constructor(
    private utilsService: UtilsService,
    private store: Store,
    private networkService: NetworkingControllersService,
    private informService: InformService
  ) {}

  ngOnInit() {
    this.closeIonSheet();
    this.subscribeToRequested();
    this.subscribeToPending();
    this.subscribeToComments();
    this.subscribeToLikes();
    this.subscribeToIonSheetEvent();
  }

  private subscribeToIonSheetEvent(): void {
    this.ionSheetSubscription = this.store.select(IonSheetState.getIonSheetStatus).subscribe((visible) => {
      if (visible) {
        this.openSheet(this.store.selectSnapshot(IonSheetState.getSheetType));
      }
    });
  }

  public openSheet(type) {
    this.type = type;
    if (this.isLikeType()) {
      this.store.dispatch(new GetRequestsSentAction());
      this.store.dispatch(new GetPendingRequestsAction());
    }

    this.userId = this.store.selectSnapshot(UserState.getUserInfo)?.id;

    (<HTMLStyleElement>document.getElementById(this.ionSheetContainerId)).style.bottom = '0px';
    (<HTMLStyleElement>document.getElementById(this.shadowId)).style.display = 'block';
  }

  public closeIonSheet() {
    this.store.dispatch(new CloseSheetAction());
    (<HTMLStyleElement>document.getElementById(this.ionSheetContainerId)).style.bottom = '-1000px';
    (<HTMLStyleElement>document.getElementById(this.ionSheetContainerId)).style.transform = 'translate3d(0px,0px,0px)';
    (<HTMLStyleElement>document.getElementById(this.shadowId)).style.display = 'none';

    this.commentItems = [];
    this.likeItems = [];
  }

  private subscribeToComments() {
    this.commentsSubscription = this.store
      .select(TimelineState.getCommentsOfPublication)
      .subscribe((res: IComment[]) => {
        this.commentItems = res;
      });
  }

  private subscribeToLikes() {
    this.likesSubscription = this.store
      .select(TimelineState.getLikesOfPublication)
      .subscribe((res: IUserPublicInfo[]) => {
        this.likeItems = res;
      });
  }

  public isLikeType() {
    return this.type === ION_SHEET_TYPE.LIKES;
  }

  public isCommentType() {
    return this.type === ION_SHEET_TYPE.COMMENTS;
  }

  public newComment() {
    if (this.comment.length > this.maxLenghtMessage) {
      this.informService.showWarning('formValidations.maxLength');
      return;
    }
    if (this.comment.trim().length === 0) {
      return;
    }
    this.currentPublicationId = this.store.selectSnapshot(IonSheetState.getCurrentPublicationId);
    this.store.dispatch(new AddComment(this.comment, this.currentPublicationId));
    this.comment = '';
  }

  public likeComment(commentId: string) {
    const comment = this.commentItems.find((item: IComment) => item.id === commentId);
    if (comment) {
      this.store.dispatch(new ChangeCommentLikeStatus({ ...comment }));
    }
  }

  public connect(item) {
    this.closeIonSheet();
    this.networkService.onRequestConnection(item.id);
  }

  public onItemClick(userId) {
    this.closeIonSheet();
    this.utilsService.goProfile(userId, PUBLISHER_TYPE.USER);
  }

  public onMessageClick(userId, userName) {
    this.closeIonSheet();
    this.utilsService.goChatDetailWithUserData(userId, userName);
  }

  private subscribeToRequested(): void {
    this.subscriptionRequested$ = this.userSentList$.subscribe((data) => {
      this.itemsRequests = data;
    });
  }

  private subscribeToPending(): void {
    this.subscriptionPending$ = this.userPendingList$.subscribe((data) => {
      this.itemsPending = data;
    });
  }

  public isItemRequested(id): boolean {
    return Utils.isItemOnList(this.itemsRequests, id);
  }

  public isItemPending(id): boolean {
    return Utils.isItemOnList(this.itemsPending, id);
  }

  ngOnDestroy() {
    if (this.subscriptionRequested$) {
      this.subscriptionRequested$.unsubscribe();
    }
    if (this.subscriptionPending$) {
      this.subscriptionPending$.unsubscribe();
    }
    if (this.commentsSubscription) {
      this.commentsSubscription.unsubscribe();
    }
    if (this.likesSubscription) {
      this.likesSubscription.unsubscribe();
    }
    if (this.ionSheetSubscription) {
      this.ionSheetSubscription.unsubscribe();
    }
  }
}
