import { Action, Selector, State, StateContext, Store } from '@ngxs/store';
import { Injectable } from '@angular/core';
import {
  GetNextNotificationsPage,
  GetUserNotifications,
  MarkAllAsRead,
  MarkAsRead,
  AddNotificationToCount,
  ResetNotificationsState,
} from './notifications.actions';
import { NotificationsStateModel } from './notifications.model';
import { catchError, tap } from 'rxjs/operators';
import { from, throwError } from 'rxjs';
import { AsyncCallFail, AsyncCallStart, AsyncCallSuccess } from '../common/common.actions';
import { INotificationList, INotification } from 'src/app/@shared/interfaces/notification';
import { NotificationsService } from 'src/app/@core/notifications.service';
import { Utils } from 'src/app/@global/utils';
import { UtilsService } from 'src/app/@core/utils.service';

const initialState: NotificationsStateModel = {
  notificationList: {
    page: 0,
    total_pages: 0,
    total_count: 0,
    notifications: [],
    total_unread_count: 0,
  },
};

@State({
  name: 'notifications',
  defaults: initialState,
})
@Injectable()
export class NotificationsState {
  constructor(private notificationsService: NotificationsService, private utils: UtilsService) {}

  @Selector()
  static getUserNotifications(state: NotificationsStateModel): INotificationList {
    return state.notificationList;
  }

  @Action(GetUserNotifications)
  public getUserNotifications(
    { getState, patchState, dispatch }: StateContext<NotificationsStateModel>,
    { payload }: GetUserNotifications
  ) {
    dispatch(new AsyncCallStart());
    return from(this.notificationsService.getUserNotifications(payload)).pipe(
      tap((response: any) => {
        dispatch(new AsyncCallSuccess());
        const state = getState();
        const notifications = this.mapNotifications(response.data.getUserNotifications.notifications);
        patchState({
          ...state,
          notificationList: {
            page: response.data.getUserNotifications.page,
            total_count: response.data.getUserNotifications.total_count,
            total_pages: response.data.getUserNotifications.total_pages,
            notifications,
            total_unread_count: response.data.getUserNotifications.total_unread_count,
          },
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError('[GetUserNotifications]: ' + err.message);
      })
    );
  }

  @Action(MarkAllAsRead)
  public markAllAsRead({ getState, patchState, dispatch }: StateContext<NotificationsStateModel>) {
    dispatch(new AsyncCallStart());
    return from(this.notificationsService.readAllNotifications()).pipe(
      tap(() => {
        dispatch(new AsyncCallSuccess());

        const state = getState();
        const notifications = this.markAsReadAllNotificationMap(state.notificationList.notifications);
        patchState({
          ...state,
          notificationList: {
            ...state.notificationList,
            notifications,
            total_unread_count: 0,
          },
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError('[MarkAllAsRead]: ' + err.message);
      })
    );
  }

  @Action(MarkAsRead)
  public markAsRead(
    { getState, patchState, dispatch }: StateContext<NotificationsStateModel>,
    { payload }: MarkAsRead
  ) {
    dispatch(new AsyncCallStart());
    return from(this.notificationsService.readNotification(payload)).pipe(
      tap(() => {
        dispatch(new AsyncCallSuccess());

        const state = getState();

        const notificationIndex = state.notificationList.notifications.findIndex(
          (notification) => notification.id === payload
        );

        if (notificationIndex !== -1) {
          const newNotification = this.markAsReadNotificationMap(
            state.notificationList.notifications[notificationIndex]
          );

          const notifications = state.notificationList.notifications.map((item, index) => {
            if (index !== notificationIndex) {
              return item;
            }

            return newNotification;
          });

          patchState({
            notificationList: {
              ...state.notificationList,
              notifications,
              total_unread_count: state.notificationList.total_unread_count - 1,
            },
          });
        }
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError('[MarkAsRead]: ' + err.message);
      })
    );
  }

  @Action(GetNextNotificationsPage)
  public getNextNotificationsPage(
    { getState, patchState, dispatch }: StateContext<NotificationsStateModel>,
    { payload }: GetUserNotifications
  ) {
    dispatch(new AsyncCallStart());
    return from(this.notificationsService.getUserNotifications(payload)).pipe(
      tap((response: any) => {
        dispatch(new AsyncCallSuccess());
        const state = getState();
        const notifications = this.mapNotifications(response.data.getUserNotifications.notifications);
        patchState({
          ...state,
          notificationList: {
            page: response.data.getUserNotifications.page,
            total_count: response.data.getUserNotifications.total_count,
            total_pages: response.data.getUserNotifications.total_pages,
            notifications: [...state.notificationList.notifications, ...notifications],
            total_unread_count: response.data.getUserNotifications.total_unread_count,
          },
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError('[GetUserNotifications]: ' + err.message);
      })
    );
  }

  @Action(AddNotificationToCount)
  public addNotificationToCount({ getState, patchState }: StateContext<NotificationsStateModel>) {
    const state = getState();

    patchState({
      ...state,
      notificationList: {
        ...state.notificationList,
        total_unread_count: state.notificationList.total_unread_count + 1,
      },
    });
  }

  @Action(ResetNotificationsState)
  resetNotificationsState({ setState }: StateContext<ResetNotificationsState>): void {
    setState({
      ...initialState,
    });
  }

  private markAsReadAllNotificationMap(notifications) {
    return notifications.map((notification) => {
      return this.markAsReadNotificationMap(notification);
    });
  }

  private markAsReadNotificationMap(notification) {
    const n = {
      ...notification,
      read: true,
      additional_data: { ...notification.additional_data },
    };

    if (notification.sender) {
      n['sender'] = {
        ...notification.sender,
        country: { ...notification.sender.country },
        region: { ...notification.sender.region },
        work_area: { ...notification.sender.work_area },
      };
    }

    return n;
  }

  private mapNotifications(notifications: any[]): INotification[] {
    const notificationsFiltered = notifications.filter((notification) => !!notification.notification_data);

    return notificationsFiltered.map((notification) => {
      const n = {
        ...notification,
        notification_data: JSON.parse(notification.notification_data),
        creation_date: this.utils.calculateCreatedLiteral(Date.now(), notification.creation_date),
        additional_data: {
          action: notification.additional_data?.action,
          user_id: notification.additional_data?.user_id,
          publication_id: notification.additional_data?.publication_id,
          chat_id: notification.additional_data?.chat_id,
          organization_id: notification.additional_data?.organization_id,
        },
      };

      if (notification.sender) {
        n['sender'] = {
          ...notification.sender,
          country: { ...notification.sender.country },
          region: { ...notification.sender.region },
          work_area: { ...notification.sender.work_area },
          photo: Utils.mapCloudfrontProfilePhoto(notification.sender.photo),
        };
      }

      return n;
    });
  }
}
