export enum EAuthActions {
  SignIn = '[Auth] SignIn',
  GetIdentityId = '[Auth] Get Identity Id',
  SignUp = '[Auth] SignUp',
  ConfirmSignUp = '[Auth] ConfirmSignUp',
  SignOut = '[Auth] SignOut',
  ChangeUserPassword = '[Auth] ChangeUserPassword',
  ChangePassword = '[Auth] ChangePassword',
  ForgotPassword = '[Auth] ForgotPassword',
  ForgotPasswordSubmit = '[Auth] ForgotPasswordSubmit',
  ResendVerificationCode = '[Auth] ResendVerificationCode',
  SetAutoLoginData = '[Auth] Set Auto Login Data',
  ResetAuthState = '[Auth] Reset Auth State',
}

export class SignIn {
  static readonly type = EAuthActions.SignIn;
  constructor(public payload: { username: string; password: string }) {}
}

export class GetIdentityId {
  static readonly type = EAuthActions.GetIdentityId;
  constructor() {}
}

export class SignUp {
  static readonly type = EAuthActions.SignUp;
  constructor(public payload: { username: string; password: string }) {}
}

export class ConfirmSignUp {
  static readonly type = EAuthActions.ConfirmSignUp;
  constructor(public payload: { username: string; code: string }) {}
}

export class SignOut {
  static readonly type = EAuthActions.SignOut;
}

export class ChangeUserPassword {
  static readonly type = EAuthActions.ChangeUserPassword;
  constructor(public payload: { user: any; oldPassword: string; newPassword: string }) {}
}

export class ChangePassword {
  static readonly type = EAuthActions.ChangePassword;
  constructor(public payload: { user: any; oldPassword: string; newPassword: string }) {}
}

export class ForgotPassword {
  static readonly type = EAuthActions.ForgotPassword;
  constructor(public payload: { username: string }) {}
}

export class ForgotPasswordSubmit {
  static readonly type = EAuthActions.ForgotPasswordSubmit;
  constructor(public payload: { username: string; code: string; newPassword: string }) {}
}

export class ResendVerificationCode {
  static readonly type = EAuthActions.ResendVerificationCode;
  constructor(public payload: { username: string }) {}
}
export class SetAutoLoginData {
  static readonly type = EAuthActions.SetAutoLoginData;
  constructor(public payload: { identityId: string; token: string; userId: string }) {}
}

export class ResetAuthState {
  static readonly type = EAuthActions.ResetAuthState;
  constructor() {}
}
