import { FormControl, FormGroup, ValidationErrors } from '@angular/forms';
import { Constants } from '../../@global/constants';
import { Utils } from '../../@global/utils';
import emojiRegex from 'emoji-regex/RGI_Emoji';

export class FormCustomValidators {
  static validatePassPattern(c: FormControl): ValidationErrors {
    if (c.value && c.value.length > 0) {
      if (c.value.length < Constants.PASSWORD_MIN_LENGTH) {
        return { passMinLength: true };
      }
      if (c.value.length > Constants.PASSWORD_MAX_LENGTH) {
        return { passMaxLength: true };
      }
      const re3 = new RegExp('^' + Constants.NOT_CONTAINS_NUMBER_PATTERN + '$');
      if (c.value.match(re3)) {
        return { passNeedsNumber: true };
      }
      const re4 = new RegExp('^' + Constants.NOT_CONTAINS_LETTERS_PATTERN + '$');
      if (c.value.match(re4)) {
        return { passNeedsLetters: true };
      }
      if (c.value.toUpperCase() === c.value) {
        return { passNeedsLowercase: true };
      }
      if (c.value.toLowerCase() === c.value) {
        return { passNeedsUppercase: true };
      }
      const re1 = new RegExp(Constants.PASSWORD_PATTERN);
      if (c.value.match(re1)) {
        return null;
      } else {
        return { passNeedsSymbol: true };
      }
    }
    return null;
  }

  static validateVerificationCode(c: FormControl): ValidationErrors {
    if (!c.value || !c.value.length) {
      return null;
    }

    const onlyNumbers = new RegExp('^' + Constants.PHONE_PATTERN + '$');

    if (c.value.length !== Constants.VERIFICATION_CODE_LENGTH || !c.value.match(onlyNumbers)) {
      return { verificationCodeLength: true };
    }
  }

  static MustMatch(controlName: string, matchingControlName: string) {
    return (formGroup: FormGroup) => {
      const control = formGroup.controls[controlName];
      const matchingControl = formGroup.controls[matchingControlName];

      // return null if controls haven't initialised yet
      if (!control || !matchingControl) {
        return null;
      }

      // return null if another validator has already found an error on the matchingControl
      if (matchingControl.errors && !matchingControl.errors.mustMatch) {
        return null;
      }

      // set error on matchingControl if validation fails
      if (control.value !== matchingControl.value) {
        matchingControl.setErrors({ mustMatch: true });
      } else {
        matchingControl.setErrors(null);
      }
    };
  }

  static onlyContainsLetters(c: FormControl): ValidationErrors {
    if (c.value && c.value.length > 0) {
      const lowercase = Utils.removeSpecialChars(c.value);
      const containsSymbol = new RegExp('^' + Constants.ONLY_LETTERS_PATTERN + '$');
      if (!lowercase.match(containsSymbol)) {
        return { notAllowedSymbolsAndNumbers: true };
      }
    }
    return null;
  }

  static notAllowedNumbers(c: FormControl): ValidationErrors {
    if (c.value && c.value.length > 0) {
      const re3 = new RegExp('^' + Constants.NOT_CONTAINS_NUMBER_PATTERN + '$');
      if (!c.value.match(re3)) {
        return { notAllowedNumbers: true };
      }
    }
    return null;
  }

  static notAllowedEmojis(c: FormControl): ValidationErrors {
    const regex = emojiRegex();
    const validation = regex.exec(c.value);
    if (validation !== null) {
      return { notAllowedEmojis: true };
    }
    return null;
  }

  static validUrl(c: FormControl): ValidationErrors {
    if (c.value && c.value.length > 0) {
      const lowercase = Utils.removeSpecialChars(c.value);
      const validUrl = new RegExp('^' + Constants.URL_PATTERN + '$');
      if (!lowercase.match(validUrl)) {
        return { notValidUrl: true };
      }
    }
    return null;
  }
}
