import { Action, Selector, State, StateContext } from '@ngxs/store';
import { Injectable } from '@angular/core';
import { ChatsStateModel } from './chats.model';
import { catchError, tap } from 'rxjs/operators';
import { from, throwError } from 'rxjs';
import { AsyncCallFail, AsyncCallStart, AsyncCallSuccess } from '../common/common.actions';
import { Utils } from 'src/app/@global/utils';
import { UtilsService } from 'src/app/@core/utils.service';
import { IChatList, IChat, IMessage, IChatMessageList } from '../../@shared/interfaces/chat';
import { ChatService } from '../../@core/chat.service';
import {
  GetUserChatList,
  ResetChatsState,
  GetChatMessageList,
  AddNextChatMessageList,
  AddNextUserChatList,
} from './chats.actions';
import { IUserPublicInfo } from '../../@shared/interfaces/user-public-info';
import { CreateMessage, SetChatMessagesAsRead, GetChatMessageListByUserId } from './chats.actions';

const initialState: ChatsStateModel = {
  chatList: {
    page: 0,
    total_pages: 0,
    total_count: 0,
    total_unread_count: 0,
    chats: [],
  },
  currentChat: {
    messages: [],
    page: 0,
    total_pages: 0,
    total_count: 0,
    participants: [],
  },
};

@State({
  name: 'chats',
  defaults: initialState,
})
@Injectable()
export class ChatsState {
  constructor(private chatService: ChatService, private utilsService: UtilsService) {}

  @Selector()
  static getChatList(state: ChatsStateModel): IChatList {
    return state.chatList;
  }

  @Selector()
  static getCurrentChat(state: ChatsStateModel): IChatMessageList {
    return state.currentChat;
  }

  @Action(GetUserChatList)
  public getUserChatList(
    { getState, patchState, dispatch }: StateContext<ChatsStateModel>,
    { payload }: GetUserChatList
  ) {
    dispatch(new AsyncCallStart());
    return from(this.chatService.getUserChatList(payload)).pipe(
      tap((response: any) => {
        dispatch(new AsyncCallSuccess());
        const state = getState();

        const chats = this.mapChats(response.data.getUserChatList.chats);

        patchState({
          ...state,
          chatList: {
            page: response.data.getUserChatList.page,
            total_count: response.data.getUserChatList.total_count,
            total_pages: response.data.getUserChatList.total_pages,
            total_unread_count: response.data.getUserChatList.total_unread_count,
            chats,
          },
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError('[GetUserChatList]: ' + err.message);
      })
    );
  }

  @Action(GetChatMessageList)
  public getChatMessageList(context: StateContext<ChatsStateModel>, { payload }: GetChatMessageList) {
    context.dispatch(new AsyncCallStart());
    return from(this.chatService.getChatMessageList(payload.id, payload.input)).pipe(
      tap((response: any) => {
        this.processChatMessageListResponse(context, response, 'getChatMessageList');
      }),
      catchError((err) => {
        context.dispatch(new AsyncCallFail());
        return throwError('[GetChatMessageList]: ' + err.message);
      })
    );
  }

  @Action(GetChatMessageListByUserId)
  public getChatMessageListByUserId(context: StateContext<ChatsStateModel>, { payload }: GetChatMessageListByUserId) {
    context.dispatch(new AsyncCallStart());
    return from(this.chatService.getChatMessageListByUserId(payload.id, payload.input)).pipe(
      tap((response: any) => {
        this.processChatMessageListResponse(context, response, 'getChatMessageListByUserId');
      }),
      catchError((err) => {
        context.dispatch(new AsyncCallFail());
        return throwError('[GetChatMessageListByUserId]: ' + err.message);
      })
    );
  }

  @Action(AddNextUserChatList)
  public addNextUserChatList(
    { getState, patchState, dispatch }: StateContext<ChatsStateModel>,
    { payload }: AddNextUserChatList
  ) {
    dispatch(new AsyncCallStart());
    return from(this.chatService.getUserChatList(payload)).pipe(
      tap((response: any) => {
        dispatch(new AsyncCallSuccess());

        const state = getState();

        const chats = this.mapChats(response.data.getUserChatList.chats);

        patchState({
          ...state,
          chatList: {
            page: response.data.getUserChatList.page,
            total_count: response.data.getUserChatList.total_count,
            total_pages: response.data.getUserChatList.total_pages,
            total_unread_count: response.data.getUserChatList.total_unread_count,
            chats: [...state.chatList.chats, ...chats],
          },
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError('[AddNextUserChatList]: ' + err.message);
      })
    );
  }

  @Action(AddNextChatMessageList)
  public addNextChatMessageList(
    { getState, patchState, dispatch }: StateContext<ChatsStateModel>,
    { payload }: AddNextChatMessageList
  ) {
    dispatch(new AsyncCallStart());
    return from(this.chatService.getChatMessageList(payload.id, payload.input)).pipe(
      tap((response: any) => {
        dispatch(new AsyncCallSuccess());

        const state = getState();

        const messages = this.mapMessageList(response.data.getChatMessageList.messages);
        messages.reverse();
        const participants = this.mapChatParticipants(response.data.getChatMessageList.participants);

        patchState({
          ...state,
          currentChat: {
            page: response.data.getChatMessageList.page,
            total_count: response.data.getChatMessageList.total_count,
            total_pages: response.data.getChatMessageList.total_pages,
            participants,
            messages: [...messages, ...state.currentChat.messages],
          },
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError('[AddNextChatMessageList]: ' + err.message);
      })
    );
  }

  @Action(CreateMessage)
  public createMessage({ getState, patchState, dispatch }: StateContext<ChatsStateModel>, { payload }: CreateMessage) {
    dispatch(new AsyncCallStart());
    return from(this.chatService.createMessage(payload.receiverId, payload.message)).pipe(
      tap((response: any) => {
        dispatch(new AsyncCallSuccess());

        const state = getState();

        const message = this.mapMessage(response.data.createMessage);

        patchState({
          ...state,
          currentChat: {
            ...state.currentChat,
            messages: [...state.currentChat.messages, message],
          },
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError('[CreateMessage]: ' + err.message);
      })
    );
  }

  @Action(SetChatMessagesAsRead)
  public setChatMessagesAsRead(
    { getState, patchState, dispatch }: StateContext<ChatsStateModel>,
    { payload }: SetChatMessagesAsRead
  ) {
    dispatch(new AsyncCallStart());
    return from(this.chatService.setChatMessagesAsRead(payload)).pipe(
      tap((response: any) => {
        dispatch(new AsyncCallSuccess());

        const state = getState();

        const messages = this.mapMessageListAsRead(state.currentChat.messages);

        patchState({
          ...state,
          currentChat: {
            ...state.currentChat,
            messages: messages,
          },
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError('[CreateMessage]: ' + err.message);
      })
    );
  }

  @Action(ResetChatsState)
  resetChatsState({ setState }: StateContext<ChatsStateModel>): void {
    setState({
      ...initialState,
    });
  }

  private processChatMessageListResponse(
    { dispatch, getState, patchState }: StateContext<ChatsStateModel>,
    response: any,
    responsePath: string
  ) {
    dispatch(new AsyncCallSuccess());
    const state = getState();

    const messages = this.mapMessageList(response.data[responsePath].messages);
    messages.reverse();
    const participants = this.mapChatParticipants(response.data[responsePath].participants);

    patchState({
      ...state,
      currentChat: {
        page: response.data[responsePath].page,
        total_count: response.data[responsePath].total_count,
        total_pages: response.data[responsePath].total_pages,
        participants,
        messages,
        chatId: response.data[responsePath].chatId,
      },
    });
  }

  private mapChats(chats: IChat[]): IChat[] {
    return chats.map((chat) => {
      const participants = this.mapChatParticipants(chat.participants);

      return {
        ...chat,
        creation_date_literal: this.utilsService.calculateCreatedLiteral(Date.now(), chat.creation_date),
        participants,
        last_message: {
          ...chat.last_message,
          creation_date_literal: this.utilsService.calculateCreatedLiteral(Date.now(), chat.last_message.creation_date),
        },
      };
    });
  }

  private mapChatParticipants(participants: IUserPublicInfo[]): IUserPublicInfo[] {
    return participants.map((participant) => {
      return {
        ...participant,
        photo: Utils.mapCloudfrontProfilePhoto(participant.photo),
      };
    });
  }

  private mapMessageList(messages: IMessage[]): IMessage[] {
    return messages.map((message) => {
      return this.mapMessage(message);
    });
  }

  private mapMessageListAsRead(messages: IMessage[]): IMessage[] {
    return messages.map((message) => {
      return this.mapMessageAsRead(message);
    });
  }

  private mapMessage(message: IMessage): IMessage {
    return {
      ...message,
      sender: {
        ...message.sender,
        photo: Utils.mapCloudfrontProfilePhoto(message.sender.photo),
      },
      creation_date_literal: this.utilsService.calculateCreatedLiteral(Date.now(), message.creation_date),
    };
  }

  private mapMessageAsRead(message: IMessage): IMessage {
    return {
      ...message,
      sender: {
        ...message.sender,
      },
      creation_date_literal: this.utilsService.calculateCreatedLiteral(Date.now(), message.creation_date),
      read: true,
    };
  }
}
