import { Injectable } from '@angular/core';
import { API, graphqlOperation } from 'aws-amplify';
import {
  createAcademicExperience,
  createProfessionalExperience,
  createSkill,
  deleteAcademicExperience,
  deleteProfessionalExperience,
  deleteSkill,
  updateAcademicExperience,
  updateProfessionalExperience,
  updateUser,
} from '../../graphql/mutations';
import { AcademicExperienceInput, ProfessionalExperienceInput, SkillInput } from '../../API';
import {
  getUserLogged,
  getUser,
  isConnectedWithUser,
  getUserProfile,
  getPublicationPrivacyStatus,
} from '../../graphql/queries';
import { visitProfile } from '../../graphql/mutations';

@Injectable({
  providedIn: 'root',
})
export class UserService {
  constructor() {}

  public async getUserById(id: string) {
    return API.graphql(graphqlOperation(getUser, { id }));
  }

  public async updateUserInfo(info: any) {
    return API.graphql(graphqlOperation(updateUser, { input: info }));
  }

  public async getUserLogged() {
    return API.graphql(graphqlOperation(getUserLogged));
  }

  public async isConnectedWithUser(id: string) {
    return API.graphql(graphqlOperation(isConnectedWithUser, { id }));
  }

  public async getUserProfile(id: string) {
    return API.graphql(graphqlOperation(getUserProfile, { id }));
  }
  public async createUserProfessionalExperience(info: ProfessionalExperienceInput) {
    return API.graphql(graphqlOperation(createProfessionalExperience, { input: info }));
  }

  public async updateUserProfessionalExperience(id: string, info: ProfessionalExperienceInput) {
    return API.graphql(graphqlOperation(updateProfessionalExperience, { id, input: info }));
  }

  public async deleteUserProfessionalExperience(id: string) {
    return API.graphql(graphqlOperation(deleteProfessionalExperience, { id }));
  }

  public async createUserAcademicExperience(info: AcademicExperienceInput) {
    return API.graphql(graphqlOperation(createAcademicExperience, { input: info }));
  }

  public async updateAcademicExperience(id: string, info: any) {
    return API.graphql(graphqlOperation(updateAcademicExperience, { id, input: info }));
  }

  public async deleteAcademicExperience(id: string) {
    return API.graphql(graphqlOperation(deleteAcademicExperience, { id }));
  }

  public async createUserSkill(info: SkillInput) {
    return API.graphql(graphqlOperation(createSkill, { input: info }));
  }

  public async deleteUserSkill(id: string) {
    return API.graphql(graphqlOperation(deleteSkill, { id }));
  }

  public async visitProfile(id: string) {
    return API.graphql(graphqlOperation(visitProfile, { id }));
  }

  public async getPublicationPrivacyStatus(id: string) {
    return API.graphql(graphqlOperation(getPublicationPrivacyStatus, { id }));
  }
}
