export enum EPreferencesActions {
  GetUserCoordinates = '[Preferences] GetUserCoordinates',
  GetUserGeolocation = '[Preferences] GetUserGeolocation',
  ResetPreferencesState = '[Preferences] Reset Preferences State',
}

export class GetUserCoordinates {
  static readonly type = EPreferencesActions.GetUserCoordinates;
  constructor() {}
}

export class GetUserGeolocation {
  static readonly type = EPreferencesActions.GetUserGeolocation;
  constructor(public payload: { latitude: number; longitude: number }) {}
}

export class ResetPreferencesState {
  static readonly type = EPreferencesActions.ResetPreferencesState;
  constructor() {}
}
