import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { UserPublicInfo } from '../../../networking/models/user-public-info.model';
import { IOrganizationInfo } from '../../interfaces/organization-info';
import { Utils } from '../../../@global/utils';
import { ConnectionRequestModel } from '../../../networking/models/connection-request.model';

@Component({
  selector: 'app-suggestions-list',
  templateUrl: './suggestions-list.component.html',
  styleUrls: ['./suggestions-list.component.scss'],
})
export class SuggestionsListComponent implements OnInit {
  @Input() itemsSuggestions: (UserPublicInfo | IOrganizationInfo)[];
  @Input() pages: IOrganizationInfo[];
  @Input() itemsRequests: ConnectionRequestModel[];
  @Input() maxSuggestions: number;
  @Output() followPageClick = new EventEmitter<any>();
  @Output() itemClick = new EventEmitter<any>();
  @Output() requestConnectionClick = new EventEmitter<any>();

  constructor() {}

  ngOnInit() {}

  public isItemOnList(id): boolean {
    return Utils.isItemOnList(this.itemsRequests, id);
  }

  public isPageOnList(id): boolean {
    return Utils.isItemOnList(this.pages, id);
  }

  public onFollowPage($event) {
    this.followPageClick.emit($event);
  }

  public onItemClick($event) {
    this.itemClick.emit($event);
  }

  public onRequestConnection($event) {
    this.requestConnectionClick.emit($event);
  }
}
