import { Injectable } from '@angular/core';
import { Coordinates, Geolocation } from '@ionic-native/geolocation/ngx';
import { NativeGeocoder, NativeGeocoderOptions, NativeGeocoderResult } from '@ionic-native/native-geocoder/ngx';

export type geocodePayload = { latitude: number; longitude: number };

@Injectable({
  providedIn: 'root',
})
export class LocationService {
  constructor(private geolocation: Geolocation, private nativeGeocoder: NativeGeocoder) {}

  public getCoordinates(): Promise<Coordinates> {
    return new Promise((resolve, reject) => {
      this.geolocation
        .getCurrentPosition()
        .then((resp) => {
          resolve(resp.coords);
        })
        .catch((error) => {
          reject(error);
        });
    });
  }

  public getReverseGeocode(payload: geocodePayload): Promise<NativeGeocoderResult> {
    return new Promise<NativeGeocoderResult>((resolve, reject) => {
      const options: NativeGeocoderOptions = {
        useLocale: true,
        defaultLocale: 'es_ES',
        maxResults: 5,
      };

      this.nativeGeocoder
        .reverseGeocode(payload.latitude, payload.longitude, options)
        .then((result: NativeGeocoderResult[]) => {
          resolve(result[0]);
        })
        .catch((error) => {
          reject(error);
        });
    });
  }
}
