import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { Platform } from '@ionic/angular';
import { Constants } from '../../../@global/constants';

@Component({
  selector: 'app-new-post-button',
  templateUrl: './new-post-button.component.html',
  styleUrls: ['./new-post-button.component.scss'],
})
export class NewPostButtonComponent implements OnInit {
  public newPostPath = Constants.NEW_POST_IMAGE_PATH;
  public platformName = '';
  @Output() actionButtonClick = new EventEmitter<void>();
  @Input() fadeIn = true;
  constructor(private platform: Platform) {}

  ngOnInit() {
    this.platformName = this.platform.is('ios') ? 'ios' : 'android';
  }

  onNewPostClick() {
    this.actionButtonClick.emit();
  }
}
