import { Location } from '@angular/common';
import { Router } from '@angular/router';

import { Constants } from '../@global/constants';
import { Store } from '@ngxs/store';
import { UtilsService } from '../@core/utils.service';
import { UserState } from '../@store/user/user.state';

export class BasePage {
  public showBackButton: boolean;
  public logoPath = Constants.LOGO_PATH;
  public userInfo$ = this.store.select(UserState.getUserInfo);

  constructor(
    protected location: Location,
    protected router: Router,
    protected store: Store,
    protected utilsService: UtilsService
  ) {}

  public ionViewWillEnter(): void {
    this.checkShowBackButton();

    // Call doIonViewWillEnter from child if it is overriden
    this.doIonViewWillEnter();
  }

  /**
   * Overwritten by the child classes if want to execute custom code on view will enter
   */
  protected doIonViewWillEnter(): void {
    console.log('Nothing custom to do on doIonViewWillEnter');
  }

  public goBack(): void {
    this.location.back();
  }

  public checkShowBackButton(): void {
    this.showBackButton = !this.utilsService.isRootPage(this.router.url);
  }

  public navigateTo(url) {
    this.router.navigate([url]);
  }
}
