import { IComment } from 'src/app/@shared/interfaces/comment';
import { PaginationInput } from '../../../API';
import { IPublication } from '../../@shared/interfaces/publication';

export enum ETimelineActions {
  GetTimeline = '[Timeline] Get Timeline',
  AddNextPublicationsToTimeline = '[Timeline] Add Next Publications To Timeline',
  AddPreviousPublicationsToTimeline = '[Timeline] Add Previous Publications To Timeline',
  SetTimelineFirstPublicationReference = '[Timeline] Set Timeline First Publication Reference',
  ResetTimelineState = '[Timeline] Reset Timeline State',
  GetPublicationById = '[Timeline] Get Publication By Id',
  AddComment = '[Timeline] Add comment to Publication',
  ChangeLikeStatusTimeline = '[Timeline] Change Like Status Timeline',
  ChangeLikeStatus = '[Timeline] Change Publication Like Status',
  PublicationLikes = '[Timeline] Get Publication Likes',
  CommentsOfPublication = '[Timeline] Get Publication Comments',
  ChangeCommentLikeStatus = '[Timeline] Change Comment Like Status',
  SetPublication = '[Timeline] Set Publication',
}

export class GetTimeline {
  static readonly type = ETimelineActions.GetTimeline;
  constructor(public payload: PaginationInput) {}
}

export class AddNextPublicationsToTimeline {
  static readonly type = ETimelineActions.AddNextPublicationsToTimeline;
  constructor(public payload: PaginationInput, public maxPublicationPages?: number) {}
}

export class AddPreviousPublicationsToTimeline {
  static readonly type = ETimelineActions.AddPreviousPublicationsToTimeline;
  constructor(public payload: PaginationInput, public maxPublicationPages?: number) {}
}
export class SetTimelineFirstPublicationReference {
  static readonly type = ETimelineActions.SetTimelineFirstPublicationReference;
  constructor(public payload: IPublication) {}
}

export class ChangeLikeStatusTimeline {
  static readonly type = ETimelineActions.ChangeLikeStatusTimeline;
  constructor(public payload: IPublication) {}
}

export class ChangeLikeStatus {
  static readonly type = ETimelineActions.ChangeLikeStatus;
  constructor(public payload: { id: string; like: boolean }) {}
}

export class ChangeCommentLikeStatus {
  static readonly type = ETimelineActions.ChangeCommentLikeStatus;
  constructor(public payload: IComment) {}
}

export class ResetTimelineState {
  static readonly type = ETimelineActions.ResetTimelineState;
  constructor() {}
}

export class AddComment {
  static readonly type = ETimelineActions.AddComment;
  constructor(public comment: string, public publicationId: string) {}
}

export class PublicationLikes {
  static readonly type = ETimelineActions.PublicationLikes;
  constructor(public payload: string) {}
}

export class GetPublicationDetail {
  static readonly type = ETimelineActions.GetPublicationById;
  constructor(public payload: { id: string }) {}
}

export class CommentsOfPublication {
  static readonly type = ETimelineActions.CommentsOfPublication;
  constructor(public payload: string) {}
}

export class SetPublication {
  static readonly type = ETimelineActions.SetPublication;
  constructor(public payload: IPublication) {}
}
