import { InformService } from '../inform.service';
import { Logger } from '../logger.service';
import { ErrorHandler, Injectable, Injector } from '@angular/core';
import { LocationStrategy, PathLocationStrategy } from '@angular/common';
import { AWSError } from './aws-error';
import { SentryService } from './sentry.service';
import { environment } from '@env/environment';
import { GraphqlError } from './graphql-error';
import { Platform } from '@ionic/angular';

const log = new Logger('AppGlobalErrorhandler');

@Injectable({
  providedIn: 'root',
})
export class AppGlobalErrorhandler implements ErrorHandler {
  constructor(
    private injector: Injector,
    private informService: InformService,
    private sentryService: SentryService,
    private platform: Platform
  ) {}

  handleError(error: any) {
    const location = this.injector.get(LocationStrategy);
    const url = location instanceof PathLocationStrategy ? location.path() : '';
    let logMessage;
    let userMessage = 'defaultErrorMessage';

    if (error instanceof AWSError) {
      logMessage = error.origin + ' - ' + error.code + ' - ' + error.message;
      userMessage = 'awsCognito.' + error.customMessage;
    }
    if (error instanceof GraphqlError) {
      logMessage =
        error.getErrorPath() +
        ' - ' +
        error.getErrorInfo() +
        ' - ' +
        error.getErrorMessage() +
        ' data: ' +
        error.getErrorData();

      if (error.errorType === 'NOT_FOUND') {
        userMessage = 'graphqlError.' + error.errorType;
      }
    }
    if (!logMessage) {
      // uncontrolled error
      logMessage = (url ? ' URL: ' + url + ' ' : '') + (error.stack || error);
    }
    log.error(logMessage);

    if (environment.name !== 'DEV' && this.platform.is('cordova')) {
      this.sentryService.captureException(logMessage);
    }

    this.informService.showError(userMessage);
  }
}
