import { Injectable } from '@angular/core';
import { CanActivate, Router } from '@angular/router';
import { Store } from '@ngxs/store';
import { GlobalInfoState } from '../../@store/global-info/global-info.state';
import { Constants } from '../../@global/constants';

@Injectable({
  providedIn: 'root',
})
export class LegalConditionsUpdatedGuard implements CanActivate {
  constructor(private router: Router, private store: Store) {}

  async canActivate() {
    return this.store.selectSnapshot(GlobalInfoState.getVersionInfo)?.user_needs_accept_terms
      ? this.router.parseUrl(Constants.PAGE_AFTER_COOKIES)
      : true;
  }
}
