import { Injectable } from '@angular/core';
import {
  GetPendingRequestsAction,
  GetRequestsSentAction,
  GetSuggestionsAction,
  RequestConnectionAction,
  ResponseRequestAction,
} from '../../@store/network/network.actions';
import { take } from 'rxjs/operators';
import { Store } from '@ngxs/store';
import { InformService } from '../../@core/inform.service';
import { IsConnectedWithUser } from '../../@store/profiles/profiles.actions';
import { NetworkState } from '../../@store/network/network.state';
import { GetFollowedPages } from '../../@store/pages/pages.actions';

@Injectable({
  providedIn: 'root',
})
export class NetworkingControllersService {
  constructor(private store: Store, private informService: InformService) {}

  public loadData(): void {
    if (this.store.selectSnapshot(NetworkState.getSuggestions).length === 0) {
      this.store.dispatch(new GetSuggestionsAction());
    }
    this.store.dispatch(new GetRequestsSentAction());
    this.store.dispatch(new GetPendingRequestsAction());
    this.store.dispatch(new GetFollowedPages());
  }

  public onResponseRequest(id, accept) {
    console.log('onResponseRequest ' + id + ' ' + accept);
    this.store
      .dispatch(new ResponseRequestAction({ id, accept }))
      .pipe(take(1))
      .subscribe(
        async () => {
          this.informService.showInfo(
            accept ? 'networking.responseRequestAccept' : 'networking.responseRequestReject',
            3000,
            'bottom'
          );
        },
        (error: any) => {
          console.log(error);
        }
      );
  }

  public onRequestConnection($event) {
    this.store
      .dispatch(new RequestConnectionAction({ id: $event }))
      .pipe(take(1))
      .subscribe(
        async () => {
          this.store.dispatch(new GetRequestsSentAction());
          this.informService.showInfo('networking.requestSucces', 3000, 'bottom');
        },
        (error: any) => {
          console.log(error);
        }
      );
  }

  public requestConnectionUpdateList(id) {
    this.store
      .dispatch(new RequestConnectionAction({ id }))
      .pipe(take(1))
      .subscribe(
        async () => {
          this.store.dispatch(new GetRequestsSentAction());
          this.store.dispatch(new IsConnectedWithUser({ id }));
          this.informService.showInfo('networking.requestSucces', 3000, 'bottom');
        },
        (error: any) => {
          console.log(error);
        }
      );
  }
}
