import { Action, Selector, State, StateContext, Store } from '@ngxs/store';
import { Injectable } from '@angular/core';
import {
  CreatePage,
  CreatePageAdministrator,
  DeletePage,
  DeletePageAdministrator,
  FollowPageAction,
  GetFollowedPages,
  GetPageAdministrators,
  GetPageFollowers,
  GetUserPages,
  ResetPagesState,
  UnfollowPageAction,
  UpdatePage,
} from './pages.actions';
import { PagesStateModel } from './pages.model';
import { PagesService } from '../../@core/pages.service';
import { catchError, tap, mergeMap, take } from 'rxjs/operators';
import { from, throwError } from 'rxjs';
import { AsyncCallFail, AsyncCallStart, AsyncCallSuccess } from '../common/common.actions';
import { IOrganizationInfo } from '../../@shared/interfaces/organization-info';
import { Utils } from '../../@global/utils';
import { SetOrganizationProfileInfo } from '../profiles/profiles.actions';
import { IUserPublicInfo } from 'src/app/@shared/interfaces/user-public-info';
import { AuthState } from '../auth/auth.state';
import {
  UploadPageProfileImage,
  UpdateCurrentPageData,
  SetCurrentPageData,
  CreateOrganizationFlowData,
} from './pages.actions';
import { environment } from '@env/environment';
import { AwsService } from '../../@core/aws.service';
import { GraphqlError } from '../../@core/errors/graphql-error';

const initialState: PagesStateModel = {
  userPageList: [],
  followedPageList: [],
  pageAdministrators: [],
  pageFollowers: [],
  currentPageData: null,
};

@State({
  name: 'pages',
  defaults: initialState,
})
@Injectable()
export class PagesState {
  constructor(private pagesService: PagesService, private store: Store, private awsService: AwsService) {}

  @Selector()
  static getUserPageList(state: PagesStateModel): IOrganizationInfo[] {
    return state.userPageList;
  }

  @Selector()
  static getPageAdministrators(state: PagesStateModel): IUserPublicInfo[] {
    return state.pageAdministrators;
  }

  @Selector()
  static getFollowedPageList(state: PagesStateModel): IOrganizationInfo[] {
    return state.followedPageList;
  }

  @Selector()
  static getCurrentPageData(state: PagesStateModel): CreateOrganizationFlowData {
    return state.currentPageData;
  }

  @Selector()
  static getPageFollowers(state: PagesStateModel): IUserPublicInfo[] {
    return state.pageFollowers;
  }

  @Action(GetUserPages)
  public getUserPages({ getState, patchState, dispatch }: StateContext<PagesStateModel>) {
    dispatch(new AsyncCallStart());

    return from(this.pagesService.getUserPages()).pipe(
      tap((response) => {
        dispatch(new AsyncCallSuccess());

        const state = getState();

        // map page logo
        let pageList = [];
        if (response['data'].getOrganizationsManagedByUser) {
          const allPages = response['data'].getOrganizationsManagedByUser.sport_organizations.concat(
            response['data'].getOrganizationsManagedByUser.academic_organizations,
            response['data'].getOrganizationsManagedByUser.other_organizations
          );
          pageList = allPages.map((element: IOrganizationInfo) => {
            return {
              ...element,
              photo: Utils.mapCloudfrontProfilePhoto(element.photo),
            };
          });
        }

        patchState({
          ...state,
          userPageList: pageList,
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new GraphqlError(err));
      })
    );
  }

  @Action(GetFollowedPages)
  public getFollowedPages({ getState, patchState, dispatch }: StateContext<PagesStateModel>) {
    dispatch(new AsyncCallStart());

    return from(this.pagesService.getFollowedPages()).pipe(
      tap((response) => {
        dispatch(new AsyncCallSuccess());

        const state = getState();

        // map page logo
        const pageList = response['data'].getOrganizationsFollowedByUser.map((element: IOrganizationInfo) => {
          return {
            ...element,
            photo: Utils.mapCloudfrontProfilePhoto(element.photo),
          };
        });

        patchState({
          ...state,
          followedPageList: pageList,
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new GraphqlError(err));
      })
    );
  }

  @Action(CreatePage)
  public createPage({ getState, patchState, dispatch }: StateContext<PagesStateModel>, { payload }: CreatePage) {
    dispatch(new AsyncCallStart());

    return from(this.pagesService.createOrganization(payload.page)).pipe(
      tap((response) => {
        dispatch(new AsyncCallSuccess());

        const newPageData = response['data'].createOrganization;

        dispatch(new SetCurrentPageData({ id: newPageData.id }));

        const state = getState();

        patchState({
          ...state,
          userPageList: [
            ...state.userPageList,
            {
              ...newPageData,
              photo: Utils.mapCloudfrontProfilePhoto(newPageData.photo),
            },
          ],
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new GraphqlError(err));
      })
    );
  }

  @Action(UpdatePage)
  public updatePage({ getState, patchState, dispatch }: StateContext<PagesStateModel>, { payload }: UpdatePage) {
    dispatch(new AsyncCallStart());

    return from(this.pagesService.updateOrganization(payload.id, payload.input)).pipe(
      tap((response) => {
        dispatch(new AsyncCallSuccess());

        const state = getState();

        // find the index of object from array that you want to update
        const objIndex = state.userPageList.findIndex((item) => item.id === payload.id);

        // set updated object.
        const updatedObj = response['data'].updateOrganization;
        updatedObj.photo = Utils.mapCloudfrontProfilePhoto(updatedObj.photo);

        // make final new array of objects by combining updated object.
        const updatedObjects = [
          ...state.userPageList.slice(0, objIndex),
          updatedObj,
          ...state.userPageList.slice(objIndex + 1),
        ];

        // Dispatch action to update profile organization info
        dispatch(new SetOrganizationProfileInfo(updatedObj));
        patchState({
          ...state,
          userPageList: updatedObjects,
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new GraphqlError(err));
      })
    );
  }

  @Action(UnfollowPageAction)
  public unfollowPage(
    { getState, patchState, dispatch }: StateContext<PagesStateModel>,
    { payload }: UnfollowPageAction
  ) {
    dispatch(new AsyncCallStart());

    return from(this.pagesService.unFollowOrganization(payload.id)).pipe(
      tap(() => {
        dispatch(new AsyncCallSuccess());
        const state = getState();

        const objIndex = state.followedPageList.findIndex((item) => item.id === payload.id);

        const updatedObjects = [
          ...state.followedPageList.slice(0, objIndex),
          ...state.followedPageList.slice(objIndex + 1),
        ];

        patchState({
          ...state,
          followedPageList: updatedObjects,
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new GraphqlError(err));
      })
    );
  }

  @Action(FollowPageAction)
  public followPage({ getState, patchState, dispatch }: StateContext<PagesStateModel>, { payload }: FollowPageAction) {
    dispatch(new AsyncCallStart());

    return from(this.pagesService.followOrganization(payload.organization.id)).pipe(
      tap(() => {
        dispatch(new AsyncCallSuccess());
        const state = getState();
        patchState({
          ...state,
          followedPageList: [...state.followedPageList, payload.organization],
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new GraphqlError(err));
      })
    );
  }

  @Action(ResetPagesState)
  resetPagesState({ setState }: StateContext<PagesStateModel>): void {
    setState({
      ...initialState,
    });
  }

  @Action(GetPageAdministrators)
  public getPageAdministrators(
    { getState, patchState, dispatch }: StateContext<PagesStateModel>,
    { payload }: GetPageAdministrators
  ) {
    dispatch(new AsyncCallStart());
    return from(this.pagesService.getPageAdministrators(payload.id)).pipe(
      tap((response) => {
        dispatch(new AsyncCallSuccess());

        const administrators = response['data'].getOrganizationAdministrators.map((admin) => {
          const photo = Utils.mapCloudfrontProfilePhoto(admin.photo);
          return {
            ...admin,
            photo,
          };
        });

        const state = getState();
        patchState({
          ...state,
          pageAdministrators: administrators,
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new GraphqlError(err));
      })
    );
  }

  @Action(DeletePageAdministrator)
  public deletePageAdministrator(
    { getState, patchState, dispatch }: StateContext<PagesStateModel>,
    { payload }: DeletePageAdministrator
  ) {
    dispatch(new AsyncCallStart());
    return from(this.pagesService.deletePageAdministrator(payload.organizationId, payload.userId)).pipe(
      tap(() => {
        dispatch(new AsyncCallSuccess());

        const state = getState();
        let userPageList = state.userPageList;
        const administrators = state.pageAdministrators.filter((admin) => admin.id !== payload.userId);

        const myId = this.store.selectSnapshot(AuthState.getUserId);
        if (myId === payload.userId) {
          userPageList = state.userPageList.filter((page) => page.id !== payload.organizationId);
        }
        patchState({
          ...state,
          userPageList,
          pageAdministrators: administrators,
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new GraphqlError(err));
      })
    );
  }

  @Action(CreatePageAdministrator)
  public createPageAdministrator(
    { getState, patchState, dispatch }: StateContext<PagesStateModel>,
    { payload }: CreatePageAdministrator
  ) {
    dispatch(new AsyncCallStart());
    return from(this.pagesService.createPageAdministrator(payload.organizationId, payload.user.id)).pipe(
      tap(() => {
        dispatch(new AsyncCallSuccess());

        const state = getState();

        patchState({
          ...state,
          pageAdministrators: [...state.pageAdministrators, payload.user],
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError('[GetPageAdministrators]: ' + err.message);
      })
    );
  }

  @Action(DeletePage)
  public deletePage({ getState, patchState, dispatch }: StateContext<PagesStateModel>, { payload }: DeletePage) {
    dispatch(new AsyncCallStart());
    return from(this.pagesService.deleteOrganization(payload.id)).pipe(
      tap(() => {
        dispatch(new AsyncCallSuccess());

        const state = getState();
        const userPageList = state.userPageList.filter((page) => page.id !== payload.id);
        const followedPageList = state.followedPageList.filter((page) => page.id !== payload.id);

        patchState({
          ...state,
          userPageList,
          followedPageList,
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new GraphqlError(err));
      })
    );
  }

  @Action(UploadPageProfileImage)
  uploadPageProfileImage({ dispatch }: StateContext<PagesStateModel>, { payload }: UploadPageProfileImage) {
    dispatch(new AsyncCallStart());

    return from(this.pagesService.assumeAdminRole(payload.pageId)).pipe(
      mergeMap((roleResponse) => {
        return Utils.makeFileIntoBlob(payload.file).pipe(
          mergeMap((body) => {
            return from(
              this.awsService.uploadFileToS3Manual({
                filename: payload.filename,
                body,
                type: payload.type,
                bucket: environment.mediaBucket,
                credentials: roleResponse['data'].assumeAdminRole,
              })
            );
          })
        );
      }),
      take(1),
      tap(() => {
        dispatch(new AsyncCallSuccess());
        dispatch(new UpdateCurrentPageData({ photo: payload.filename }));
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new GraphqlError(err));
      })
    );
  }

  @Action(SetCurrentPageData)
  setCurrentPageData({ getState, patchState }: StateContext<PagesStateModel>, { payload }: SetCurrentPageData) {
    const state = getState();

    patchState({
      ...state,
      currentPageData: payload,
    });
  }

  @Action(UpdateCurrentPageData)
  updateCurrentPageData({ getState, patchState }: StateContext<PagesStateModel>, { payload }: UpdateCurrentPageData) {
    const state = getState();

    patchState({
      ...state,
      currentPageData: {
        ...state.currentPageData,
        ...payload,
      },
    });
  }

  @Action(GetPageFollowers)
  public getPageFollowers(
    { getState, patchState, dispatch }: StateContext<PagesStateModel>,
    { payload }: GetPageFollowers
  ) {
    dispatch(new AsyncCallStart());

    return from(this.pagesService.getOrganizationFollowers(payload.id)).pipe(
      take(1),
      tap((response) => {
        dispatch(new AsyncCallSuccess());

        const state = getState();
        const followers = Utils.mapObjectPhotoList(response['data'].getOrganizationFollowers);

        patchState({
          ...state,
          pageFollowers: followers,
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new GraphqlError(err));
      })
    );
  }
}
