import { Action, Selector, State, StateContext } from '@ngxs/store';
import { Injectable } from '@angular/core';
import { AsyncCallFail, AsyncCallStart, AsyncCallSuccess } from '../common/common.actions';
import { LocationService } from '../../@core/location.service';
import { PreferencesStateModel } from './preferences.model';
import { GetUserCoordinates, GetUserGeolocation, ResetPreferencesState } from './preferences.actions';
import { Coordinates } from '@ionic-native/geolocation';
import { throwError } from 'rxjs';
import { NativeGeocoderResult } from '@ionic-native/native-geocoder';
import { GraphqlError } from '../../@core/errors/graphql-error';

const initialState: PreferencesStateModel = {
  coordinates: null,
  geolocation: null,
};

@State({
  name: 'preferences',
  defaults: initialState,
})
@Injectable()
export class PreferencesState {
  constructor(private locationService: LocationService) {}

  @Selector()
  static getCoordinates(state: PreferencesStateModel): Coordinates {
    return state.coordinates;
  }

  @Selector()
  static getGeolocation(state: PreferencesStateModel): NativeGeocoderResult {
    return state.geolocation;
  }

  @Action(GetUserCoordinates)
  public getUserCoordinates({ getState, patchState, dispatch }: StateContext<PreferencesStateModel>) {
    dispatch(new AsyncCallStart());

    return this.locationService
      .getCoordinates()
      .then((data: Coordinates) => {
        dispatch(new AsyncCallSuccess());

        const state = getState();

        patchState({
          ...state,
          coordinates: data,
        });
      })
      .catch((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new GraphqlError(err));
      });
  }

  @Action(GetUserGeolocation)
  public getUserGeolocation(
    { getState, patchState, dispatch }: StateContext<PreferencesStateModel>,
    { payload }: GetUserGeolocation
  ) {
    dispatch(new AsyncCallStart());

    return this.locationService
      .getReverseGeocode(payload)
      .then((data: NativeGeocoderResult) => {
        dispatch(new AsyncCallSuccess());

        const state = getState();

        patchState({
          ...state,
          geolocation: data,
        });
      })
      .catch((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new GraphqlError(err));
      });
  }

  @Action(ResetPreferencesState)
  resetAuthState({ setState }: StateContext<PreferencesStateModel>): void {
    setState({
      ...initialState,
    });
  }
}
