import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { ICredentials } from '@aws-amplify/core';
import { CognitoUserSession } from 'amazon-cognito-identity-js';

import {
  AwsService,
  signInPayload,
  confirmSignUpPayload,
  changePasswordPayload,
  forgotPasswordPayload,
  forgotPasswordSubmitPayload,
} from './aws.service';
import { ApiService } from './api.service';

@Injectable({
  providedIn: 'root',
})
export class AuthService {
  constructor(private awsService: AwsService, private apiService: ApiService) {}

  public signIn(payload: signInPayload): Observable<{ userId: string; token: string }> {
    return this.awsService.signIn(payload);
  }

  public getCurrentUserCredentials(): Observable<ICredentials> {
    return this.awsService.getCurrentUserCredentials();
  }

  public getCurrentUserInfo(): Observable<any> {
    return this.awsService.getCurrentUserInfo();
  }

  public getCurrentSession(): Observable<CognitoUserSession> {
    return this.awsService.getCurrentSession();
  }

  public getCurrentAuthenticatedUser(): Observable<any> {
    return this.awsService.getCurrentAuthenticatedUser();
  }

  public signUp(payload: signInPayload): Observable<{ user: any; userConfirmed: any; userSub: any }> {
    return this.awsService.signUp(payload);
  }

  public confirmSignUp(payload: confirmSignUpPayload): Observable<{ user: any; userConfirmed: any; userSub: any }> {
    return this.awsService.confirmSignUp(payload);
  }

  public signOut(): Observable<any> {
    return this.awsService.signOut();
  }

  public changePassword(payload: changePasswordPayload): Observable<any> {
    return this.awsService.changePassword(payload);
  }

  public forgotPassword(payload: forgotPasswordPayload): Observable<any> {
    return this.awsService.forgotPassword(payload);
  }

  public forgotPasswordSubmit(payload: forgotPasswordSubmitPayload): Observable<any> {
    return this.awsService.forgotPasswordSubmit(payload);
  }

  public resendVerificationCode(payload: forgotPasswordPayload): Observable<any> {
    return this.awsService.resendVerificationCode(payload);
  }
}
