import { Component, HostListener, Input, OnDestroy, OnInit } from '@angular/core';
import { Store } from '@ngxs/store';
import { Subscription } from 'rxjs';

import { GetPublisherPublicationList } from '../../../@store/profiles/profiles.actions';
import { PUBLICATION_TYPE } from '../../../@shared/enums/publication-type';
import { IPublicationList } from '../../../@shared/interfaces/publication-list';
import { IPublication } from '../../../@shared/interfaces/publication';
import { ProfilesState } from '../../../@store/profiles/profiles.state';
import { SetProfilesFirstPublicationReference } from '../../../@store/profiles/profiles.actions';
import { Router } from '@angular/router';
import { Constants } from 'src/app/@global/constants';

@Component({
  selector: 'app-highlights',
  templateUrl: './highlights.page.html',
  styleUrls: ['./highlights.page.scss'],
})
export class HighlightsPage implements OnInit, OnDestroy {
  @Input() profileId: string;
  @Input() isOrganization: boolean;
  @Input() editable: boolean;
  @Input() showSpinner: boolean;

  private profileEnterSubscription: Subscription;
  private highlightSubscription: Subscription;
  public publisherHighlights: IPublicationList;
  public publicationType = PUBLICATION_TYPE;
  public loadingSpinnerObserver: IntersectionObserver;

  constructor(private store: Store, private router: Router) {}

  ngOnInit() {
    this.setFirstPublication();
    this.subscribeToHighlightsChanges();
  }

  private setFirstPublication(): void {
    this.store.dispatch(new SetProfilesFirstPublicationReference(null));
  }

  private subscribeToHighlightsChanges(): void {
    this.highlightSubscription = this.store.select(ProfilesState.getPublisherHighlights).subscribe(async (data) => {
      this.publisherHighlights = data;
      this.showSpinner = false;

      if (data.publications.length && !this.getFirstPublicationReference()) {
        this.store.dispatch(new SetProfilesFirstPublicationReference(this.publisherHighlights.publications[0]));
      }
    });
  }

  navigateDetail(id: string) {
    this.router.navigate(['/publication-detail', id]);
  }

  public doRefresh(event): void {
    this.store.dispatch(new SetProfilesFirstPublicationReference(null));
    const subscription = this.store
      .dispatch(
        new GetPublisherPublicationList({
          publisherId: this.profileId,
          input: {
            limit: Constants.PUBLICATION_LOAD_LIMIT_HIGHLIGHTS,
          },
        })
      )
      .subscribe(
        () => {
          subscription.unsubscribe();
          event.target.complete();
        },
        (error) => {
          subscription.unsubscribe();
          event.target.complete();
        }
      );
  }

  onNewPostClick() {
    this.router.navigate(['/create-post', this.profileId]);
  }

  private disconnectObservers(): void {
    if (this.highlightSubscription) {
      this.highlightSubscription.unsubscribe();
    }

    if (this.loadingSpinnerObserver) {
      this.loadingSpinnerObserver.disconnect();
    }

    if (this.profileEnterSubscription) {
      this.profileEnterSubscription.unsubscribe();
    }
  }

  public getFirstPublicationReference(): IPublication {
    return this.store.selectSnapshot(ProfilesState.getFirstPublicationReference);
  }

  public trackByPublications(index: number, item: IPublication): string {
    return item.id;
  }

  ngOnDestroy(): void {
    this.store.dispatch(new SetProfilesFirstPublicationReference(null));
    this.disconnectObservers();
  }
}
