import { Selector, State, StateContext, Action } from '@ngxs/store';
import { Injectable } from '@angular/core';
import { NavigationStateModel } from './navigation.model';
import { SetCanEnterCreatePageFlow } from './navigation.actions';

const initialState: NavigationStateModel = {
  canEnterCreatePageFlow: false,
};

@State({
  name: 'navigation',
  defaults: initialState,
})
@Injectable()
export class NavigationState {
  constructor() {}

  @Selector()
  static getCanEnterCreatePageFlow(state: NavigationStateModel): boolean {
    return state.canEnterCreatePageFlow;
  }

  @Action(SetCanEnterCreatePageFlow)
  setCanEnterCreatePageFlow(
    { patchState }: StateContext<NavigationStateModel>,
    { payload }: SetCanEnterCreatePageFlow
  ) {
    patchState({
      canEnterCreatePageFlow: payload,
    });
  }
}
