import { Action, Selector, State, StateContext } from '@ngxs/store';
import { Injectable } from '@angular/core';
import { environment } from '@env/environment';

import { AcademicProgramStateModel, IAcademicProgram } from './academic-programs.model';
import {
  AddFavouriteAcademicProgram,
  CreateAcademicProgram,
  DeleteAcademicProgram,
  DeleteAcademicProgramsFavourite,
  UpdateAcademicProgram,
  GetAcademicProgramsFavourites,
  GetAcademicProgramsRequested,
  GetAcademicProgramsSuggestions,
  GetOrganizationAcademicPrograms,
  RequestEmailAcademicProgram,
  SetCurrentAcademicProgram,
} from './academic-programs.actions';
import { AcademicProgramsService } from '../../@core/academic-programs.service';
import { AsyncCallSuccess, AsyncCallFail, AsyncCallStart } from '../common/common.actions';
import { tap, catchError, mergeMap } from 'rxjs/operators';
import { EMPTY, from, throwError, Observable, of } from 'rxjs';
import { UtilsService } from 'src/app/@core/utils.service';
import { Utils } from 'src/app/@global/utils';
import { GraphqlError } from '../../@core/errors/graphql-error';
import { ResetAcademicProgramsState } from './academic-programs.actions';
import { PagesService } from 'src/app/@core/pages.service';
import { AwsService } from 'src/app/@core/aws.service';
import { Constants } from 'src/app/@global/constants';
import { ChooserResult } from '@ionic-native/chooser/ngx';

const initialState: AcademicProgramStateModel = {
  academicProgramsFavourites: [],
  academicProgramsRequested: [],
  academicProgramsSuggestions: [],
  academicProgramsOrganization: [],
  currentAcademicProgram: null,
};

@State({
  name: 'academicPrograms',
  defaults: initialState,
})
@Injectable()
export class AcademicProgramsState {
  constructor(
    private academicProgramsService: AcademicProgramsService,
    private utils: UtilsService,
    private pagesService: PagesService,
    private awsService: AwsService
  ) {}

  @Selector()
  public static getOrganizationAcademicPrograms(state: AcademicProgramStateModel): IAcademicProgram[] {
    return state.academicProgramsOrganization;
  }

  @Selector()
  public static getAcademicProgramsSuggestions(state: AcademicProgramStateModel): IAcademicProgram[] {
    return state.academicProgramsSuggestions;
  }

  @Selector()
  public static getAcademicProgramsFavourites(state: AcademicProgramStateModel): IAcademicProgram[] {
    return state.academicProgramsFavourites;
  }

  @Selector()
  public static getAcademicProgramsRequested(state: AcademicProgramStateModel): IAcademicProgram[] {
    return state.academicProgramsRequested;
  }

  @Selector()
  public static getCurrentAcademicProgram(state: AcademicProgramStateModel): IAcademicProgram {
    return state.currentAcademicProgram;
  }

  @Action(CreateAcademicProgram)
  createAcademicProgram(
    { getState, patchState, dispatch }: StateContext<AcademicProgramStateModel>,
    { organizationId, input, brochure }: CreateAcademicProgram
  ) {
    dispatch(new AsyncCallStart());

    let brochureObservable: Observable<any>;

    if (brochure) {
      const filenameToUpload = `${Constants.PRIVATE_FOLDER}/${organizationId}/${
        Constants.PROGRAMS_FOLDER
      }/${Utils.generateFileUniqueName(brochure)}`;
      input.brochure = filenameToUpload;

      brochureObservable = this.uploadBrochureFile(brochure, filenameToUpload, organizationId);
    } else {
      brochureObservable = of({});
    }

    return brochureObservable.pipe(
      mergeMap(() => {
        return from(this.academicProgramsService.createAcademicProgram(organizationId, input)).pipe(
          tap((response: any) => {
            const state = getState();

            patchState({
              ...state,
              academicProgramsOrganization: [
                this.mapProgramItem(response.data.createAcademicProgram),
                ...state.academicProgramsOrganization,
              ],
            });
          })
        );
      }),
      tap(() => {
        dispatch(new AsyncCallSuccess());
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new GraphqlError(err));
      })
    );
  }

  @Action(GetAcademicProgramsSuggestions)
  public getAcademicProgramsSuggestions({ getState, patchState, dispatch }: StateContext<AcademicProgramStateModel>) {
    dispatch(new AsyncCallStart());

    return from(this.academicProgramsService.getAcademicProgramsSuggestions()).pipe(
      tap((response: any) => {
        dispatch(new AsyncCallSuccess());

        const suggestions = this.mapAcademicPrograms(response.data.getAcademicProgramsSuggestions);
        const state = getState();
        patchState({
          ...state,
          academicProgramsSuggestions: suggestions,
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new GraphqlError(err));
      })
    );
  }

  @Action(GetAcademicProgramsFavourites)
  public getAcademicProgramsFavourites({ getState, patchState, dispatch }: StateContext<AcademicProgramStateModel>) {
    dispatch(new AsyncCallStart());

    return from(this.academicProgramsService.getAcademicProgramsFavourites()).pipe(
      tap((response: any) => {
        dispatch(new AsyncCallSuccess());

        const favourites = this.mapAcademicPrograms(response.data.getUserFavouriteAcademicPrograms);
        const state = getState();

        patchState({
          ...state,
          academicProgramsFavourites: favourites,
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new GraphqlError(err));
      })
    );
  }

  @Action(GetAcademicProgramsRequested)
  public getAcademicProgramsRequested({ getState, patchState, dispatch }: StateContext<AcademicProgramStateModel>) {
    dispatch(new AsyncCallStart());

    return from(this.academicProgramsService.getAcademicProgramsRequested()).pipe(
      tap((response: any) => {
        dispatch(new AsyncCallSuccess());

        const requested = this.mapAcademicPrograms(response.data.getUserRequestedAcademicPrograms);
        const state = getState();

        patchState({
          ...state,
          academicProgramsRequested: requested,
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new GraphqlError(err));
      })
    );
  }

  @Action(GetOrganizationAcademicPrograms)
  public getOrganizationAcademicPrograms(
    { getState, patchState, dispatch }: StateContext<AcademicProgramStateModel>,
    { payload }: GetOrganizationAcademicPrograms
  ) {
    dispatch(new AsyncCallStart());

    return from(this.academicProgramsService.getOrganizationAcademicPrograms(payload)).pipe(
      tap((response: any) => {
        dispatch(new AsyncCallSuccess());
        const programs = this.mapAcademicPrograms(response.data.getOrganizationAcademicPrograms);
        const state = getState();

        patchState({
          ...state,
          academicProgramsOrganization: programs,
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new GraphqlError(err));
      })
    );
  }

  @Action(DeleteAcademicProgramsFavourite)
  public deleteAcademicProgramFavourite(
    { getState, patchState, dispatch }: StateContext<AcademicProgramStateModel>,
    { payload }: DeleteAcademicProgramsFavourite
  ) {
    const state = getState();
    const favourites = state.academicProgramsFavourites.filter((prog) => prog.id !== payload);

    patchState({
      ...state,
      academicProgramsFavourites: favourites,
    });
    return from(this.academicProgramsService.deleteAcademicProgramFavourite(payload)).pipe(
      tap((response: any) => {
        // do nothing
      }),
      catchError((err) => {
        console.log('[DeleteAcademicProgramsFavourite]: ' + err.message);
        return EMPTY;
      })
    );
  }

  @Action(AddFavouriteAcademicProgram)
  public addFavouriteAcademicProgram(
    { getState, patchState }: StateContext<AcademicProgramStateModel>,
    { payload }: AddFavouriteAcademicProgram
  ) {
    const state = getState();

    patchState({
      ...state,
      academicProgramsFavourites: [payload, ...state.academicProgramsFavourites],
    });
    return from(this.academicProgramsService.createAcademicProgramFavourite(payload.id)).pipe(
      tap(() => {
        // do nothing
      }),
      catchError((err) => {
        console.log('[AddFavouriteAcademicProgram]: ' + err.message);
        return EMPTY;
      })
    );
  }

  @Action(DeleteAcademicProgram)
  public deleteAcademicProgram(
    { getState, patchState, dispatch }: StateContext<AcademicProgramStateModel>,
    { payload }: DeleteAcademicProgram
  ) {
    dispatch(new AsyncCallStart());

    return from(this.academicProgramsService.deleteAcademicProgram(payload)).pipe(
      tap(() => {
        dispatch(new AsyncCallSuccess());
        const state = getState();
        const programs = state.academicProgramsOrganization.filter((prog) => prog.id !== payload);

        patchState({
          ...state,
          academicProgramsOrganization: programs,
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new GraphqlError(err));
      })
    );
  }

  @Action(SetCurrentAcademicProgram)
  public setCurrentAcademicProgram(
    { getState, patchState }: StateContext<AcademicProgramStateModel>,
    { payload }: SetCurrentAcademicProgram
  ) {
    const state = getState();

    patchState({
      ...state,
      currentAcademicProgram: payload,
    });
  }

  @Action(RequestEmailAcademicProgram)
  public requestEmailAcademicProgram(
    { getState, patchState, dispatch }: StateContext<AcademicProgramStateModel>,
    { payload }: RequestEmailAcademicProgram
  ) {
    dispatch(new AsyncCallStart());

    return from(this.academicProgramsService.createAcademicProgramEmailRequest(payload.id, payload.input)).pipe(
      tap(() => {
        dispatch(new AsyncCallSuccess());
        const state = getState();

        patchState({
          ...state,
          academicProgramsRequested: [...state.academicProgramsRequested, payload.program],
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError('[RequestEmailAcademicProgram]: ' + err.message);
      })
    );
  }

  @Action(UpdateAcademicProgram)
  public editAcademicProgram(
    { getState, patchState, dispatch }: StateContext<AcademicProgramStateModel>,
    { input, id }: UpdateAcademicProgram
  ) {
    dispatch(new AsyncCallStart());

    return from(this.academicProgramsService.updateAcademicProgram(id, input)).pipe(
      tap((response: any) => {
        dispatch(new AsyncCallSuccess());
        const state = getState();

        const programsFiltered = state.academicProgramsOrganization.filter((prog) => prog.id !== id);
        const modified = this.mapProgramItem(response.data.updateAcademicProgram);
        patchState({
          ...state,
          academicProgramsOrganization: [...programsFiltered, modified],
          currentAcademicProgram: modified,
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError('[EditAcademicProgram]: ' + err.message);
      })
    );
  }

  @Action(ResetAcademicProgramsState)
  resetAcademicProgramsState({ setState }: StateContext<ResetAcademicProgramsState>): void {
    setState({
      ...initialState,
    });
  }

  private uploadBrochureFile(file: ChooserResult, filenameToUpload: string, organizationId: string) {
    return from(this.pagesService.assumeAdminRole(organizationId)).pipe(
      mergeMap((roleResponse: any) => {
        return from(
          this.awsService.uploadFileToS3Manual({
            filename: filenameToUpload,
            body: file.data,
            type: file.mediaType,
            bucket: environment.mediaBucket,
            credentials: roleResponse['data'].assumeAdminRole,
          })
        );
      })
    );
  }

  private mapAcademicPrograms(academicPrograms: IAcademicProgram[]): IAcademicProgram[] {
    if (academicPrograms) {
      return academicPrograms.map((program: IAcademicProgram) => {
        return this.mapProgramItem(program);
      });
    }
    return [];
  }

  private mapProgramItem(program: IAcademicProgram) {
    return {
      ...program,
      photo: Utils.mapCloudfrontProfilePhoto(program.photo),
      brochure: Utils.mapBrochure(program.brochure),
      location: Utils.generateUserFriendlyLocation(
        program.country,
        program.region,
        program.city,
        program.province,
        program.region_name
      ),
      friendly_start_date: Utils.getDateMMMYY(program.start_date),
      creation_date_literal: this.utils.calculateCreatedLiteral(Date.now(), program.creation_date),
      friendly_end_date: Utils.getDateMMMYY(program.end_date),
    };
  }
}
