import { Injectable } from '@angular/core';
import { API, graphqlOperation } from 'aws-amplify';
import { getUserChatList, getChatMessageList, getChatMessageListByUserId } from '../../graphql/queries';
import { PaginationInput } from '../../API';
import { createMessage, setChatMessagesAsRead } from '../../graphql/mutations';

@Injectable({
  providedIn: 'root',
})
export class ChatService {
  constructor() {}

  public async getUserChatList(input: PaginationInput) {
    return API.graphql(graphqlOperation(getUserChatList, { input }));
  }

  public async getChatMessageList(id: string, input: PaginationInput) {
    return API.graphql(graphqlOperation(getChatMessageList, { id, input }));
  }

  public async getChatMessageListByUserId(id: string, input: PaginationInput) {
    return API.graphql(graphqlOperation(getChatMessageListByUserId, { id, input }));
  }

  public async createMessage(receiverId: string, message: string) {
    return API.graphql(graphqlOperation(createMessage, { receiver_id: receiverId, message }));
  }

  public async setChatMessagesAsRead(id: string) {
    return API.graphql(graphqlOperation(setChatMessagesAsRead, { id }));
  }
}
