import { Injectable } from '@angular/core';
import { API, graphqlOperation } from 'aws-amplify';
import {
  createJobOffer,
  createJobOfferEmailRequest,
  createJobOfferFavourite,
  deleteJobOffer,
  deleteJobOfferFavourite,
} from 'src/graphql/mutations';
import {
  getJobOfferSuggestions,
  getOrganizationJobOffers,
  getUserFavouriteJobOffers,
  getUserRequestedJobOffers,
} from '../../graphql/queries';
import { OfferRequestInput, JobOfferInput } from '../../API';

@Injectable({
  providedIn: 'root',
})
export class JobOffersService {
  constructor() {}

  public async createJobOffer(organizationId: string, input: JobOfferInput) {
    return API.graphql(graphqlOperation(createJobOffer, { organizationId, input }));
  }

  public async deleteJobOffer(id: string) {
    return API.graphql(graphqlOperation(deleteJobOffer, { id }));
  }

  public async getOrganizationJobOffers(id: string) {
    return API.graphql(graphqlOperation(getOrganizationJobOffers, { id }));
  }

  public async getJobOffersSuggestions() {
    return API.graphql(graphqlOperation(getJobOfferSuggestions, {}));
  }

  public async getJobOffersFavourites() {
    return API.graphql(graphqlOperation(getUserFavouriteJobOffers, {}));
  }

  public async getJobOffersRequested() {
    return API.graphql(graphqlOperation(getUserRequestedJobOffers, {}));
  }

  public async deleteJobOfferFavourite(id) {
    return API.graphql(graphqlOperation(deleteJobOfferFavourite, { id }));
  }

  public async createJobOfferFavourite(id) {
    return API.graphql(graphqlOperation(createJobOfferFavourite, { id }));
  }

  public async createJobOfferEmailRequest(id, input: OfferRequestInput) {
    return API.graphql(graphqlOperation(createJobOfferEmailRequest, { id, input }));
  }
}
