import { Injectable } from '@angular/core';
import { API, graphqlOperation } from 'aws-amplify';
import {
  getOrganization,
  getOrganizationsManagedByUser,
  getOrganizationsFollowedByUser,
  getOrganizationAdministrators,
  getOrganizationFollowers,
} from '../../graphql/queries';
import { OrganizationInput, UpdateOrganizationInput } from '../../API';
import { assumeAdminRole } from '../../graphql/mutations';
import {
  createOrganization,
  createOrganizationPageAdministrator,
  deleteOrganization,
  deleteOrganizationPageAdministrator,
  followOrganization,
  unFollowOrganization,
  updateOrganization,
} from '../../graphql/mutations';

@Injectable({
  providedIn: 'root',
})
export class PagesService {
  constructor() {}

  public getUserPages() {
    return API.graphql(graphqlOperation(getOrganizationsManagedByUser));
  }

  public getFollowedPages() {
    return API.graphql(graphqlOperation(getOrganizationsFollowedByUser));
  }

  public async getPageById(id: string) {
    return API.graphql(graphqlOperation(getOrganization, { id }));
  }

  public async createOrganization(input: OrganizationInput) {
    return API.graphql(graphqlOperation(createOrganization, { input }));
  }

  public async updateOrganization(id: string, input: UpdateOrganizationInput) {
    return API.graphql(graphqlOperation(updateOrganization, { id, input }));
  }

  public async followOrganization(id: string) {
    return API.graphql(graphqlOperation(followOrganization, { id }));
  }

  public async unFollowOrganization(id: string) {
    return API.graphql(graphqlOperation(unFollowOrganization, { id }));
  }

  public getPageAdministrators(id: string) {
    return API.graphql(graphqlOperation(getOrganizationAdministrators, { id }));
  }

  public deletePageAdministrator(organizationId: string, userId: string) {
    return API.graphql(graphqlOperation(deleteOrganizationPageAdministrator, { organizationId, userId }));
  }

  public createPageAdministrator(organizationId: string, userId: string) {
    return API.graphql(graphqlOperation(createOrganizationPageAdministrator, { organizationId, userId }));
  }

  public deleteOrganization(id: string) {
    return API.graphql(graphqlOperation(deleteOrganization, { id }));
  }

  public async assumeAdminRole(pageId: string) {
    return API.graphql(graphqlOperation(assumeAdminRole, { page_id: pageId }));
  }

  public getOrganizationFollowers(id: string) {
    return API.graphql(graphqlOperation(getOrganizationFollowers, { id }));
  }
}
