import { OfferRequestInput, JobOfferInput } from '../../../API';
import { IJobOffer } from './job-offers.model';
import { ChooserResult } from '@ionic-native/chooser/ngx';

export enum EJobOffersActions {
  CreateJobOffer = '[JobOffers] Create Job Offer',
  DeleteJobOffer = '[JobOffers] Delete Job Offer',
  GetJobOffers = '[JobOffers] Get Job Offers',
  GetJobOffersSuggestions = '[JobOffers] Get Job Offer Suggestions',
  GetJobOffersFavourites = '[JobOffers] Get Job Offer Favourites',
  GetJobOffersRequested = '[JobOffers] Get Job Offer Requested',
  AddJobOfferFavourite = '[JobOffers] Add Job Offer Favourite',
  DeleteJobOfferFavourite = '[JobOffers] Delete Job Offer Favourite',
  SetCurrentJobOffer = '[JobOffers] Set Current Job Offer',
  RequestEmailJobOffer = '[JobOffers] Request Email Job Offer',
  ResetJobOffersState = '[JobOffers] Reset Job Offers State',
}

export class CreateJobOfferAction {
  static readonly type = EJobOffersActions.CreateJobOffer;
  constructor(public organizationId: string, public input: JobOfferInput) {}
}

export class DeleteJobOfferAction {
  static readonly type = EJobOffersActions.DeleteJobOffer;
  constructor(public id: string) {}
}

export class GetOrganizationJobOffersAction {
  static readonly type = EJobOffersActions.GetJobOffers;
  constructor(public id: string) {}
}

export class GetJobOffersSuggestions {
  static readonly type = EJobOffersActions.GetJobOffersSuggestions;
  constructor() {}
}

export class GetJobOffersFavourites {
  static readonly type = EJobOffersActions.GetJobOffersFavourites;
  constructor() {}
}

export class GetJobOffersRequested {
  static readonly type = EJobOffersActions.GetJobOffersRequested;
  constructor() {}
}

export class SetCurrentJobOffer {
  static readonly type = EJobOffersActions.SetCurrentJobOffer;
  constructor(public payload: IJobOffer) {}
}

export class DeleteJobOfferFavourite {
  static readonly type = EJobOffersActions.DeleteJobOfferFavourite;
  constructor(public payload: string) {}
}

export class AddJobOfferFavourite {
  static readonly type = EJobOffersActions.AddJobOfferFavourite;
  constructor(public payload: IJobOffer) {}
}

export class RequestEmailJobOffer {
  static readonly type = EJobOffersActions.RequestEmailJobOffer;
  constructor(public payload: { id: string; input: OfferRequestInput; jobOffer: IJobOffer; resume?: ChooserResult }) {}
}

export class ResetJobOffersState {
  static readonly type = EJobOffersActions.ResetJobOffersState;
  constructor() {}
}
