import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { RouteReuseStrategy } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { IonicModule, IonicRouteStrategy } from '@ionic/angular';
import { SplashScreen } from '@ionic-native/splash-screen/ngx';
import { StatusBar } from '@ionic-native/status-bar/ngx';
import { TranslateLoader, TranslateModule } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { NgxsModule } from '@ngxs/store';
import { NgxsReduxDevtoolsPluginModule } from '@ngxs/devtools-plugin';
import { NgxsLoggerPluginModule } from '@ngxs/logger-plugin';
import { SocialSharing } from '@ionic-native/social-sharing/ngx';
import { Deeplinks } from '@ionic-native/deeplinks/ngx';

import { AppComponent } from './app.component';
import { AppRoutingModule } from './app-routing.module';
import { SharedModule } from './@shared/shared.module';
import { CoreModule } from './@core/core.module';
import { environment } from '@env/environment';
import { AuthState } from './@store/auth/auth.state';
import { IonicStorageModule } from '@ionic/storage';
import { PagesState } from './@store/pages/pages.state';
import { GlobalInfoState } from './@store/global-info/global-info.state';
import { CommonState } from './@store/common/common.state';
import { TimelineState } from './@store/timeline/timeline.state';
import { PreferencesState } from './@store/preferences/preferences.state';
import { UserState } from './@store/user/user.state';
import { FilePath } from '@ionic-native/file-path/ngx';
import { PostsState } from './@store/posts/posts.state';
import { OneSignal } from '@ionic-native/onesignal/ngx';
import { NetworkState } from './@store/network/network.state';
import { ProfilesState } from './@store/profiles/profiles.state';
import { AcademicProgramsState } from './@store/academic-programs/academic-programs.state';
import { JobOffersState } from './@store/job-offers/job-offers.state';
import { NotificationsState } from './@store/notifications/notifications.state';
import { ChatsState } from './@store/chats/chats.state';
import { Chooser } from '@ionic-native/chooser/ngx';
import { NavigationState } from './@store/navigation/navigation.state';
import { InAppBrowser } from '@ionic-native/in-app-browser/ngx';
import { IonSheetModule } from './timeline/components/ion-sheet/ion-sheet.module';
import { IonSheetState } from './@store/ion-sheet/ion-sheet.state';

export function createTranslateLoader(http: HttpClient) {
  return new TranslateHttpLoader(http, './assets/i18n/', '.json');
}

@NgModule({
  declarations: [AppComponent],
  entryComponents: [],
  imports: [
    BrowserModule,
    IonicModule.forRoot({ mode: 'md' }),
    IonicStorageModule.forRoot({
      name: environment.localStorageKey,
      driverOrder: ['indexeddb', 'sqlite', 'websql', 'localstorage'],
    }),
    AppRoutingModule,
    CoreModule,
    SharedModule,
    TranslateModule.forRoot({
      defaultLanguage: environment.defaultLanguage,
      loader: {
        provide: TranslateLoader,
        useFactory: createTranslateLoader,
        deps: [HttpClient],
      },
    }),
    NgxsModule.forRoot(
      [
        AuthState,
        PagesState,
        GlobalInfoState,
        CommonState,
        TimelineState,
        PreferencesState,
        UserState,
        PostsState,
        NetworkState,
        ProfilesState,
        AcademicProgramsState,
        JobOffersState,
        NotificationsState,
        ChatsState,
        NavigationState,
        IonSheetState,
      ],
      {
        developmentMode: !environment.production,
      }
    ),
    NgxsReduxDevtoolsPluginModule.forRoot({
      disabled: environment.production,
    }),
    NgxsLoggerPluginModule.forRoot({
      disabled: environment.production,
    }),
    IonSheetModule,
  ],
  providers: [
    StatusBar,
    FilePath,
    OneSignal,
    SplashScreen,
    Deeplinks,
    SocialSharing,
    { provide: RouteReuseStrategy, useClass: IonicRouteStrategy },
    Chooser,
    InAppBrowser,
  ],
  bootstrap: [AppComponent],
})
export class AppModule {}
