export enum ECommonActions {
  AsyncCallStart = '[Common] Async Call Start',
  AsyncCallSuccess = '[Common] Async Call Success',
  AsyncCallFail = '[Common] Async Call Fail',
  SetRootPage = '[Common] Set Root Page',
  SetLang = '[Common] Set Language',
  SetAnalyticsCookies = '[Common] Set Analytics Cookies',
}

export class AsyncCallStart {
  static readonly type = ECommonActions.AsyncCallStart;
}

export class AsyncCallSuccess {
  static readonly type = ECommonActions.AsyncCallSuccess;
}

export class AsyncCallFail {
  static readonly type = ECommonActions.AsyncCallFail;
}

export class SetRootPage {
  static readonly type = ECommonActions.SetRootPage;
  constructor(public rootPage: boolean) {}
}

export class SetLanguage {
  static readonly type = ECommonActions.SetLang;
  constructor(public lang: string) {}
}

export class SetAnalyticsCookies {
  static readonly type = ECommonActions.SetAnalyticsCookies;
  constructor(public accepted: boolean) {}
}
