  @Action(GetVersionInfo)
  getVersionsInfo({ patchState, dispatch }: StateContext<GlobalInfoStateModel>) {
    dispatch(new AsyncCallStart());

    return from(this.globalInfoService.getVersionsData()).pipe(
      tap((response: any) => {
        dispatch(new AsyncCallSuccess());

        const needsUpdateApp = env.npm_package_version < response.data['getVersionsData'].latest_compatible_app_version;

        patchState({
          versionInfo: {
            ...response.data['getVersionsData'],
            user_needs_accept_app: needsUpdateApp,
          },
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError('[GetVersionInfo]: ' + err.message);
      })
    );
  }

  @Action(CreateUserLegalTerms)
  createUserLegalTerms(
    { getState, patchState, dispatch }: StateContext<GlobalInfoStateModel>,
    { payload }: CreateUserLegalTerms
  ) {
    dispatch(new AsyncCallStart());

    return from(this.globalInfoService.createUserLegalTerms(payload)).pipe(
      tap((response: any) => {
        dispatch(new AsyncCallSuccess());

        const state = getState();

        patchState({
          versionInfo: {
            ...state.versionInfo,
            user_needs_accept_terms: false,
          },
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError('[CreateUserLegalTerms]: ' + err.message);
      })
    );
  }
