import { Injectable } from '@angular/core';
import { API, graphqlOperation } from 'aws-amplify';
import {
  getUserConnections,
  getUsers,
  pendingRequests,
  sendedPendingRequests,
  getSuggestedToConnect,
  getVisitsProfileLastDays,
} from '../../graphql/queries';
import { removeConnection, requestConnection, responseConnection } from '../../graphql/mutations';
import { Constants } from '../@global/constants';

@Injectable({
  providedIn: 'root',
})
export class NetworkingService {
  constructor() {}

  public async getSuggestedToConnect() {
    return API.graphql(graphqlOperation(getSuggestedToConnect));
  }

  public async getUsers() {
    return API.graphql(graphqlOperation(getUsers, { limit: Constants.MAX_CONTACTS }));
  }

  public async getPendingRequests() {
    return API.graphql(graphqlOperation(pendingRequests));
  }

  public async getConnectedUsers(id: string) {
    return API.graphql(graphqlOperation(getUserConnections, { id, limit: Constants.MAX_CONTACTS }));
  }

  public async createConnectionRequest(id: string) {
    return API.graphql(graphqlOperation(requestConnection, { id }));
  }

  public async answerConnectionRequest(id: string, accept: boolean) {
    return API.graphql(graphqlOperation(responseConnection, { id, accept }));
  }

  public async getRequestSent() {
    return API.graphql(graphqlOperation(sendedPendingRequests));
  }

  public async removeConnection(id: string) {
    return API.graphql(graphqlOperation(removeConnection, { id }));
  }

  public async getVisitsProfileLastDays(days: number) {
    return API.graphql(graphqlOperation(getVisitsProfileLastDays, { days }));
  }
}
