import { Injectable } from '@angular/core';
import { OneSignal } from '@ionic-native/onesignal/ngx';
import { environment } from '@env/environment';
import { Platform } from '@ionic/angular';
import { NetworkingControllersService } from '../networking/services/networking-controllers.service';
import { MarkAsRead, AddNotificationToCount } from '../@store/notifications/notifications.actions';
import { Store } from '@ngxs/store';
import { NOTIFICATION_ACTION } from '../@shared/enums/notification-action';
import { Router } from '@angular/router';
import { INotificationAdditionalData } from '../@shared/interfaces/notification';
import { AuthState } from '../@store/auth/auth.state';
import { NavigationExtrasService } from './navigation-extras.service';
import { ORGANIZATION_PROFILE_TAB } from '../@shared/enums/organization-profile-tab';
import { SetCanEnterCreatePageFlow } from '../@store/navigation/navigation.actions';

@Injectable({
  providedIn: 'root',
})
export class PushService {
  constructor(
    private oneSignal: OneSignal,
    private platform: Platform,
    private networkingService: NetworkingControllersService,
    private store: Store,
    private router: Router,
    private navigationExtrasService: NavigationExtrasService
  ) {}

  initPush() {
    if (this.platform.is('cordova')) {
      this.oneSignal.startInit(environment.oneSignalAppId, environment.googleProjectNumber);

      this.oneSignal.handleNotificationOpened().subscribe((notification) => {
        this.handleNotification(notification);
      });

      this.oneSignal.handleNotificationReceived().subscribe(() => {
        this.store.dispatch(new AddNotificationToCount());
      });

      this.oneSignal.inFocusDisplaying(this.oneSignal.OSInFocusDisplayOption.Notification);
      this.oneSignal.endInit();
    }
  }

  private handleNotification(notification: any) {
    console.log(notification);
    const notificationPayload = notification.notification.payload;
    const notificationAction = notification.action;
    let senderId: string;

    if (notificationPayload.additionalData && notificationPayload.additionalData.notification_id) {
      this.store.dispatch(new MarkAsRead(notificationPayload.additionalData.notification_id));
    }
    if (notificationPayload.additionalData && notificationPayload.additionalData.sender_id) {
      senderId = notificationPayload.additionalData.sender_id;
    }
    if (notificationAction) {
      // If the notification has action buttons, here get de id of the button pressed
      if (notificationAction.actionID === 'cancel-request-connection') {
        this.networkingService.onResponseRequest(senderId, false);
      } else if (notificationAction.actionID === 'accept-request-connection') {
        this.networkingService.onResponseRequest(senderId, true);
      }
    }

    // process custom actions
    if (notificationPayload.additionalData && notificationPayload.additionalData.action) {
      this.processCustomAction(notificationPayload.additionalData);
    }
  }

  public processCustomAction(data: INotificationAdditionalData): void {
    if (!data) {
      return;
    }

    const action = data.action;

    switch (action) {
      case NOTIFICATION_ACTION.COMMENT_PUBLICATION:
      case NOTIFICATION_ACTION.COMMENT_PAGE_PUBLICATION:
      case NOTIFICATION_ACTION.LIKE_PUBLICATION:
      case NOTIFICATION_ACTION.LIKE_PAGE_PUBLICATION:
      case NOTIFICATION_ACTION.VIDEO_PROCESSED:
        this.router.navigate(['/publication-detail', data.publication_id]);
        break;
      case NOTIFICATION_ACTION.ACCEPT_CONNECT:
        this.router.navigate(['/user-profile', data.user_id]);
        break;
      case NOTIFICATION_ACTION.FOLLOWED_PAGE:
        this.router.navigate(['/organization-profile', data.organization_id]);
        break;
      case NOTIFICATION_ACTION.REQUEST_CONNECT:
      case NOTIFICATION_ACTION.CONNECT_WITH_PEOPLE:
        this.router.navigate(['/tabnav/networking']);
        break;
      case NOTIFICATION_ACTION.SEND_MESSAGE:
        this.router.navigate(['/chat-detail', data.chat_id]);
        break;

      case NOTIFICATION_ACTION.SHARE_POSTS:
        this.router.navigate(['/create-post', this.store.selectSnapshot(AuthState.getUserId)]);
        break;

      case NOTIFICATION_ACTION.COMPLETE_YOUR_PROFILE:
        this.router.navigate(['/user-profile', this.store.selectSnapshot(AuthState.getUserId)]);
        break;

      case NOTIFICATION_ACTION.OPPORTUNITY_RECOMMENDATIONS:
        this.router.navigate(['/tabnav/opportunities']);
        break;

      case NOTIFICATION_ACTION.ACADEMIC_PROGRAM_RECOMMENDATIONS:
        this.router.navigate(['/tabnav/academics']);
        break;

      case NOTIFICATION_ACTION.CREATE_YOUR_PAGE:
        this.store.dispatch(new SetCanEnterCreatePageFlow(true));
        this.router.navigate(['/type-organization']);
        break;

      case NOTIFICATION_ACTION.SHARE_POST_PAGE:
        this.router.navigate(['/create-post', data.organization_id]);
        break;

      case NOTIFICATION_ACTION.PUBLISH_OPPORTUNITIES:
        this.navigationExtrasService.setExtras({ tab: ORGANIZATION_PROFILE_TAB.JOBS });
        this.router.navigate(['/organization-profile', data.organization_id]);
        break;

      case NOTIFICATION_ACTION.PUBLISH_ACADEMIC_PROGRAMS:
        this.navigationExtrasService.setExtras({ tab: ORGANIZATION_PROFILE_TAB.PROGRAMS });
        this.router.navigate(['/organization-profile', data.organization_id]);
        break;

      case NOTIFICATION_ACTION.COMPLETE_PAGE_PROFILE:
        this.navigationExtrasService.setExtras({ tab: ORGANIZATION_PROFILE_TAB.ABOUT });
        this.router.navigate(['/organization-profile', data.organization_id]);
        break;

      default:
        console.log('Unrecognized notification custom action: ', action);
        break;
    }
  }

  public setExternalUserId(userId: string) {
    if (this.platform.is('cordova') && userId) {
      this.oneSignal.setExternalUserId(userId);
    }
  }

  public desactivatePush() {
    if (this.platform.is('cordova')) {
      this.oneSignal.setSubscription(false);
    }
  }

  public activatePush() {
    if (this.platform.is('cordova')) {
      this.oneSignal.setSubscription(true);
    }
  }

  public sendTags(tags: any) {
    if (this.platform.is('cordova')) {
      this.oneSignal.sendTags({
        tags,
      });
    }
  }
}
