import { Injectable } from '@angular/core';
import * as Sentry from '@sentry/angular';
import { Platform } from '@ionic/angular';

import { environment } from '@env/environment';

@Injectable({
  providedIn: 'root',
})
export class SentryService {
  constructor(private platform: Platform) {}

  public initialize(): void {
    Sentry.init({
      dsn: 'https://1df5a0480659447998ea0cd629a38fbf@o399925.ingest.sentry.io/5738313',
      release: 'com.impulsyn.onetec@' + environment.version,
    });
    Sentry.configureScope((scope) => {
      const platform = this.platform.is('cordova') ? (this.platform.is('android') ? 'android' : 'ios') : 'web';
      scope.setExtra('platform', platform);
      scope.setExtra('env', environment.name);
      scope.setExtra('version', environment.version);
    });
  }

  public captureException(e: any): void {
    Sentry.captureException(e);
  }
}
