import { Injectable } from '@angular/core';
import { API, graphqlOperation } from 'aws-amplify';
import { getUserNotifications } from '../../graphql/queries';
import { PaginationInput } from 'src/API';
import { readAllNotifications, readNotification } from 'src/graphql/mutations';

@Injectable({
  providedIn: 'root',
})
export class NotificationsService {
  constructor() {}

  public async getUserNotifications(input: PaginationInput) {
    return API.graphql(graphqlOperation(getUserNotifications, { input }));
  }

  public async readAllNotifications() {
    return API.graphql(graphqlOperation(readAllNotifications));
  }

  public async readNotification(id) {
    return API.graphql(graphqlOperation(readNotification, { id }));
  }
}
