export enum ENetworkActions {
  GetSuggestions = '[Network] Get suggestion contacts',
  GetUsers = '[Network] Get users',
  RequestConnection = '[Network] Request connection',
  DeleteConnection = '[Network] Delete connection',
  GetPendingRequests = '[Network] Get Pending Requests',
  GetRequestsSent = '[Network] Get Requests Sent',
  GetConnections = '[Network] Get Connections',
  AnswerRequest = '[Network] Answer Request',
  ResetNetworkState = '[Network] Reset Network State',
  GetProfileVisits = '[Network] Get Profile Visits',
}

export class GetSuggestionsAction {
  public static readonly type = ENetworkActions.GetSuggestions;
  constructor() {}
}

export class GetUsersAction {
  public static readonly type = ENetworkActions.GetUsers;
  constructor() {}
}

export class RequestConnectionAction {
  public static readonly type = ENetworkActions.RequestConnection;
  constructor(public payload: { id: string }) {}
}

export class DeleteConnectionAction {
  public static readonly type = ENetworkActions.DeleteConnection;
  constructor(public payload: { id: string }) {}
}

export class GetPendingRequestsAction {
  public static readonly type = ENetworkActions.GetPendingRequests;
  constructor() {}
}

export class ResponseRequestAction {
  public static readonly type = ENetworkActions.AnswerRequest;
  constructor(public payload: { id: string; accept: boolean }) {}
}

export class GetConnectionsAction {
  public static readonly type = ENetworkActions.GetConnections;
  constructor(public payload: { id: string }) {}
}

export class GetRequestsSentAction {
  public static readonly type = ENetworkActions.GetRequestsSent;
  constructor() {}
}
export class ResetNetworkState {
  static readonly type = ENetworkActions.ResetNetworkState;
  constructor() {}
}
export class GetProfileVisitsAction {
  static readonly type = ENetworkActions.GetProfileVisits;
  constructor(public payload: { days: number }) {}
}
