import { Injectable } from '@angular/core';
import { State, Action, StateContext, Selector } from '@ngxs/store';
import { throwError, Observable, from, EMPTY } from 'rxjs';
import { tap, catchError, take } from 'rxjs/operators';

import {
  GetTimeline,
  AddNextPublicationsToTimeline,
  AddPreviousPublicationsToTimeline,
  ResetTimelineState,
  AddComment,
  ChangeLikeStatusTimeline,
  PublicationLikes,
  GetPublicationDetail,
  CommentsOfPublication,
  ChangeCommentLikeStatus,
  ChangeLikeStatus,
} from './timeline.actions';
import { TimelineStateModel } from './timeline.model';
import { PublicationService } from '../../@core/publication.service';
import { IPublication } from '../../@shared/interfaces/publication';
import { IPublicationList } from '../../@shared/interfaces/publication-list';
import { UtilsService } from '../../@core/utils.service';
import { PaginationInput } from '../../../API';
import { SetTimelineFirstPublicationReference, SetPublication } from './timeline.actions';
import { EGetPublicationsMode } from '../../@shared/enums/get-publications-mode';
import { Utils } from '../../@global/utils';
import { AsyncCallFail, AsyncCallStart, AsyncCallSuccess } from '../common/common.actions';
import { IUserPublicInfo } from 'src/app/@shared/interfaces/user-public-info';
import { IComment } from 'src/app/@shared/interfaces/comment';
import { Constants } from '../../@global/constants';
import { environment } from '@env/environment';
import { GraphqlError } from '../../@core/errors/graphql-error';

const initialState: TimelineStateModel = {
  timeline: {
    page: 0,
    total_pages: 0,
    total_count: 0,
    publications: [],
  },
  firstPublicationReference: null,
  publication: null,
  likes: [],
  comments: [],
};

@State({
  name: 'timeline',
  defaults: initialState,
})
@Injectable()
export class TimelineState {
  constructor(private publicationService: PublicationService, private utilsService: UtilsService) {}

  @Selector()
  static getLikesOfPublication(state: TimelineStateModel): IUserPublicInfo[] {
    return state.likes;
  }

  @Selector()
  static getCommentsOfPublication(state: TimelineStateModel): IComment[] {
    return state.comments;
  }

  @Selector()
  static getTimeline(state: TimelineStateModel): IPublicationList {
    return state.timeline;
  }

  @Selector()
  static getFirstPublicationReference(state: TimelineStateModel): IPublication {
    return state.firstPublicationReference;
  }

  @Selector()
  static getPublication(state: TimelineStateModel): IPublication {
    return state.publication;
  }

  @Action(GetTimeline)
  getTimeline(context: StateContext<TimelineStateModel>, { payload }: GetTimeline) {
    return this.getTimelinePublicationList(context, payload, EGetPublicationsMode.Replace);
  }

  @Action(AddNextPublicationsToTimeline)
  addNextPublicationsToTimeline(
    context: StateContext<TimelineStateModel>,
    { payload, maxPublicationPages }: AddNextPublicationsToTimeline
  ) {
    return this.getTimelinePublicationList(context, payload, EGetPublicationsMode.AddNext, maxPublicationPages);
  }

  @Action(AddPreviousPublicationsToTimeline)
  addPreviousPublicationsToTimeline(
    context: StateContext<TimelineStateModel>,
    { payload, maxPublicationPages }: AddPreviousPublicationsToTimeline
  ) {
    return this.getTimelinePublicationList(context, payload, EGetPublicationsMode.AddPrevious, maxPublicationPages);
  }

  private getTimelinePublicationList(
    { getState, patchState }: StateContext<TimelineStateModel>,
    payload: PaginationInput,
    mode: EGetPublicationsMode,
    maxPublicationPages?: number
  ): Observable<IPublicationList> {
    return from(this.publicationService.getTimeline(payload)).pipe(
      tap((response: any) => {
        let publicationsWithCreationLiteral = this.utilsService.mapTimelinePublicationsResponse(
          response.data.getTimeline.publications,
          response.data.getTimeline.page
        );

        publicationsWithCreationLiteral = publicationsWithCreationLiteral.map((publication: IPublication) => {
          this.utilsService.mapPublicationContent(publication);
          return publication;
        });

        const state = getState();

        const publications = Utils.mergePublicationList(
          state.timeline.publications,
          publicationsWithCreationLiteral,
          mode,
          { pages: maxPublicationPages, limit: payload.limit }
        );

        patchState({
          ...state,
          timeline: {
            page: response.data.getTimeline.page,
            total_count: response.data.getTimeline.total_count,
            total_pages: response.data.getTimeline.total_pages,
            publications,
          },
        });
      }),
      take(1),
      catchError((err) => {
        return throwError(new GraphqlError(err));
      })
    );
  }

  @Action(SetTimelineFirstPublicationReference)
  setFirstPublicationReference(
    { getState, patchState }: StateContext<TimelineStateModel>,
    { payload }: SetTimelineFirstPublicationReference
  ): void {
    const state = getState();

    patchState({
      ...state,
      firstPublicationReference: payload,
    });
  }

  @Action(SetPublication)
  setPublication({ getState, patchState }: StateContext<TimelineStateModel>, { payload }: SetPublication): void {
    const state = getState();

    patchState({
      ...state,
      publication: payload,
    });
  }

  @Action(ResetTimelineState)
  resetTimelineState({ setState }: StateContext<TimelineStateModel>): void {
    setState({
      ...initialState,
    });
  }

  @Action(AddComment)
  addComment(
    { getState, patchState, dispatch }: StateContext<TimelineStateModel>,
    { comment, publicationId }: AddComment
  ) {
    dispatch(new AsyncCallStart());
    return from(this.publicationService.createComment(publicationId, comment)).pipe(
      tap((response) => {
        dispatch(new AsyncCallSuccess());
        const state = getState();
        const publicationsTL = state.timeline.publications.map((pub) => {
          if (pub.id === publicationId) {
            const copy = { ...pub, publisher: { ...pub.publisher } };
            copy.comments += 1;
            return copy;
          }
          return pub;
        });

        const newComment = this.mapComment(response['data'].createCommentToPublication);
        const comments = [newComment, ...state.comments];

        patchState({
          ...state,
          timeline: {
            ...state.timeline,
            publications: publicationsTL,
          },
          comments,
        });
      }),
      take(1),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new GraphqlError(err));
      })
    );
  }

  @Action(ChangeLikeStatusTimeline)
  changeLikeStatusTimeline(
    { getState, patchState, dispatch }: StateContext<TimelineStateModel>,
    { payload }: ChangeLikeStatusTimeline
  ) {
    const state = getState();

    const publicationsTL = state.timeline.publications.map((pub: IPublication) => {
      if (pub.id === payload.id && !payload.like) {
        const copy = { ...pub, publisher: { ...pub.publisher } };
        copy.likes += 1;
        copy.like = true;
        return copy;
      } else if (pub.id === payload.id && payload.like) {
        const copy = { ...pub, publisher: { ...pub.publisher } };
        copy.likes -= 1;
        copy.like = false;
        return copy;
      }
      return pub;
    });

    patchState({
      ...state,
      timeline: {
        ...state.timeline,
        publications: publicationsTL,
      },
    });

    return from(this.publicationService.changeLikeStatus(payload.id, !payload.like)).pipe(
      take(1),
      tap((response) => {
        console.log('[changeLikeStatusTimeline]: ' + response);
      }),
      catchError((err) => {
        console.log('[changeLikeStatusTimeline]: ' + err.errors[0].message);
        return EMPTY;
      })
    );
  }

  @Action(ChangeLikeStatus)
  changeLikeStatus(
    { getState, patchState, dispatch }: StateContext<TimelineStateModel>,
    { payload }: ChangeLikeStatus
  ) {
    const state = getState();

    const uploadPublication = { ...state.publication };
    uploadPublication.likes = payload.like ? uploadPublication.likes + 1 : uploadPublication.likes - 1;
    uploadPublication.like = payload.like;
    patchState({
      ...state,
      publication: uploadPublication,
    });

    return from(this.publicationService.changeLikeStatus(payload.id, payload.like)).pipe(
      take(1),
      tap((response) => {
        console.log('[ChangeLikeStatus]: ' + response);
      }),
      catchError((err) => {
        console.log('[ChangeLikeStatus]: ' + err.errors[0].message);
        return EMPTY;
      })
    );
  }

  @Action(ChangeCommentLikeStatus)
  changeCommentLikeStatus(
    { getState, patchState, dispatch }: StateContext<TimelineStateModel>,
    { payload }: ChangeCommentLikeStatus
  ) {
    const state = getState();

    const comments = state.comments.map((comment: IComment) => {
      if (comment.id === payload.id && !payload.like) {
        const copy = { ...comment, user: { ...comment.user } };
        copy.likes += 1;
        copy.like = true;
        return copy;
      } else if (comment.id === payload.id && payload.like) {
        const copy = { ...comment, user: { ...comment.user } };
        copy.likes -= 1;
        copy.like = false;
        return copy;
      }
      return comment;
    });

    patchState({
      ...state,
      comments,
    });

    return from(this.publicationService.changeLikeCommentStatus(payload.id, !payload.like)).pipe(
      take(1),
      tap((response) => {
        console.log('[ChangeCommentLikeStatus]: ' + response);
      }),
      catchError((err) => {
        console.log('[ChangeCommentLikeStatus]: ' + err.errors[0].message);
        return EMPTY;
      })
    );
  }

  @Action(PublicationLikes)
  getPublicationLikes(
    { getState, patchState, dispatch }: StateContext<TimelineStateModel>,
    { payload }: PublicationLikes
  ) {
    dispatch(new AsyncCallStart());
    return from(this.publicationService.getPublicationLikes(payload)).pipe(
      take(1),
      tap((response) => {
        dispatch(new AsyncCallSuccess());

        const users: IUserPublicInfo[] = response['data'].getLikesOfPublication.map((user: IUserPublicInfo) => {
          const photo = user.photo
            ? environment.cloudfrontBaseUrl + user.photo + Constants.IMAGE_200_X_200_SUFIX
            : Constants.NO_PROFILE_IMAGE_PATH;

          return {
            ...user,
            photo,
          };
        });

        const state = getState();

        patchState({
          ...state,
          likes: users,
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new GraphqlError(err));
      })
    );
  }

  @Action(CommentsOfPublication)
  getCommentsOfPublication(
    { getState, patchState, dispatch }: StateContext<TimelineStateModel>,
    { payload }: CommentsOfPublication
  ) {
    dispatch(new AsyncCallStart());
    return from(this.publicationService.getCommentsOfPublication(payload)).pipe(
      take(1),
      tap((response) => {
        dispatch(new AsyncCallSuccess());
        const state = getState();

        const comments: IComment[] = response['data'].getCommentsOfPublication.map((comment: IComment) => {
          return this.mapComment(comment);
        });
        patchState({
          ...state,
          comments,
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new GraphqlError(err));
      })
    );
  }

  @Action(GetPublicationDetail)
  getPublicationDetail(
    { getState, patchState, dispatch }: StateContext<TimelineStateModel>,
    { payload }: GetPublicationDetail
  ) {
    dispatch(new AsyncCallStart());
    return from(this.publicationService.getPublication(payload.id)).pipe(
      take(1),
      tap((response) => {
        dispatch(new AsyncCallSuccess());

        const state = getState();

        patchState({
          ...state,
          publication: this.mapPublicationDetail(response['data'].getPublicationById),
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new GraphqlError(err));
      })
    );
  }

  private mapComment(comment: IComment): IComment {
    return {
      ...comment,
      creation_date_friendly: this.utilsService.calculateCreatedLiteral(Date.now(), comment.creation_date),
      user: {
        ...comment.user,
        photo: Utils.mapCloudfrontCommentPhoto(comment.user.photo),
      },
    };
  }

  private mapPublicationDetail(publication) {
    const publicationMapped = {
      ...publication,
      publisher: {
        ...publication.publisher,
        photo: Utils.mapCloudfrontProfilePhoto(publication.publisher.photo),
      },
      creation_date_literal: this.utilsService.calculateCreatedLiteral(Date.now(), publication.creation_date),
    };

    this.utilsService.mapPublicationContent(publicationMapped);
    return publicationMapped;
  }
}
