import { Action, Selector, State, StateContext } from '@ngxs/store';
import { Injectable } from '@angular/core';
import { CommnonStateModel } from './common.model';
import {
  AsyncCallFail,
  AsyncCallStart,
  AsyncCallSuccess,
  SetAnalyticsCookies,
  SetLanguage,
  SetRootPage,
} from './common.actions';

@State({
  name: 'common',
  defaults: {
    asyncCallCount: 0,
    isRootPage: false,
  },
})
@Injectable()
export class CommonState {
  constructor() {}

  @Selector()
  static getAsyncCallCount(state: CommnonStateModel): number {
    return state.asyncCallCount;
  }

  @Selector()
  static isRootPage(state: CommnonStateModel): boolean {
    return state.isRootPage;
  }

  @Selector()
  static getLanguage(state: CommnonStateModel): string {
    return state.languageApp;
  }

  @Selector()
  static getAnalyticsCookies(state: CommnonStateModel): boolean {
    return state.cookies.analytics;
  }

  @Action(AsyncCallStart)
  asyncCallStart({ getState, patchState }: StateContext<CommnonStateModel>): void {
    const state = getState();

    patchState({
      ...state,
      asyncCallCount: state.asyncCallCount + 1,
    });
  }

  @Action(AsyncCallSuccess)
  asyncCallSuccess(context: StateContext<CommnonStateModel>): void {
    this.asyncCallFinish(context);
  }

  @Action(AsyncCallFail)
  asyncCallFail(context: StateContext<CommnonStateModel>): void {
    this.asyncCallFinish(context);
  }

  private asyncCallFinish({ getState, patchState }: StateContext<CommnonStateModel>): void {
    const state = getState();

    const count = state.asyncCallCount - 1;

    patchState({
      ...state,
      asyncCallCount: count < 0 ? 0 : count,
    });
  }

  @Action(SetRootPage)
  setRootPage({ getState, patchState }: StateContext<CommnonStateModel>, { rootPage }: SetRootPage): void {
    const state = getState();

    patchState({
      ...state,
      isRootPage: rootPage,
    });
  }

  @Action(SetLanguage)
  setLanguageApp({ getState, patchState }: StateContext<CommnonStateModel>, { lang }: SetLanguage): void {
    const state = getState();

    patchState({
      ...state,
      languageApp: lang,
    });
  }

  @Action(SetAnalyticsCookies)
  setAnalyticsCookies(
    { getState, patchState }: StateContext<CommnonStateModel>,
    { accepted }: SetAnalyticsCookies
  ): void {
    const state = getState();

    patchState({
      ...state,
      cookies: {
        analytics: accepted,
      },
    });
  }
}
