import { Component, Input } from '@angular/core';
import { AbstractControlDirective, AbstractControl } from '@angular/forms';

@Component({
  selector: 'app-show-errors',
  template: `
    <div *ngIf="showErrors()" class="form-error-message">
      <div *ngFor="let error of errors()">
        <ion-text color="danger" class="small-size">
          {{ error | translate }}
        </ion-text>
      </div>
    </div>
  `,
  styleUrls: ['./input-error.scss'],
})
export class InputErrorComponent {
  private static readonly errorMessages = {
    required: () => 'formValidations.requiredField',
    pattern: () => 'formValidations.patternError',
    minlength: () => 'formValidations.minLength',
    maxlength: () => 'formValidations.maxLength',
    emailNotEqual: () => 'formValidations.emailMatchError',
    email: () => 'formValidations.email',
    mustMatch: () => 'formValidations.mustMatchError',
    verificationCodeLength: () => 'formValidations.verificationCodeLengthError',
    passPattern: () => 'formValidations.passPatternError',
    passMinLength: () => 'formValidations.passMinLengthError',
    passMaxLength: () => 'formValidations.passMaxLengthError',
    passNeedsNumber: () => 'formValidations.passNeedsNumberError',
    passNeedsLetters: () => 'formValidations.passNeedsLettersError',
    passNeedsUppercase: () => 'formValidations.passNeedsUppercaseError',
    passNeedsSymbol: () => 'formValidations.passNeedsSymbolError',
    passNeedsLowercase: () => 'formValidations.passNeedsLowercaseError',
    notAllowedSymbolsAndNumbers: () => 'formValidations.notAllowedSymbolsAndNumbers',
    notAllowedSymbols: () => 'formValidations.notAllowedSymbols',
    notAllowedNumbers: () => 'formValidations.notAllowedNumbers',
    notAllowedEmojis: () => 'formValidations.notAllowedEmojis',
    notValidUrl: () => 'formValidations.notValidUrl',
    onlyOne: () => 'formValidations.onlyOne',
  };

  @Input()
  private control: AbstractControlDirective | AbstractControl;

  showErrors(): boolean {
    return this.control && this.control.errors && (this.control.dirty || this.control.touched);
  }

  errors(): string[] {
    return Object.keys(this.control.errors).map((field) => {
      return this.getMessage(field, this.control.errors[field]);
    });
  }

  private getMessage(type: string, params: any) {
    return InputErrorComponent.errorMessages[type] ? InputErrorComponent.errorMessages[type](params) : '';
  }
}
