export class GraphqlError extends Error {
  errorType: string;
  errorInfo: string;
  message: string;
  path: any;
  data: any;

  constructor(error: any) {
    super(error);
    if (error.errors) {
      this.errorInfo = error.errors[0].errorInfo;
      this.errorType = error.errors[0].errorType;
      this.message = error.errors[0].message;
      this.path = error.errors[0].path[0];
    }
    this.data = error.data;
  }

  public getErrorType() {
    return this.errorType;
  }

  public getErrorInfo() {
    return this.errorInfo;
  }

  public getErrorMessage() {
    return this.message;
  }

  public getErrorPath() {
    return this.path;
  }

  public getErrorData() {
    return this.data;
  }

  public getCompleteError() {
    return {
      message: this.message,
      path: this.path,
      errorInfo: this.errorInfo,
      errorType: this.errorType,
      data: this.data,
    };
  }
}
