import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, UrlTree, Router } from '@angular/router';
import { Observable } from 'rxjs';
import { Store } from '@ngxs/store';
import { AuthState } from '../../@store/auth/auth.state';
import { AuthService } from '../auth.service';
import { UtilsService } from '../utils.service';

@Injectable({
  providedIn: 'root',
})
export class LinkablePageGuard implements CanActivate {
  constructor(
    private store: Store,
    private router: Router,
    private authService: AuthService,
    private utilsService: UtilsService
  ) {}

  async canActivate() {
    // if user is in store, navigates directly
    if (this.store.selectSnapshot(AuthState.getToken)) {
      return true;
    }

    // if no user, check if session is still valid and make autologin in that case
    const credentials = await this.authService.getCurrentUserCredentials().toPromise();
    if (!credentials.authenticated) {
      return this.router.parseUrl('/login');
    }

    const [userInfo, currentSession] = await Promise.all([
      this.authService.getCurrentUserInfo().toPromise(),
      this.authService.getCurrentSession().toPromise(),
    ]);

    await this.utilsService
      .autoLogin({
        identityId: credentials?.identityId,
        token: currentSession?.getIdToken()?.getJwtToken(),
        userId: userInfo?.attributes?.sub,
      })
      .toPromise();

    return true;
  }
}
