import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

@Component({
  selector: 'app-action-button-link',
  templateUrl: './action-button-link.component.html',
  styleUrls: ['./action-button-link.component.scss'],
})
export class ActionButtonLinkComponent implements OnInit {
  @Input() buttonText: string;
  @Input() linkText: string;
  @Output() actionButtonClick = new EventEmitter<void>();
  @Output() actionLinkClick = new EventEmitter<void>();
  constructor() {}

  ngOnInit() {}

  public onActionButton(): void {
    this.actionButtonClick.emit();
  }

  public onActionLink(): void {
    this.actionLinkClick.emit();
  }
}
