import { Injectable } from '@angular/core';
import { environment } from '@env/environment';
import { Store } from '@ngxs/store';
import { AuthState } from '../@store/auth/auth.state';
import { CommonState } from '../@store/common/common.state';

declare var gtag: Function;

@Injectable({
  providedIn: 'root',
})
export class GoogleAnalyticsService {
  private googleAnalyticsKey = environment.analyticsTrackId;

  constructor(private store: Store) {}

  public init() {
    try {
      const script1 = document.createElement('script');
      script1.async = true;
      script1.src = 'https://www.googletagmanager.com/gtag/js?id=' + this.googleAnalyticsKey;
      document.head.appendChild(script1);

      const script2 = document.createElement('script');
      script2.innerHTML =
        `
                window.dataLayer = window.dataLayer || [];
                function gtag(){dataLayer.push(arguments);}
                gtag('js', new Date());
                gtag('config', '` +
        this.googleAnalyticsKey +
        `', {
          send_page_view: false
        });
              `;
      document.head.appendChild(script2);
      console.log('Analytics init');
    } catch (ex) {
      console.error('Error appending google analytics');
      console.error(ex);
    }
  }

  public pageEnter(screen: string) {
    if (this.store.selectSnapshot(CommonState.getAnalyticsCookies)) {
      gtag('event', 'page_view', {
        page_path: screen,
        page_title: screen,
        user_id: this.store.selectSnapshot(AuthState.getUserId),
        userId: this.store.selectSnapshot(AuthState.getUserId),
        send_to: this.googleAnalyticsKey,
        version: environment.version,
      });
    }
  }

  public userLogin() {
    if (this.store.selectSnapshot(CommonState.getAnalyticsCookies)) {
      gtag('event', 'login', {
        userId: this.store.selectSnapshot(AuthState.getUserId),
        version: environment.version,
      });
    }
  }

  public userAutoLogin() {
    if (this.store.selectSnapshot(CommonState.getAnalyticsCookies)) {
      gtag('event', 'auto_login', {
        userId: this.store.selectSnapshot(AuthState.getUserId),
        version: environment.version,
      });
    }
  }

  public userSignUp() {
    if (this.store.selectSnapshot(CommonState.getAnalyticsCookies)) {
      gtag('event', 'sign_up', {
        userId: this.store.selectSnapshot(AuthState.getUserId),
        version: environment.version,
      });
    }
  }

  public userSignOut() {
    if (this.store.selectSnapshot(CommonState.getAnalyticsCookies)) {
      gtag('event', 'sign_out', {
        userId: this.store.selectSnapshot(AuthState.getUserId),
        version: environment.version,
      });
    }
  }
}
