import { Pipe, PipeTransform } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { Utils } from '../../@global/utils';

@Pipe({
  name: 'orderTranslate',
})
export class OrderTranslatePipe implements PipeTransform {
  constructor(private translate: TranslateService) {}

  transform(array: any, ...args: string[]): unknown {
    if (array.length > 0) {
      const attrNameToTranslate: string = args[0];
      const translationPrefix: string = args[1] ? args[1] + '.' : '';

      return array.slice().sort((a: any, b: any) => {
        const first = Utils.removeSpecialChars(this.translate.instant(translationPrefix + a[attrNameToTranslate]));
        const second = Utils.removeSpecialChars(this.translate.instant(translationPrefix + b[attrNameToTranslate]));
        if (first < second) {
          return -1;
        }
        if (first > second) {
          return 1;
        }
        return 0;
      });
    }
  }
}
