import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, Router, UrlTree } from '@angular/router';
import { Store } from '@ngxs/store';

import { StorageService } from '../storage.service';
import { NavigationExtrasService } from '../navigation-extras.service';
import { Constants } from '../../@global/constants';
import { UtilsService } from '../utils.service';
import { AuthService } from '../auth.service';
import { SIGNUP_FLOW_PAGES } from 'src/app/signup-flow/signup-pages';
import { ICredentials } from '@aws-amplify/core';
import { UserState } from '../../@store/user/user.state';
import { AnalyticsService } from '../analytics.service';
import { AuthState } from '../../@store/auth/auth.state';
import { GoogleAnalyticsService } from '../google-analytics.service';
import { GlobalInfoState } from '../../@store/global-info/global-info.state';

@Injectable({
  providedIn: 'root',
})
export class FirstTimeGuard implements CanActivate {
  constructor(
    private storage: StorageService,
    private router: Router,
    private navigationExtrasService: NavigationExtrasService,
    private store: Store,
    private utilsService: UtilsService,
    private authService: AuthService,
    private analyticsService: AnalyticsService,
    private googleAnalyticsService: GoogleAnalyticsService
  ) {}

  /**
   * If the user has logged in previously -> Go Login, or home if user has session activated
   * Else -> Go Onboarding page
   */
  async canActivate(next: ActivatedRouteSnapshot, state: RouterStateSnapshot) {
    try {
      // if user has never done login go to onboarding pages
      const loginDone = await this.storage.getAvoidOnboardingPages();
      if (!loginDone) {
        return true;
      }

      // if current user session is enabled make auto login and go home or pending sign up flow page
      const credentials = await this.authService.getCurrentUserCredentials().toPromise();
      if (credentials.authenticated) {
        return await this.manageUserWithSessionEnabled(credentials);
      }

      // if user has been logged in at least once, but the session it's not enabled go to login page
      this.navigationExtrasService.setExtras({ showGoBack: false });
      return this.router.parseUrl('/login');
    } catch (error) {
      return this.router.parseUrl('/login');
    }
  }

  private async manageUserWithSessionEnabled(credentials: ICredentials): Promise<UrlTree> {
    const [userInfo, currentSession] = await Promise.all([
      this.authService.getCurrentUserInfo().toPromise(),
      this.authService.getCurrentSession().toPromise(),
    ]);

    await this.utilsService
      .autoLogin({
        identityId: credentials?.identityId,
        token: currentSession?.getIdToken()?.getJwtToken(),
        userId: userInfo?.attributes?.sub,
      })
      .toPromise();

    if (this.store.selectSnapshot(GlobalInfoState.getVersionInfo).user_needs_accept_app) {
      return this.router.parseUrl(Constants.DOWNLOAD_NEW_VERSION_PAGE);
    }

    const signUpCompleted = this.store.selectSnapshot(UserState.getUserInfo).signup_complete;

    if (signUpCompleted) {
      this.sendAutoLoginEvent();

      // remove remaining signup data
      this.utilsService.removeSignUpDataFromLocalStorage();

      return this.router.parseUrl(Constants.FIRST_PAGE_AFTER_LOGIN);
    }

    // if user has not finished sign up process go to the corresponding sign up flow page
    const signupInProgress = await this.storage.getLastCompleteSignupPage();

    if (signupInProgress && !(await this.utilsService.hasUserFinishedSignUp(userInfo?.attributes?.email))) {
      console.log('proceso de registro no terminado');
      const lastPageIndex = SIGNUP_FLOW_PAGES.indexOf(signupInProgress);
      this.navigationExtrasService.setExtras({ showGoBack: false });

      return this.router.parseUrl(`/${SIGNUP_FLOW_PAGES[lastPageIndex + 1]}`);
    } else {
      console.log("Sign up process not finished but there's no data is in the local storage");
      return this.router.parseUrl(`/${Constants.FIRST_SIGNUP_PAGE_AFTER_VERIFICATION_CODE}`);
    }
  }

  private sendAutoLoginEvent(): void {
    // pinpoint
    this.analyticsService.record(Constants.USER_AUTO_LOGGED_IN_EVENT, {
      userId: this.store.selectSnapshot(AuthState.getUserId),
    });

    // google analytics
    this.googleAnalyticsService.userAutoLogin();
  }
}
