import { State, StateContext, Action, Store, Selector } from '@ngxs/store';
import { Injectable } from '@angular/core';
import { catchError, tap, take, mergeMap } from 'rxjs/operators';
import { throwError, from, Observable } from 'rxjs';

import { PostsStateModel } from './posts.model';
import {
  CreatePost,
  UploadPostImage,
  ResetPostsState,
  UploadPostVideo,
  UploadPostVideoMetadataFile,
  IUploadMediaMetadataFilePayload,
  UploadPagePostImage,
  UploadPagePostVideo,
  UploadPagePostVideoMetadataFile,
} from './posts.actions';

import { AsyncCallStart, AsyncCallSuccess, AsyncCallFail } from '../common/common.actions';
import { PostsService } from '../../@core/posts.service';
import { Constants } from '../../@global/constants';
import { AuthState } from '../auth/auth.state';
import { PublicationInput } from '../../../API';
import { Utils } from '../../@global/utils';
import { AwsService } from '../../@core/aws.service';
import { PagesService } from '../../@core/pages.service';
import { GraphqlError } from '../../@core/errors/graphql-error';
import { environment } from '../../../environments/environment';
import { UtilsService } from 'src/app/@core/utils.service';
import { VIDEO_POSITION } from '../../@shared/enums/video-position';
import { IFile } from '@ionic-native/file/ngx';
import { SetFocusOnPublication } from './posts.actions';

const initialState: PostsStateModel = {
  currentPostInput: null,
  lastCreatedPostId: '',
  focusOnPublication: false,
};

@State({
  name: 'posts',
  defaults: initialState,
})
@Injectable()
export class PostsState {
  constructor(
    private postsService: PostsService,
    private store: Store,
    private awsService: AwsService,
    private pageServices: PagesService,
    private utilsService: UtilsService
  ) {}

  @Selector()
  static getCurrentPostInput(state: PostsStateModel): PublicationInput {
    return state.currentPostInput;
  }

  @Selector()
  static getLastCreatedPostId(state: PostsStateModel): string {
    return state.lastCreatedPostId;
  }

  @Selector()
  static isFocusOnPublication(state: PostsStateModel): boolean {
    return state.focusOnPublication;
  }

  @Action(CreatePost)
  createPost({ getState, patchState, dispatch }: StateContext<PostsStateModel>, { payload }: CreatePost) {
    dispatch(new AsyncCallStart());

    return from(this.postsService.createPost(payload.publisher_id, payload.input)).pipe(
      take(1),
      tap((response: any) => {
        dispatch(new AsyncCallSuccess());

        const state = getState();

        patchState({
          ...state,
          currentPostInput: null,
          lastCreatedPostId: response.data['createPublication'].id,
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new GraphqlError(err));
      })
    );
  }

  @Action(UploadPostImage)
  uploadPostImage({ getState, patchState, dispatch }: StateContext<PostsStateModel>, { payload }: UploadPostImage) {
    dispatch(new AsyncCallStart());

    return Utils.makeFileIntoBlob(payload.file).pipe(
      mergeMap((body) => {
        return from(
          this.awsService.uploadFileToS3({
            filename: payload.filename,
            body,
            type: payload.type,
            level: Constants.PROTECTED_LEVEL,
            bucket: payload.bucket,
          })
        );
      }),
      take(1),
      tap((result) => {
        dispatch(new AsyncCallSuccess());

        const state = getState();

        patchState({
          ...state,
          currentPostInput: {
            ...state.currentPostInput,
            content:
              Constants.PROTECTED_FOLDER + '/' + this.store.selectSnapshot(AuthState.getIdentityId) + '/' + result.key,
          },
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new GraphqlError(err));
      })
    );
  }

  @Action(UploadPagePostImage)
  uploadPagePostImage(
    { getState, patchState, dispatch }: StateContext<PostsStateModel>,
    { payload }: UploadPagePostImage
  ) {
    dispatch(new AsyncCallStart());

    return from(this.pageServices.assumeAdminRole(payload.pageId)).pipe(
      mergeMap((roleResponse) => {
        return Utils.makeFileIntoBlob(payload.file).pipe(
          mergeMap((body) => {
            return from(
              this.awsService.uploadFileToS3Manual({
                filename: payload.filename,
                body,
                type: payload.type,
                bucket: payload.bucket,
                credentials: roleResponse['data'].assumeAdminRole,
              })
            );
          })
        );
      }),
      take(1),
      tap(() => {
        dispatch(new AsyncCallSuccess());

        const state = getState();

        // TODO PAGES set the corresponding content
        patchState({
          ...state,
          currentPostInput: {
            ...state.currentPostInput,
            content: payload.filename,
          },
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new GraphqlError(err));
      })
    );
  }

  @Action(UploadPostVideo)
  uploadPostVideo({ dispatch }: StateContext<PostsStateModel>, { payload }: UploadPostVideo) {
    dispatch(new AsyncCallStart());

    return Utils.makeFileIntoBlob(payload.file).pipe(
      mergeMap((body) => {
        return from(
          this.awsService.uploadFileToS3({
            filename: payload.filename,
            body,
            type: payload.type,
            level: Constants.PROTECTED_LEVEL,
            bucket: payload.bucket,
            metadata: payload.metadata,
          })
        );
      }),
      mergeMap(() => {
        return from(this.getVideoRotationAndPosition(payload.file, payload.mediaWidth, payload.mediaHeight));
      }),
      mergeMap(({ needsRotate, position }) => {
        const metadataFilename = Utils.replaceFileExtension(payload.filename, 'json');
        const metadataPayload: IUploadMediaMetadataFilePayload = {
          filename: metadataFilename,
          bucket: payload.bucket,
          rotate: needsRotate,
          position,
          srcVideo: `${Constants.PROTECTED_FOLDER}/${this.store.selectSnapshot(AuthState.getIdentityId)}/${
            payload.filename
          }`,
        };
        return dispatch(new UploadPostVideoMetadataFile(metadataPayload));
      }),
      take(1),
      tap(() => {
        dispatch(new AsyncCallSuccess());
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(err);
      })
    );
  }

  @Action(UploadPagePostVideo)
  uploadPagePostVideo({ dispatch }: StateContext<PostsStateModel>, { payload }: UploadPagePostVideo) {
    dispatch(new AsyncCallStart());

    return from(this.pageServices.assumeAdminRole(payload.pageId)).pipe(
      mergeMap((roleResponse) => {
        const credentials = roleResponse['data'].assumeAdminRole;
        return Utils.makeFileIntoBlob(payload.file).pipe(
          mergeMap((body) => {
            return from(
              this.awsService.uploadFileToS3Manual({
                filename: payload.filename,
                body,
                type: payload.type,
                bucket: payload.bucket,
                credentials,
                metadata: payload.metadata,
              })
            );
          }),
          mergeMap(() => {
            return from(this.getVideoRotationAndPosition(payload.file, payload.mediaWidth, payload.mediaHeight));
          }),
          mergeMap(({ needsRotate, position }) => {
            const metadataFilename = Utils.replaceFileExtension(payload.filename, 'json');
            const metadataPayload: IUploadMediaMetadataFilePayload = {
              filename: metadataFilename,
              bucket: payload.bucket,
              srcVideo: payload.filename,
              rotate: needsRotate,
              position,
              credentials,
            };
            return dispatch(new UploadPagePostVideoMetadataFile(metadataPayload));
          })
        );
      }),
      take(1),
      tap(() => {
        dispatch(new AsyncCallSuccess());
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(err);
      })
    );
  }

  @Action(UploadPostVideoMetadataFile)
  uploadPostVideoMetadataFile({ dispatch }: StateContext<PostsStateModel>, { payload }: UploadPostVideoMetadataFile) {
    dispatch(new AsyncCallStart());

    const metadata = this.generateVideoMetadata(payload.srcVideo, payload.rotate, payload.position);
    const body = JSON.stringify(metadata);

    return from(
      this.awsService.uploadFileToS3({
        filename: payload.filename,
        body,
        type: 'application/json',
        level: Constants.PROTECTED_LEVEL,
        bucket: payload.bucket,
      })
    ).pipe(
      take(1),
      tap(() => {
        dispatch(new AsyncCallSuccess());
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(err);
      })
    );
  }

  @Action(UploadPagePostVideoMetadataFile)
  uploadPagePostVideoMetadataFile(
    { dispatch }: StateContext<PostsStateModel>,
    { payload }: UploadPagePostVideoMetadataFile
  ) {
    dispatch(new AsyncCallStart());

    const metadata = this.generateVideoMetadata(payload.srcVideo, payload.rotate, payload.position);
    const body = JSON.stringify(metadata);

    return from(
      this.awsService.uploadFileToS3Manual({
        filename: payload.filename,
        body,
        type: 'application/json',
        bucket: payload.bucket,
        credentials: payload.credentials,
      })
    ).pipe(
      take(1),
      tap(() => {
        dispatch(new AsyncCallSuccess());
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(err);
      })
    );
  }

  @Action(SetFocusOnPublication)
  setFocusOnPublication({ patchState }: StateContext<PostsStateModel>, { payload }: SetFocusOnPublication) {
    patchState({
      focusOnPublication: payload,
    });
  }

  @Action(ResetPostsState)
  resetPostsState({ setState }: StateContext<PostsStateModel>): void {
    setState({
      ...initialState,
    });
  }

  private async getVideoRotationAndPosition(
    file: IFile,
    videoWidth: number,
    videoHeight: number
  ): Promise<{ needsRotate: boolean; position: VIDEO_POSITION }> {
    const { metadataWidth, metadataHeight } = await this.utilsService.getVideoMedatataWidthHeight(file);

    if (!metadataWidth || !metadataHeight) {
      return { needsRotate: false, position: VIDEO_POSITION.HORIZONTAL };
    }

    const needsRotate =
      (videoWidth > videoHeight && !(metadataWidth > metadataHeight)) ||
      (videoWidth < videoHeight && !(metadataWidth < metadataHeight));

    const position = videoWidth < videoHeight ? VIDEO_POSITION.VERTICAL : VIDEO_POSITION.HORIZONTAL;

    return { needsRotate, position };
  }

  private generateVideoMetadata(srcVideo: string, rotate: boolean, position: VIDEO_POSITION): any {
    const metadata = {
      srcVideo,
      InputRotate: rotate ? Constants.VIDEO_ROTATE_DEGREES_90 : Constants.VIDEO_ROTATE_DEGREE_0,
    };

    if (position === VIDEO_POSITION.VERTICAL) {
      metadata['jobTemplate_2160p'] = environment.jobTemplatePortrait_2160p;
      metadata['jobTemplate_1080p'] = environment.jobTemplatePortrait_1080p;
      metadata['jobTemplate_720p'] = environment.jobTemplatePortrait_720p;
    }

    return metadata;
  }
}
