import { IUserPublicInfo } from 'src/app/@shared/interfaces/user-public-info';
import { OrganizationInput, UpdateOrganizationInput } from '../../../API';
import { IOrganizationInfo } from '../../@shared/interfaces/organization-info';
import { IFile } from '@ionic-native/file/ngx';

export type CreateOrganizationFlowData = {
  id?: string;
  organization_type?: number;
  organization_subtype?: string;
  complete_name?: string;
  description?: string;
  sport?: string;
  students_size?: string;
  employees_size?: string;
  website?: string;
  public_url?: string;
  country?: string;
  region?: string;
  region_name?: string;
  province?: string;
  city?: string;
  tagline?: string;
  photo?: string;
  phone?: string;
};

export enum EPagesActions {
  GetUserPages = '[Pages] Get User Pages',
  GetFollowedPages = '[Pages] Get Followed Pages',
  CreatePage = '[Pages] Create Page',
  UpdatePage = '[Pages] Update Page',
  ResetPagesState = '[Pages] Reset Pages State',
  UnfollowPage = '[Pages] Unfollow Page',
  FollowPage = '[Pages] Follow Page',
  GetPageAdministrators = '[Pages] Page Administrator',
  DeletePageAdministrator = '[Pages] Delete Page Administrator',
  CreatePageAdministrator = '[Pages] Create Page Administrator',
  DeletePage = '[Pages] Delete Page',
  UploadPageProfileImage = '[Pages] Upload Page Profile Image',
  SetCurrentPageData = '[Pages] Set Current Page Data',
  UpdateCurrentPageData = '[Pages] Update Current Page Data',
  GetPageFollowers = '[Pages] Get Page Followers',
}

export class GetUserPages {
  static readonly type = EPagesActions.GetUserPages;
  constructor() {}
}

export class GetFollowedPages {
  static readonly type = EPagesActions.GetFollowedPages;
  constructor() {}
}

export class CreatePage {
  static readonly type = EPagesActions.CreatePage;
  constructor(public payload: { page: OrganizationInput }) {}
}

export class UpdatePage {
  static readonly type = EPagesActions.UpdatePage;
  constructor(public payload: { id: string; input: UpdateOrganizationInput }) {}
}

export class ResetPagesState {
  static readonly type = EPagesActions.ResetPagesState;
  constructor() {}
}

export class FollowPageAction {
  static readonly type = EPagesActions.FollowPage;
  constructor(public payload: { organization: IOrganizationInfo }) {}
}

export class UnfollowPageAction {
  static readonly type = EPagesActions.UnfollowPage;
  constructor(public payload: { id: string }) {}
}

export class GetPageAdministrators {
  static readonly type = EPagesActions.GetPageAdministrators;
  constructor(public payload: { id: string }) {}
}

export class DeletePageAdministrator {
  static readonly type = EPagesActions.DeletePageAdministrator;
  constructor(public payload: { organizationId: string; userId: string }) {}
}

export class CreatePageAdministrator {
  static readonly type = EPagesActions.CreatePageAdministrator;
  constructor(public payload: { organizationId: string; user: IUserPublicInfo }) {}
}

export class DeletePage {
  static readonly type = EPagesActions.DeletePage;
  constructor(public payload: { id: string }) {}
}

export class UploadPageProfileImage {
  static readonly type = EPagesActions.UploadPageProfileImage;
  constructor(public payload: { filename: string; file: IFile; type?: string; pageId: string }) {}
}

export class SetCurrentPageData {
  static readonly type = EPagesActions.SetCurrentPageData;
  constructor(public payload: CreateOrganizationFlowData) {}
}

export class UpdateCurrentPageData {
  static readonly type = EPagesActions.UpdateCurrentPageData;
  constructor(public payload: CreateOrganizationFlowData) {}
}

export class GetPageFollowers {
  static readonly type = EPagesActions.GetPageFollowers;
  constructor(public payload: { id: string }) {}
}
