import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Location } from '@angular/common';
import { Select, Store } from '@ngxs/store';
import { BasePage } from '../../../base/base.page';
import { Constants } from '../../../@global/constants';
import { IUserInfo } from '../../../@store/user/user.model';
import { IUserPublicInfo } from '../../../@shared/interfaces/user-public-info';
import { Observable, Subscription } from 'rxjs';
import { UtilsService } from '../../../@core/utils.service';
import { AlertController, ModalController, PopoverController } from '@ionic/angular';
import { UserState } from '../../../@store/user/user.state';
import { GetUserInfo, GetUserProfile } from '../../../@store/user/user.actions';
import {
  GetUserProfilePublicInfo,
  IsConnectedWithUser,
  SetShowProfileNewPostButton,
  SetCurrentProfileId,
  SetProfilesFirstPublicationReference,
} from '../../../@store/profiles/profiles.actions';
import { ProfilesState } from '../../../@store/profiles/profiles.state';
import { Utils } from '../../../@global/utils';
import { PopoverHeaderComponent } from '../../organization/organization-profile/components/popover-header/popover-header.component';
import { UserService } from 'src/app/@core/user.service';
import { TranslateService } from '@ngx-translate/core';
import { DeleteConnectionAction } from '../../../@store/network/network.actions';
import { finalize, take } from 'rxjs/operators';
import { NetworkState } from '../../../@store/network/network.state';
import { ConnectionRequestModel } from '../../../networking/models/connection-request.model';
import { GetRequestsSentAction } from '../../../@store/network/network.actions';
import { IPublicationList } from 'src/app/@shared/interfaces/publication-list';
import { ModalComponent } from './modal/modal.component';
import { NetworkingControllersService } from '../../../networking/services/networking-controllers.service';
import { IActionIcon } from '../../../@shared/components/header/header.component';
import { USER_PROFILE_TAB } from '../../../@shared/enums/user-profile-tab';

export enum USER_CONNECTION_STATUS {
  CONNECTED = 'connected',
  NOT_CONNECTED = 'not_connected',
  PENDING = 'pending',
}

interface TabConfig {
  title: string;
  icon: string;
  tab: USER_PROFILE_TAB;
}

@Component({
  selector: 'app-user-profile',
  templateUrl: './user-profile.page.html',
  styleUrls: ['./user-profile.page.scss'],
})
export class UserProfilePage extends BasePage implements OnInit, OnDestroy {
  @Select(NetworkState.getRequested) userSentList$: Observable<ConnectionRequestModel[]>;
  public profileId: string;
  public imagePathOnboarding = Constants.ONBOARDING_IMAGE_PATH;
  public profilePublicInfo: IUserInfo | IUserPublicInfo;
  private profilePublicInfo$: Subscription;
  private highlightSubscription$: Subscription;
  public userLocation: string;
  public publisherHighlights: IPublicationList;
  public tabsConfig: TabConfig[] = [
    {
      title: 'userProfile.highlights',
      icon: 'icon-highlights',
      tab: USER_PROFILE_TAB.HIGHLIGHTS,
    },
    {
      title: 'userProfile.experience',
      icon: 'icon-experience',
      tab: USER_PROFILE_TAB.EXPERIENCE,
    },
    {
      title: 'userProfile.education',
      icon: 'icon-education',
      tab: USER_PROFILE_TAB.EDUCATION,
    },
    {
      title: 'userProfile.skills',
      icon: 'icon-skills',
      tab: USER_PROFILE_TAB.SKILLS,
    },
  ];
  public sports = 'tenis, fútbol';
  public areas = '';
  public editable: boolean;
  public isConnected: boolean = null;
  public isPending: boolean = null;
  public isPrivate: boolean = null;
  public profileImage: string;
  public currentTab = USER_PROFILE_TAB.HIGHLIGHTS;
  public userProbileTabs = USER_PROFILE_TAB;
  public headerIcon: IActionIcon;
  public hasUserLocation: boolean;
  public showSpinner: boolean;
  public inProcess = false;
  private userLoggedId = '';

  constructor(
    router: Router,
    location: Location,
    store: Store,
    utilsService: UtilsService,
    private route: ActivatedRoute,
    private popoverController: PopoverController,
    private userService: UserService,
    private translateService: TranslateService,
    private alertController: AlertController,
    public modalController: ModalController,
    private networkingService: NetworkingControllersService
  ) {
    super(location, router, store, utilsService);
  }

  ngOnInit(): void {
    this.subscribeToHighlightsChanges();
    this.subscribeToUserInfo();
    this.subscribeToProfilePublicInfo();
    this.subscribeToIsConnectedWithUser();
    this.profileId = this.route.snapshot.params['profileId'];
    this.userLoggedId = this.store.selectSnapshot(UserState.getUserInfo).id;
    if (this.profileId !== this.userLoggedId) {
      // only save visit if its not own profile
      this.visitProfileCall(this.profileId);
    }
  }

  public doIonViewWillEnter() {
    this.profileId = this.route.snapshot.params['profileId'];
    this.editable = this.profileId === this.userLoggedId;
    this.store.dispatch(new SetProfilesFirstPublicationReference(null));
    // own user
    if (this.editable) {
      this.store.dispatch(new GetUserInfo());
      this.store.dispatch(new GetUserProfile({ id: this.userLoggedId }));
      this.headerIcon = { name: 'pencil' };
      this.isConnected = false;
      this.isPending = false;
    } else {
      this.store.dispatch(new GetUserProfilePublicInfo({ id: this.profileId }));
      this.store.dispatch(new IsConnectedWithUser({ id: this.profileId }));

      this.store.dispatch(new GetUserProfile({ id: this.profileId }));
      this.store.dispatch(new GetRequestsSentAction());
    }
    this.loadPublications();

    this.setShowProfileNewPostButton();
    this.store.dispatch(new SetCurrentProfileId(this.profileId));
  }

  private loadPublications() {
    this.showSpinner = true;
    this.utilsService
      .loadPublications(this.profileId)
      .pipe(
        finalize(() => {
          this.showSpinner = false;
        })
      )
      .subscribe();
  }

  private visitProfileCall(profileId) {
    this.userService.visitProfile(profileId);
  }

  private subscribeToHighlightsChanges(): void {
    this.highlightSubscription$ = this.store.select(ProfilesState.getPublisherHighlights).subscribe(async (data) => {
      this.publisherHighlights = data;
    });
  }

  private subscribeToProfilePublicInfo(): void {
    this.profilePublicInfo$ = this.store.select(ProfilesState.getUserPublicInfo).subscribe((data) => {
      if (!data) {
        return;
      }
      this.generateProfileDataToShow(data);
    });
  }

  private subscribeToUserInfo(): void {
    this.profilePublicInfo$ = this.store.select(UserState.getUserInfo).subscribe((data) => {
      if (!data) {
        return;
      }

      this.generateProfileDataToShow(data);
    });
  }

  private subscribeToIsConnectedWithUser(): void {
    this.profilePublicInfo$ = this.store.select(ProfilesState.isConnectedWithUser).subscribe((result) => {
      this.isConnected = result === USER_CONNECTION_STATUS.CONNECTED;
      this.isPending = result === USER_CONNECTION_STATUS.PENDING;
      this.headerIcon = { name: this.isConnected ? 'ellipsis-vertical' : '' };
    });
  }

  private generateProfileDataToShow(data: IUserInfo | IUserPublicInfo): void {
    this.profilePublicInfo = data;

    this.profileImage = this.profilePublicInfo.photo;
    this.hasUserLocation = !!this.profilePublicInfo.country;
    this.areas =
      this.profilePublicInfo.work_area && this.profilePublicInfo.work_area.name
        ? this.profilePublicInfo.work_area.name
        : '';

    if (this.hasUserLocation) {
      this.userLocation = Utils.generateUserFriendlyLocation(
        this.profilePublicInfo.country,
        this.profilePublicInfo.region,
        this.profilePublicInfo.city,
        this.profilePublicInfo.province,
        this.profilePublicInfo.region_name
      );
    }

    this.isPrivate = this.profilePublicInfo.private_profile || false;
  }

  public changeTab(tab: USER_PROFILE_TAB): void {
    this.currentTab = tab;
    this.setShowProfileNewPostButton();
  }

  private setShowProfileNewPostButton(): void {
    if (this.currentTab === USER_PROFILE_TAB.HIGHLIGHTS) {
      this.store.dispatch(new SetShowProfileNewPostButton(this.editable));
    } else {
      this.store.dispatch(new SetShowProfileNewPostButton(false));
    }
  }

  public goEditPage() {
    this.router.navigate(['/edit-user-info']);
  }

  public onHeaderIconClick($event) {
    if (this.editable) {
      this.goEditPage();
    } else {
      this.showPopover($event);
    }
  }

  public async showPopover(params): Promise<void> {
    const items: any[] = [];

    if (this.isConnected) {
      if (
        !Utils.isEmpty(this.profilePublicInfo.contact_email) ||
        !Utils.isEmpty(this.profilePublicInfo.contact_phone)
      ) {
        items.push({
          text: 'contactInfo',
        });
      }
      items.push({
        text: 'deleteContact',
        color: 'danger',
      });
    }

    const itemsLength = items.length;

    const popover = await this.popoverController.create({
      event: params.event,
      component: PopoverHeaderComponent,
      componentProps: { items },
      cssClass: 'menu-popover',
      translucent: true,
    });

    popover.onDidDismiss().then((result) => {
      if (itemsLength === 2 && result.data !== undefined) {
        switch (result.data) {
          case 0:
            this.showContactInfo();
            break;
          case 1:
            this.showDeleteContactConfirm();
            break;
          default:
            console.error('No action');
            break;
        }
      }

      if (itemsLength === 1 && result.data !== undefined) {
        this.showDeleteContactConfirm();
      }
    });

    await popover.present();
  }

  private async showContactInfo() {
    const modal = await this.modalController.create({
      component: ModalComponent,
      componentProps: {
        contact: this.profilePublicInfo,
      },
    });
    return await modal.present();
  }

  private async showDeleteContactConfirm(): Promise<void> {
    const message = await this.translateService
      .get('userProfile.deleteContactMessage', { name: this.profilePublicInfo?.complete_name })
      .toPromise();

    const alert = await this.alertController.create({
      cssClass: 'custom-alert',
      message,
      buttons: [
        {
          text: this.translateService.instant(`buttons.cancel`),
          role: 'cancel',
          cssClass: 'secondary',
          handler: () => {
            // do nothing
          },
        },
        {
          text: this.translateService.instant(`buttons.accept`),
          handler: () => {
            this.deleteContact();
          },
        },
      ],
    });

    await alert.present();
  }

  public deleteContact() {
    this.store
      .dispatch(new DeleteConnectionAction({ id: this.profileId }))
      .pipe(take(1))
      .subscribe(
        () => {
          this.store.dispatch(new IsConnectedWithUser({ id: this.profileId }));
          this.goBack();
        },
        (error) => {
          console.log(error);
        }
      );
  }

  public requestConnection(): void {
    this.networkingService.requestConnectionUpdateList(this.profileId);
  }

  public goChatDetail(): void {
    this.utilsService.goChatDetailWithUserData(this.profilePublicInfo.id, this.profilePublicInfo.complete_name);
  }

  public goUserConnections(connectionsNumber: number): void {
    if (connectionsNumber > 0) {
      this.router.navigate(['connections'], {
        queryParams: { id: this.profileId, completeName: this.profilePublicInfo.complete_name },
      });
    }
  }

  public isLoaded() {
    return this.isPrivate !== null && this.isPending !== null && this.isConnected !== null;
  }

  public showUserPublications() {
    return this.editable || this.isConnected || !this.isPrivate;
  }

  public showPrivateProfile() {
    return !this.editable && !this.isConnected && this.isPrivate;
  }

  public logScrollEnd() {
    this.loadNextPublications();
  }

  private loadNextPublications(): void {
    if (this.inProcess) {
      return;
    }
    this.inProcess = true;
    this.showSpinner = true;
    this.utilsService
      .loadNextPublications(this.publisherHighlights, this.profileId)
      .pipe(
        finalize(() => {
          this.showSpinner = false;
          this.inProcess = false;
        })
      )
      .subscribe();
  }

  private disconnectObservers(): void {
    if (this.profilePublicInfo$) {
      this.profilePublicInfo$.unsubscribe();
    }
    if (this.highlightSubscription$) {
      this.highlightSubscription$.unsubscribe();
    }
  }

  public ionViewWillLeave(): void {
    this.store.dispatch(new SetShowProfileNewPostButton(false));
  }

  ngOnDestroy() {
    this.disconnectObservers();
  }
}
