import { Selector, State, StateContext, Action } from '@ngxs/store';

import { ProfilesStateModel } from './profiles.model';
import {
  GetUserProfilePublicInfo,
  GetOrganizationProfileInfo,
  IsConnectedWithUser,
  ResetProfilesState,
  GetPublisherPublicationList,
  AddPreviousPublicationsToPublisherPublicationList,
  AddNextPublicationsToPublisherPublicationList,
  GetPublisherPublicationListPayload,
  SetOrganizationProfileInfo,
  DeletePublication,
} from './profiles.actions';
import { Injectable } from '@angular/core';
import { catchError, tap, take } from 'rxjs/operators';
import { from, throwError, Observable, EMPTY } from 'rxjs';
import { AsyncCallStart, AsyncCallSuccess, AsyncCallFail } from '../common/common.actions';
import { UserService } from '../../@core/user.service';
import { IOrganizationInfo } from '../../@shared/interfaces/organization-info';
import { PagesService } from '../../@core/pages.service';
import { IUserPublicInfo } from '../../@shared/interfaces/user-public-info';
import { IPublicationList } from '../../@shared/interfaces/publication-list';
import { EGetPublicationsMode } from '../../@shared/enums/get-publications-mode';
import { PublicationService } from '../../@core/publication.service';
import { UtilsService } from '../../@core/utils.service';
import { Utils } from '../../@global/utils';
import { IPublication } from '../../@shared/interfaces/publication';
import {
  SetProfilesFirstPublicationReference,
  SetShowProfileNewPostButton,
  SetCurrentProfileId,
} from './profiles.actions';
import { USER_CONNECTION_STATUS } from '../../profiles/user/user-profile/user-profile.page';
import { GraphqlError } from '../../@core/errors/graphql-error';
import { Constants } from '../../@global/constants';

const initialState: ProfilesStateModel = {
  userPublicInfo: null,
  organizationInfo: null,
  isConnectedWithUser: USER_CONNECTION_STATUS.NOT_CONNECTED,
  publisherHighlights: {
    page: 0,
    total_pages: 0,
    total_count: 0,
    publications: [],
  },
  firstPublicationReference: null,
  showProfileNewPostButton: false,
  currentProfileId: '',
};

@State({
  name: 'profiles',
  defaults: initialState,
})
@Injectable()
export class ProfilesState {
  constructor(
    private userService: UserService,
    private pagesService: PagesService,
    private publicationService: PublicationService,
    private utilsService: UtilsService
  ) {}

  @Selector()
  static getUserPublicInfo(state: ProfilesStateModel): IUserPublicInfo {
    return state.userPublicInfo;
  }

  @Selector()
  static getOrganizationInfo(state: ProfilesStateModel): IOrganizationInfo {
    return state.organizationInfo;
  }

  @Selector()
  static isConnectedWithUser(state: ProfilesStateModel): string {
    return state.isConnectedWithUser;
  }

  @Selector()
  static getPublisherHighlights(state: ProfilesStateModel): IPublicationList {
    return state.publisherHighlights;
  }

  @Selector()
  static getFirstPublicationReference(state: ProfilesStateModel): IPublication {
    return state.firstPublicationReference;
  }

  @Selector()
  static isShowProfileNewPostButton(state: ProfilesStateModel): boolean {
    return state.showProfileNewPostButton;
  }

  @Selector()
  static getCurrentProfileId(state: ProfilesStateModel): string {
    return state.currentProfileId;
  }

  @Action(GetUserProfilePublicInfo)
  getUserProfilePublicInfo(
    { getState, patchState, dispatch }: StateContext<ProfilesStateModel>,
    { payload }: GetUserProfilePublicInfo
  ) {
    dispatch(new AsyncCallStart());

    return from(this.userService.getUserById(payload.id)).pipe(
      take(1),
      tap((response) => {
        dispatch(new AsyncCallSuccess());

        const photo = Utils.mapCloudfrontProfilePhoto(response['data'].getUser.photo);

        const state = getState();

        patchState({
          ...state,
          userPublicInfo: {
            ...response['data'].getUser,
            photo,
          },
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(
          '[getUserProfilePublicInfo]: ' + (err.message || err.errors.length > 0 ? err.errors[0].message : '')
        );
      })
    );
  }

  @Action(SetOrganizationProfileInfo)
  setOrganizationProfileInfo(
    { getState, patchState }: StateContext<ProfilesStateModel>,
    { payload }: SetOrganizationProfileInfo
  ) {
    const state = getState();

    patchState({
      ...state,
      organizationInfo: payload,
    });
  }

  @Action(GetOrganizationProfileInfo)
  getOrganizationProfileInfo(
    { getState, patchState, dispatch }: StateContext<ProfilesStateModel>,
    { payload }: GetOrganizationProfileInfo
  ) {
    dispatch(new AsyncCallStart());

    return from(this.pagesService.getPageById(payload.id)).pipe(
      take(1),
      tap((response) => {
        dispatch(new AsyncCallSuccess());

        const photo = Utils.mapCloudfrontProfilePhoto(response['data'].getOrganization.photo);

        const state = getState();

        patchState({
          ...state,
          organizationInfo: {
            ...response['data'].getOrganization,
            photo,
          },
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new GraphqlError(err));
      })
    );
  }

  @Action(IsConnectedWithUser)
  isConnectedWithUser(
    { getState, patchState, dispatch }: StateContext<ProfilesStateModel>,
    { payload }: IsConnectedWithUser
  ) {
    dispatch(new AsyncCallStart());

    return from(this.userService.isConnectedWithUser(payload.id)).pipe(
      take(1),
      tap((response) => {
        dispatch(new AsyncCallSuccess());

        const state = getState();

        patchState({
          ...state,
          isConnectedWithUser: response['data'].isConnectedWithUser,
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new GraphqlError(err));
      })
    );
  }

  @Action(GetPublisherPublicationList)
  getUserPublicationList(context: StateContext<ProfilesStateModel>, { payload }: GetPublisherPublicationList) {
    return this.getPublicationListFromPublisher(context, payload, EGetPublicationsMode.Replace);
  }

  @Action(AddNextPublicationsToPublisherPublicationList)
  addNextPublicationsToUserPublicationList(
    context: StateContext<ProfilesStateModel>,
    { payload, maxPublicationPages }: AddNextPublicationsToPublisherPublicationList
  ) {
    return this.getPublicationListFromPublisher(context, payload, EGetPublicationsMode.AddNext, maxPublicationPages);
  }

  @Action(AddPreviousPublicationsToPublisherPublicationList)
  addPreviousPublicationsToUserPublicationList(
    context: StateContext<ProfilesStateModel>,
    { payload, maxPublicationPages }: AddPreviousPublicationsToPublisherPublicationList
  ) {
    return this.getPublicationListFromPublisher(
      context,
      payload,
      EGetPublicationsMode.AddPrevious,
      maxPublicationPages
    );
  }

  private getPublicationListFromPublisher(
    { getState, patchState, dispatch }: StateContext<ProfilesStateModel>,
    payload: GetPublisherPublicationListPayload,
    mode: EGetPublicationsMode,
    maxPublicationPages?: number
  ): Observable<IPublicationList> {
    return from(this.publicationService.getPublisherPublications(payload.publisherId, payload.input)).pipe(
      tap((response: any) => {
        const publicationsWithCreationLiteral = this.utilsService.mapPublisherHighlightsPublicationsResponse(
          response.data.getPublisherPublications.publications,
          response.data.getPublisherPublications.page
        );

        const state = getState();

        const publications = Utils.mergePublicationList(
          state.publisherHighlights.publications,
          publicationsWithCreationLiteral,
          mode,
          { pages: maxPublicationPages, limit: payload.input.limit }
        );

        patchState({
          ...state,
          publisherHighlights: {
            page: response.data.getPublisherPublications.page,
            total_count: response.data.getPublisherPublications.total_count,
            total_pages: response.data.getPublisherPublications.total_pages,
            publications,
          },
        });
      }),
      take(1),
      catchError((err) => {
        const state = getState();
        patchState({
          ...state,
          publisherHighlights: {
            page: 0,
            total_pages: 0,
            total_count: 0,
            publications: [],
          },
        });
        dispatch(new AsyncCallFail());
        if (err.errors[0].errorType === Constants.ERROR_PERMISSION) {
          return EMPTY;
        } else {
          return throwError(new GraphqlError(err));
        }
      })
    );
  }

  @Action(SetProfilesFirstPublicationReference)
  setFirstPublicationReference(
    { getState, patchState }: StateContext<ProfilesStateModel>,
    { payload }: SetProfilesFirstPublicationReference
  ): void {
    const state = getState();

    patchState({
      ...state,
      firstPublicationReference: payload,
    });
  }

  @Action(ResetProfilesState)
  resetProfilesState({ setState }: StateContext<ProfilesStateModel>): void {
    setState({
      ...initialState,
    });
  }

  @Action(DeletePublication)
  deletePublication(
    { getState, patchState, dispatch }: StateContext<ProfilesStateModel>,
    { payload }: DeletePublication
  ) {
    dispatch(new AsyncCallStart());
    return from(this.publicationService.deletePublication(payload)).pipe(
      take(1),
      tap(() => {
        dispatch(new AsyncCallSuccess());
        const state = getState();
        const highlights = state.publisherHighlights.publications.filter((p) => p.id !== payload);
        patchState({
          ...state,
          publisherHighlights: {
            ...state.publisherHighlights,
            publications: highlights,
          },
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new GraphqlError(err));
      })
    );
  }

  @Action(SetShowProfileNewPostButton)
  setShowProfileNewPostButton(
    { patchState }: StateContext<ProfilesStateModel>,
    { payload }: SetShowProfileNewPostButton
  ): void {
    patchState({
      showProfileNewPostButton: payload,
    });
  }

  @Action(SetCurrentProfileId)
  setCurrentProfileId({ patchState }: StateContext<ProfilesStateModel>, { payload }: SetCurrentProfileId): void {
    patchState({
      currentProfileId: payload,
    });
  }
}
