import { SIGNUP_FLOW_PAGES } from '../signup-flow/signup-pages';
import { CREATE_ORGANIZATION_FLOW_PAGES } from '../create-organization-flow/create-organization-pages';
import { Constants } from './constants';
import * as dayjs from 'dayjs';
import { cloneDeep, find, isEmpty } from 'lodash';
import { IFile } from '@ionic-native/file/ngx';
import { Observable } from 'rxjs';
import { IPostMedia } from '../create-post/create-post.page';
import { PUBLICATION_TYPE } from '../@shared/enums/publication-type';
import { v4 as uuidv4 } from 'uuid';
import { IPublication } from '../@shared/interfaces/publication';
import { environment } from '@env/environment';
import { OrganizationTypes } from '../create-organization-flow/organization-types';
import { EGetPublicationsMode } from '../@shared/enums/get-publications-mode';
import { SafeUrl } from '@angular/platform-browser';
import { IFriendlyDate } from '../@store/global-info/global-info.model';
import { ChooserResult } from '@ionic-native/chooser/ngx';

export class Utils {
  public static cast<T>(object: any, cl: any): T {
    object.__proto__ = cl.prototype;
    return object;
  }

  public static isSignUpFlowLastPage(pagePosition: number) {
    return pagePosition === SIGNUP_FLOW_PAGES.length - 1;
  }

  public static isCreateOrganizationFlowLastPage(pagePosition) {
    return pagePosition === CREATE_ORGANIZATION_FLOW_PAGES.length - 1;
  }

  public static removeSpecialChars(s) {
    let r = s.toLowerCase();
    const nonAsciis = {
      a: 'á',
      c: 'ç',
      e: 'é',
      i: 'í',
      n: 'ñ',
      o: 'ó',
      u: 'ú',
    };
    // tslint:disable-next-line:forin
    for (const i in nonAsciis) {
      r = r.replace(new RegExp(nonAsciis[i], 'g'), i);
    }
    return r;
  }

  public static getFirstUrlMatch(text: string): string {
    const regEx = new RegExp(Constants.URL_PATTERN);
    const wordList = text.split(/\s+/);
    let link: string;

    for (const word of wordList) {
      if (word.match(regEx)) {
        link = word;
        break;
      }
    }

    if (!link) {
      return '';
    }

    return link.startsWith('http') ? link : `http://${link}`;
  }

  public static getPhoto(imageUrl: string): string {
    return !imageUrl || imageUrl === '' ? Constants.NO_PROFILE_IMAGE_PATH : imageUrl;
  }

  public static mapCloudfrontProfilePhoto(imageUrl: string): string {
    return imageUrl ? this.getPhoto400X400(imageUrl) : Constants.NO_PROFILE_IMAGE_PATH;
  }

  public static mapBrochure(brochurePath: string): string {
    return brochurePath ? environment.cloudfrontBaseUrl + brochurePath : '';
  }

  public static mapCloudfrontCommentPhoto(imageUrl: string): string {
    return imageUrl ? this.getPhoto200X200(imageUrl) : Constants.NO_PROFILE_IMAGE_PATH;
  }

  public static getPhoto400X400(imageUrl: string): string {
    return environment.cloudfrontBaseUrl + imageUrl + Constants.IMAGE_400_X_400_SUFIX;
  }

  public static getPhoto200X200(imageUrl: string): string {
    return environment.cloudfrontBaseUrl + imageUrl + Constants.IMAGE_200_X_200_SUFIX;
  }

  public static copyObject(oldObject) {
    return cloneDeep(oldObject);
  }

  public static isBlank(str) {
    return !str || /^\s*$/.test(str);
  }

  public static getDateMMYY(date): string {
    return dayjs(date).format(Constants.DATE_DISPLAY_MONTH_YEAR_FORMAT);
  }

  public static getDateMMMYY(date): IFriendlyDate {
    if (Utils.isEmpty(date)) {
      return {
        month: '',
        year: '',
      };
    }
    return {
      month: dayjs(date).format('MMM'),
      year: dayjs(date).format('YYYY'),
    };
  }

  public static checkStartDateBeforeFinishDate(startDate: string, endDate: string): boolean {
    try {
      const startDateObj = dayjs(startDate);
      const endDateObj = dayjs(endDate);
      return startDateObj.isBefore(endDateObj);
    } catch (e) {
      return false;
    }
  }

  public static getSpanishFormatDate(date: string) {
    return dayjs(date).format(Constants.DATE_DISPLAY_FORMAT);
  }

  public static getSpanishFormatDateWithHour(date: string) {
    return dayjs(date).format(Constants.DATE_HOUR_DISPLAY_FORMAT);
  }

  // return years from today in the past to set initial value of date
  public static getInitdate(years: number) {
    return dayjs().subtract(years, 'year').format(Constants.ION_DATE_FORMAT);
  }

  public static getTodayDate() {
    return dayjs().format(Constants.ION_DATE_FORMAT);
  }

  public static getISODate(date) {
    if (!Utils.isBlank(date)) {
      return dayjs(date).toISOString();
    }
    return '';
  }

  public static getPathAndFilenameFromUrl(path: string): { path: string; filename: string } {
    return {
      path: path.toString().slice(0, path.lastIndexOf('/') + 1),
      filename: path.slice(path.lastIndexOf('/') + 1, path.length),
    };
  }

  public static makeFileIntoBlob(file: IFile): Observable<Blob> {
    return new Observable<Blob>((observer) => {
      const reader = new FileReader();
      reader.onloadend = (evt: any) => {
        const blob: any = new Blob([evt.target.result], { type: file.type });
        blob.name = file.name;
        observer.next(blob);
        observer.complete();
      };

      reader.onerror = (e) => {
        console.log('Failed file read: ' + e.toString());
        observer.error(e);
      };

      reader.readAsArrayBuffer(file);
    });
  }

  public static isImageMediaType(type: string): boolean {
    return Constants.ALLOWED_IMAGE_TYPES.includes(type);
  }

  public static isVideoMediaType(type: string): boolean {
    return Constants.ALLOWED_VIDEO_TYPES.includes(type);
  }

  public static async checkPostMediaLimits(
    postMedia: IPostMedia,
    duration?: number
  ): Promise<{ isSizeValid: boolean; isDurationValid: boolean }> {
    if (postMedia && (postMedia.type === PUBLICATION_TYPE.LINK || postMedia.type === PUBLICATION_TYPE.TEXT)) {
      return { isSizeValid: true, isDurationValid: true };
    }

    if (!(postMedia && postMedia.file && postMedia.path && postMedia.type)) {
      return { isSizeValid: false, isDurationValid: false };
    }

    const isSizeValid = this.isMediaValidSize(postMedia.file);
    console.log('SIZE IS: ', postMedia.file.size);

    let isDurationValid: boolean;

    if (postMedia.type !== PUBLICATION_TYPE.VIDEO) {
      isDurationValid = true;
    } else {
      const videoDuration = duration || (await this.getVideoDuration(postMedia.path));
      console.log('DURATION IS: ', videoDuration);
      isDurationValid = videoDuration <= Constants.MAX_MEDIA_FILE_DURATION;
    }

    return { isSizeValid, isDurationValid };
  }

  public static isMediaValidSize(file: IFile): boolean {
    return file.size <= Constants.MAX_MEDIA_FILE_SIZE;
  }

  public static getVideoDuration(videoSource: SafeUrl): Promise<number> {
    return new Promise<number>((resolve, reject) => {
      const videoElement = document.createElement('video');
      videoElement.preload = 'metadata';

      // tslint:disable-next-line
      videoElement.onloadedmetadata = function () {
        resolve(videoElement.duration);
        videoElement.remove();
      };

      videoElement.src = videoSource as string;
    });
  }

  public static generateUserFriendlyLocation(country, region, city, province?, regionName?) {
    let result = '';
    if (city && city !== '' && (!province || province.name !== city)) {
      result += city + ',';
    }
    if (province && province.name && (!region || province.name !== region.name)) {
      result += ' ' + province.name + ',';
    }
    if (region && region.name && region.name !== '') {
      result += ' ' + region.name + ',';
    }
    if (regionName && regionName !== '') {
      result += ' ' + regionName + ',';
    }
    if (country && country.name) {
      result += ' ' + country.name;
    }
    let splitted = result.split(',');
    if (splitted.length > 2) {
      // get only the last 2 elements
      splitted = splitted.slice(0, 2);
      result = splitted[0] + ', ' + splitted[1];
    }
    return result;
  }

  public static generateFileUniqueName(file: IFile | ChooserResult): string {
    const filenameSplit = file.name.split('.');
    const extension = filenameSplit[filenameSplit.length - 1];

    return uuidv4() + '-' + Date.now() + '.' + extension;
  }

  public static mergePublicationList(
    publicationList: IPublication[],
    newPublicationList: IPublication[],
    mode: EGetPublicationsMode,
    maxPublicationsData?: { pages: number; limit: number }
  ): IPublication[] {
    let publicationListResult: IPublication[];

    switch (mode) {
      case EGetPublicationsMode.Replace:
        return newPublicationList;
      case EGetPublicationsMode.AddNext:
        publicationListResult = publicationList.concat(newPublicationList);

        if (
          maxPublicationsData &&
          publicationListResult.length > maxPublicationsData.pages * maxPublicationsData.limit
        ) {
          publicationListResult = this.removeFirstPagePublications(publicationListResult);
        }

        return publicationListResult;
      case EGetPublicationsMode.AddPrevious:
        publicationListResult = newPublicationList.concat(publicationList);

        if (
          maxPublicationsData &&
          publicationListResult.length > maxPublicationsData.pages * maxPublicationsData.limit
        ) {
          publicationListResult = this.removeLastPagePublications(publicationListResult);
        }

        return publicationListResult;
    }
  }

  public static removeFirstPagePublications(publicationList: IPublication[]): IPublication[] {
    const pageToRemove = publicationList[0]?.page;
    return publicationList.filter((element) => element.page !== pageToRemove);
  }

  public static removeLastPagePublications(publicationList: IPublication[]): IPublication[] {
    const pageToRemove = publicationList[publicationList.length - 1]?.page;
    return publicationList.filter((element) => element.page !== pageToRemove);
  }

  public static mapObjectPhotoList(userList: any[]): any[] {
    return userList.map((obj: any) => {
      return this.mapObjectPhoto(obj);
    });
  }

  public static mapObjectPhoto(obj: any): any {
    return {
      ...obj,
      photo: Utils.mapCloudfrontProfilePhoto(obj.photo),
    };
  }

  public static isEmpty(obj): boolean {
    return isEmpty(obj);
  }

  public static getSubtypeTranslationPrefix(type: number): string {
    switch (type) {
      case OrganizationTypes.Sport:
        return 'sportOrganization';
      case OrganizationTypes.Academic:
        return 'academicOrganization';
      case OrganizationTypes.Other:
        return 'organizationType';
      default:
        console.error('No type selected');
        break;
    }
  }

  public static getMappedVideoMedia(content: string): string {
    if (!content) {
      return '';
    }

    return environment.cloudfrontVideoBaseUrl + content;
  }

  public static replaceFileExtension(filename: string, extension: string): string {
    const filenameSplit = filename.split('.');
    filenameSplit.pop();

    return filenameSplit + '.' + extension;
  }

  public static isItemOnList(itemsRequests, id): boolean {
    return !!find(itemsRequests, { id });
  }
  public static getBadgeValue(value: number): any {
    return value > Constants.MAX_BADGE ? Constants.MAX_BADGE_VALUE : value;
  }
}
