import { Injectable } from '@angular/core';
import { API, graphqlOperation } from 'aws-amplify';
import { getBasicFilters, getCountryFilters, getVersionsData } from '../../graphql/queries';
import { createUserLegalTerms } from '../../graphql/mutations';

@Injectable({
  providedIn: 'root',
})
export class GlobalInfoService {
  constructor() {}

  public async getLocationData() {
    return API.graphql(graphqlOperation(getCountryFilters));
  }

  public async getSportsData() {
    return API.graphql(graphqlOperation(getBasicFilters, { filters: 'sport' }));
  }

  public async getAcademicDuration() {
    return API.graphql(graphqlOperation(getBasicFilters, { filters: 'academic-duration' }));
  }

  public async getAcademicModality() {
    return API.graphql(graphqlOperation(getBasicFilters, { filters: 'academic-modality' }));
  }

  public async getAcademicOrganization() {
    return API.graphql(graphqlOperation(getBasicFilters, { filters: 'academic-organization-filter' }));
  }

  public async getAcademicType() {
    return API.graphql(graphqlOperation(getBasicFilters, { filters: 'academic-type' }));
  }

  public async getCostOfAttendance() {
    return API.graphql(graphqlOperation(getBasicFilters, { filters: 'cost-of-attendance' }));
  }

  public async getOpportunityType() {
    return API.graphql(graphqlOperation(getBasicFilters, { filters: 'opportunity-type' }));
  }

  public async getOtherOrganizationType() {
    return API.graphql(graphqlOperation(getBasicFilters, { filters: 'other-organization-filter' }));
  }

  public async getSportOrganizationType() {
    return API.graphql(graphqlOperation(getBasicFilters, { filters: 'sport-organization-filter' }));
  }

  public async getStudyArea() {
    return API.graphql(graphqlOperation(getBasicFilters, { filters: 'study-area' }));
  }

  public async getWorkArea() {
    return API.graphql(graphqlOperation(getBasicFilters, { filters: 'work-area' }));
  }

  public async getLanguage() {
    return API.graphql(graphqlOperation(getBasicFilters, { filters: 'language' }));
  }

  public async getEmployeesSize() {
    return API.graphql(graphqlOperation(getBasicFilters, { filters: 'employees-size' }));
  }

  public async getStudentsSize() {
    return API.graphql(graphqlOperation(getBasicFilters, { filters: 'students-size' }));
  }

  public async getVersionsData(): Promise<any> {
    return API.graphql(graphqlOperation(getVersionsData));
  }

  public async createUserLegalTerms(email: string): Promise<any> {
    return API.graphql(graphqlOperation(createUserLegalTerms, { input: { email } }));
  }
}
