import { Injectable } from '@angular/core';
import { Deeplinks } from '@ionic-native/deeplinks/ngx';
import { Router } from '@angular/router';
import { Constants } from '../@global/constants';
import { UserService } from './user.service';
import { ORGANIZATION_PROFILE_TAB } from '../@shared/enums/organization-profile-tab';
import { NavigationExtrasService } from './navigation-extras.service';

@Injectable({
  providedIn: 'root',
})
export class DeepLinksService {
  constructor(
    private deepLinks: Deeplinks,
    private router: Router,
    private userService: UserService,
    private navigationExtrasService: NavigationExtrasService
  ) {}

  public configureDeeplinks() {
    this.deepLinks
      .routeWithNavController(this.router, {
        [`${Constants.DEEP_LINK_PUBLICATION_BASE}:id`]: 'PublicationDetailPage',
        [`${Constants.DEEP_LINK_USER_PROFILE_BASE}:profileId`]: 'UserProfilePage',
        [`${Constants.DEEP_LINK_ORGANIZATION_PROFILE_BASE}:profileId`]: 'OrganizationProfilePage',
        [`${Constants.DEEP_LINK_ORGANIZATION_PROFILE_OPPORTUNITIES_BASE}:profileId`]: 'OrganizationProfilePage',
        [`${Constants.DEEP_LINK_ORGANIZATION_PROFILE_ACADEMICS_BASE}:profileId`]: 'OrganizationProfilePage',
        [Constants.NETWORKING]: 'NetworkingPage',
        [Constants.OPPORTUNITIES]: 'OpportunitiesPage',
        [Constants.ACADEMICS]: 'AcademicsPage',
        [Constants.TYPE_ORGANIZATION]: 'TypeOrganizationPage',
      })
      .subscribe(
        async (match: any) => {
          this.manageDeepLink(match.$link.path);
        },
        async (nomatch: any) => {
          // The url not match, do nothing?
        }
      );
  }

  private manageDeepLink(matchLink: string) {
    const specialDeepLink = Constants.SPECIAL_DEEP_LIKS.find((link) => matchLink.startsWith(link));

    switch (specialDeepLink) {
      case Constants.DEEP_LINK_PUBLICATION_BASE:
        this.managePublicationDeepLink(matchLink);
        break;
      case Constants.DEEP_LINK_ORGANIZATION_PROFILE_OPPORTUNITIES_BASE:
        this.manageOrganizationProfileOpportunitiesDeepLink(matchLink);
        break;
      case Constants.DEEP_LINK_ORGANIZATION_PROFILE_ACADEMICS_BASE:
        this.manageOrganizationProfileAcademicsDeepLink(matchLink);
        break;
      default:
        this.router.navigate([matchLink]);
        break;
    }
  }

  private async managePublicationDeepLink(matchLink: string) {
    const publicationId = matchLink.replace(Constants.DEEP_LINK_PUBLICATION_BASE, '');

    const profilePrivacyStatus = (await this.userService.getPublicationPrivacyStatus(publicationId))['data']
      .getPublicationPrivacyStatus;

    if (profilePrivacyStatus.private_profile && !profilePrivacyStatus.is_connected) {
      this.router.navigate([`/user-profile/${profilePrivacyStatus.publisher_id}`]);
    } else {
      this.router.navigate([matchLink]);
    }
  }

  private async manageOrganizationProfileOpportunitiesDeepLink(matchLink: string) {
    const organizationId = matchLink.replace(Constants.DEEP_LINK_ORGANIZATION_PROFILE_OPPORTUNITIES_BASE, '');

    this.navigationExtrasService.setExtras({ tab: ORGANIZATION_PROFILE_TAB.JOBS });

    this.router.navigate(['organization-profile/' + organizationId]);
  }

  private async manageOrganizationProfileAcademicsDeepLink(matchLink: string) {
    const organizationId = matchLink.replace(Constants.DEEP_LINK_ORGANIZATION_PROFILE_ACADEMICS_BASE, '');

    this.navigationExtrasService.setExtras({ tab: ORGANIZATION_PROFILE_TAB.PROGRAMS });

    this.router.navigate(['organization-profile/' + organizationId]);
  }
}
