export enum EIonSheetActions {
  OpenSheet = '[IonSheet] Open sheet',
  CloseSheet = '[IonSheet] Close sheet',
  SetSheetType = '[IonSheet] Set Sheet Type',
  SetCurrentPublicationId = '[IonSheet] Set Current Publication Id',
}

export class OpenSheetAction {
  static readonly type = EIonSheetActions.OpenSheet;
}

export class CloseSheetAction {
  static readonly type = EIonSheetActions.CloseSheet;
}

export class SetSheetTypeAction {
  static readonly type = EIonSheetActions.SetSheetType;
  constructor(public payload: number) {}
}

export class SetCurrentPublicationIdAction {
  static readonly type = EIonSheetActions.SetCurrentPublicationId;
  constructor(public payload: string) {}
}
