import { Injectable } from '@angular/core';
import { ToastController } from '@ionic/angular';
import { TranslateService } from '@ngx-translate/core';

@Injectable({
  providedIn: 'root',
})
export class InformService {
  private toast: HTMLIonToastElement;

  constructor(private toastCtrl: ToastController, private translate: TranslateService) {}

  async showError(message: string, duration: number = 3000): Promise<HTMLIonToastElement> {
    this.toast = await this.toastCtrl.create({
      message: await this.translate.get(message).toPromise(),
      position: 'bottom',
      mode: 'ios',
      color: 'warning',
      duration,
      animated: true,
      cssClass: 'custom-error-toast',
      buttons: [
        {
          side: 'start',
          icon: 'alert-circle-outline',
        },
      ],
    });

    await this.toast.present();

    return this.toast;
  }

  async showWarning(message: string, duration: number = 3000): Promise<HTMLIonToastElement> {
    this.toast = await this.toastCtrl.create({
      message: await this.translate.get(message).toPromise(),
      position: 'bottom',
      color: 'warning',
      mode: 'ios',
      duration,
      animated: true,
      cssClass: 'custom-warning-toast',
      buttons: [
        {
          side: 'start',
          icon: 'information-circle-outline',
        },
      ],
    });

    await this.toast.present();

    return this.toast;
  }

  async showInfo(
    message: string,
    duration: number = 6000,
    position: 'bottom' | 'top' | 'middle' = 'top'
  ): Promise<HTMLIonToastElement> {
    this.toast = await this.toastCtrl.create({
      message: await this.translate.get(message).toPromise(),
      position,
      mode: 'ios',
      duration,
      animated: true,
      cssClass: 'custom-info-toast',
      buttons: [
        {
          side: 'start',
          icon: 'bulb-outline',
        },
        {
          text: 'OK',
          role: 'cancel',
          handler: () => {
            this.hideToast();
          },
        },
      ],
    });

    await this.toast.present();

    return this.toast;
  }

  hideToast() {
    this.toast.dismiss();
  }
}
