import { Injectable } from '@angular/core';
import { API, graphqlOperation } from 'aws-amplify';

import { createPublication } from '../../graphql/mutations';
import { PublicationInput } from '../../API';
import { AwsService } from './aws.service';

@Injectable({
  providedIn: 'root',
})
export class PostsService {
  constructor(private awsService: AwsService) {}

  // tslint:disable-next-line:variable-name
  public async createPost(publisher_id: string, input: PublicationInput) {
    return API.graphql(graphqlOperation(createPublication, { publisher_id, input }));
  }
}
