import { Component, OnDestroy, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { ActionSheetController, MenuController, ModalController, Platform } from '@ionic/angular';
import { StatusBar } from '@ionic-native/status-bar/ngx';
import { Keyboard } from '@ionic-native/keyboard/ngx';
import { TranslateService } from '@ngx-translate/core';

import { environment } from '@env/environment';
import { Event, NavigationEnd, Router } from '@angular/router';
import { Store, Select } from '@ngxs/store';
import { StorageService } from './@core/storage.service';
import { AuthState } from './@store/auth/auth.state';
import { AnalyticsService } from './@core/analytics.service';
import { Constants } from './@global/constants';
import { Observable, Subscription } from 'rxjs';
import { PagesState } from './@store/pages/pages.state';
import { InformService } from './@core/inform.service';
import { CommonState } from './@store/common/common.state';
import { SentryService } from './@core/errors/sentry.service';
import { IUserInfo } from './@store/user/user.model';
import { UserState } from './@store/user/user.state';
import { SplashScreen } from '@ionic-native/splash-screen/ngx';
import { UtilsService } from './@core/utils.service';
import { SetAnalyticsCookies, SetRootPage } from './@store/common/common.actions';
import { MobileAccessibility } from '@ionic-native/mobile-accessibility/ngx';
import { PushService } from './@core/push.service';
import { IOrganizationInfo } from './@shared/interfaces/organization-info';
import { GoogleAnalyticsService } from './@core/google-analytics.service';
import { SetCanEnterCreatePageFlow } from './@store/navigation/navigation.actions';
import { CREATE_ORGANIZATION_FLOW_PAGES } from './create-organization-flow/create-organization-pages';
import { DeepLinksService } from './@core/deep-links.service';
import { AddComment, ChangeCommentLikeStatus } from './@store/timeline/timeline.actions';
import { IComment } from './@shared/interfaces/comment';
import { TimelineState } from './@store/timeline/timeline.state';
import { ProfilesState } from './@store/profiles/profiles.state';

const SHOW_EXIT_TOAST_TIME = 2000;

interface NavigatorCordova extends Navigator {
  app: {
    exitApp: () => any;
  };
}

@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html',
  styleUrls: ['app.component.scss'],
})
export class AppComponent implements OnInit, OnDestroy {
  @Select(ProfilesState.isShowProfileNewPostButton) isShowProfileNewPostButton$: Observable<boolean>;

  public userInfo: IUserInfo;
  public profileImage: string;
  public userPageList$: Observable<IOrganizationInfo[]>;
  public asyncCallCount$: Observable<number>;
  private isFirstExitAttempt: boolean;
  private getUserInfoSubscription: Subscription;
  private routerSubscription: Subscription;
  private backButtonSubscription: Subscription;
  public showHighLightsPostButton$: boolean;

  public prodMode = true;

  constructor(
    private platform: Platform,
    private statusBar: StatusBar,
    private translate: TranslateService,
    private router: Router,
    private utilsService: UtilsService,
    private store: Store,
    private analyticsService: AnalyticsService,
    private menuCtrl: MenuController,
    private location: Location,
    private keyboard: Keyboard,
    private informService: InformService,
    private sentryService: SentryService,
    private actionSheetController: ActionSheetController,
    private splashScreen: SplashScreen,
    private storage: StorageService,
    private mobileAccessibility: MobileAccessibility,
    private pushService: PushService,
    private deeplinksService: DeepLinksService,
    private googleAnalyticsService: GoogleAnalyticsService,
    private modalController: ModalController
  ) {
    this.prodMode = environment.production;

    // override default console log error to avoid console messages in prod
    if (this.prodMode) {
      const noop = () => {};
      ['debug', 'dir', 'error', 'info', 'log', 'trace', 'warn'].forEach((method) => {
        window.console[method] = noop;
      });
    }

    this.googleAnalyticsService.init();
    this.initializeApp();
  }

  async ngOnInit() {
    this.getUserInfoSubscription = this.store.select(UserState.getUserInfo).subscribe((data) => {
      this.userInfo = data;
      this.profileImage = this.userInfo?.photo;
      if (data && data.push_notifications) {
        this.pushService.activatePush();
        this.pushService.setExternalUserId(data.id);
      } else if (data && !data.push_notifications) {
        this.pushService.desactivatePush();
        this.pushService.setExternalUserId('null');
      }
      if (!data) {
        this.initTranslate();
      } else {
        this.setAppLanguage(this.userInfo?.language);
      }
    });
    this.userPageList$ = this.store.select(PagesState.getUserPageList);
    this.subscribeToRouterEvents();
    this.subscribeToAsyncCallCount();
    const cookiesLocalData = await this.storage.getCookiesData();
    if (cookiesLocalData) {
      this.store.dispatch(new SetAnalyticsCookies(cookiesLocalData.cookies.analytics));
    }
  }

  private initializeApp() {
    this.platform.ready().then(async () => {
      this.deeplinksService.configureDeeplinks();
      this.configureStatusBar();
      this.initTranslate();
      this.sentryService.initialize();
      if (this.platform.is('cordova')) {
        this.subscribeToBackButton();
        setTimeout(() => {
          this.splashScreen.hide();
        }, 1000);
        await this.checkZoom();
      }
    });
  }

  private async checkZoom() {
    const zoom = await this.mobileAccessibility.getTextZoom();
    if (zoom > Constants.MAX_ZOOM_ALLOWED) {
      this.mobileAccessibility.setTextZoom(Constants.MAX_ZOOM_ALLOWED);
    }
  }

  private configureStatusBar(): void {
    const statusBarColor = getComputedStyle(document.documentElement).getPropertyValue('--ion-color-primary').trim();

    this.statusBar.overlaysWebView(false);
    this.statusBar.styleDefault();
    this.statusBar.backgroundColorByHexString(statusBarColor);
  }

  private async initTranslate() {
    const userLang = await this.storage.getLanguage();
    await this.setAppLanguage(userLang);
  }

  private async setAppLanguage(userLang) {
    const isSupportedLanguage = environment.supportedLanguages.includes(userLang);

    if (!isSupportedLanguage) {
      const browserLanguage = this.translate.getBrowserLang();
      userLang = environment.supportedLanguages.includes(browserLanguage)
        ? browserLanguage
        : environment.defaultLanguage;
    }

    console.log(`Language set to ${userLang}`);
    await this.utilsService.setLanguage(userLang);
  }

  private subscribeToRouterEvents(): void {
    this.routerSubscription = this.router.events.subscribe((event: Event) => {
      if (event instanceof NavigationEnd) {
        this.sendNavigationEventToAnalytics(event);
        this.checkRootPage(event.urlAfterRedirects);
        this.checkMenuEnable(event.urlAfterRedirects);
      }
    });
  }

  private subscribeToAsyncCallCount(): void {
    this.asyncCallCount$ = this.store.select(CommonState.getAsyncCallCount);
  }

  private subscribeToBackButton(): void {
    this.backButtonSubscription = this.platform.backButton.subscribeWithPriority(9999, async () => {
      if (this.isFirstExitAttempt) {
        (navigator as NavigatorCordova).app.exitApp();
      }

      if (this.keyboard.isVisible) {
        this.keyboard.hide();
        return;
      }

      if (await this.menuCtrl.isOpen()) {
        this.menuCtrl.close();
        return;
      }

      const modal = await this.modalController.getTop();
      if (modal) {
        modal.dismiss();
        return;
      }

      const actionSheet = await this.actionSheetController.getTop();
      if (actionSheet) {
        actionSheet.dismiss();
        return;
      }

      if (this.store.selectSnapshot(CommonState.isRootPage)) {
        const toast = await this.informService.showWarning('exitApp', SHOW_EXIT_TOAST_TIME);

        this.isFirstExitAttempt = true;

        toast.onDidDismiss().then(() => {
          this.isFirstExitAttempt = false;
        });

        return;
      }

      this.location.back();
    });
  }

  public async navigateFromMenu(url: string, profileId?: any): Promise<void> {
    // check the menu is opened before navigate. If the user clicks
    // fast before menu is completely opened an error will occur
    if (await this.menuCtrl.isOpen()) {
      const navigationSegments = [url];

      if (profileId) {
        navigationSegments.push(profileId);
      }

      this.router.navigate(navigationSegments);
      this.menuCtrl.close();
    }
  }

  public async navigateToCreatePageFlow(): Promise<void> {
    // check the menu is opened before navigate. If the user clicks
    // fast before menu is completely opened an error will occur
    if (await this.menuCtrl.isOpen()) {
      this.store.dispatch(new SetCanEnterCreatePageFlow(true));
      this.router.navigateByUrl(CREATE_ORGANIZATION_FLOW_PAGES[0]);
      this.menuCtrl.close();
    }
  }

  private sendNavigationEventToAnalytics(event: NavigationEnd): void {
    // pinpoint
    const attributes = { ...event, userId: this.store.selectSnapshot(AuthState.getUserId) };
    this.analyticsService.record(Constants.APP_NAVIGATION_EVENT, attributes);

    // google analytics
    this.googleAnalyticsService.pageEnter(event.urlAfterRedirects);
  }

  private checkRootPage(url: string): void {
    this.store.dispatch(new SetRootPage(this.utilsService.isRootPage(url)));
  }

  private checkMenuEnable(url: string): void {
    this.menuCtrl.enable(this.utilsService.isMenuPage(url));
  }

  public onNewPostClick(): void {
    const profileId = this.store.selectSnapshot(ProfilesState.getCurrentProfileId);
    this.router.navigate(['/create-post', profileId]);
  }

  ngOnDestroy() {
    if (this.getUserInfoSubscription) {
      this.getUserInfoSubscription.unsubscribe();
    }

    if (this.routerSubscription) {
      this.routerSubscription.unsubscribe();
    }

    if (this.backButtonSubscription) {
      this.backButtonSubscription.unsubscribe();
    }
  }
}
