import { PublicationInput } from '../../../API';
import { IFile } from '@ionic-native/file/ngx';
import { VIDEO_POSITION } from '../../@shared/enums/video-position';

export interface IUploadPostMediaPayload {
  filename: string;
  file: IFile;
  type?: string;
  bucket?: string;
  metadata?: any;
  mediaWidth?: number;
  mediaHeight?: number;
  pageId?: string;
}

export interface IUploadMediaMetadataFilePayload {
  filename: string;
  srcVideo: string;
  rotate?: boolean;
  position?: VIDEO_POSITION;
  bucket?: string;
  credentials?: {
    accessKeyId: string;
    secretAccessKey: string;
    sessionToken: string;
  };
}

export enum EPostsActions {
  CreatePost = '[Posts] Create Post',
  UploadPostImage = '[Posts] Upload Post Image',
  UploadPagePostImage = '[Posts] Upload Page Post Image',
  UploadPostVideo = '[Posts] Upload Post Video',
  UploadPagePostVideo = '[Posts] Upload Page Post Video',
  UploadPostVideoMetadataFile = '[Posts] Upload Post Video Metadata File',
  UploadPagePostVideoMetadataFile = '[Posts] Upload Page Post Video Metadata File',
  SetFocusOnPublication = '[Posts] Set Focus On Publication',
  ResetPostsState = '[Posts] Reset Posts State',
}

export class CreatePost {
  static readonly type = EPostsActions.CreatePost;
  constructor(public payload: { publisher_id: string; input: PublicationInput }) {}
}

export class UploadPostImage {
  static readonly type = EPostsActions.UploadPostImage;
  constructor(public payload: IUploadPostMediaPayload) {}
}

export class UploadPagePostImage {
  static readonly type = EPostsActions.UploadPagePostImage;
  constructor(public payload: IUploadPostMediaPayload) {}
}

export class UploadPostVideo {
  static readonly type = EPostsActions.UploadPostVideo;
  constructor(public payload: IUploadPostMediaPayload) {}
}

export class UploadPagePostVideo {
  static readonly type = EPostsActions.UploadPagePostVideo;
  constructor(public payload: IUploadPostMediaPayload) {}
}

export class UploadPostVideoMetadataFile {
  static readonly type = EPostsActions.UploadPostVideoMetadataFile;
  constructor(public payload: IUploadMediaMetadataFilePayload) {}
}

export class UploadPagePostVideoMetadataFile {
  static readonly type = EPostsActions.UploadPagePostVideoMetadataFile;
  constructor(public payload: IUploadMediaMetadataFilePayload) {}
}

export class SetFocusOnPublication {
  static readonly type = EPostsActions.SetFocusOnPublication;
  constructor(public payload: boolean) {}
}

export class ResetPostsState {
  static readonly type = EPostsActions.ResetPostsState;
  constructor() {}
}
