import { Injectable } from '@angular/core';
import { Storage } from '@ionic/storage';
import { Constants } from '../@global/constants';
import { CredentialsModel } from '../login/model/credentials';
import { EncoderService } from './encoder.service';
import { SignupInfoModel } from '../signup-flow/signup-info.model';
import { OrganizationInput } from '../../API';
import { ICookies } from '../@shared/interfaces/cookies';

@Injectable({
  providedIn: 'root',
})
export class StorageService {
  constructor(private storage: Storage, private encoder: EncoderService) {}

  public getLanguage(): Promise<string> {
    return this.storage.get(Constants.LANGUAGE_LOCAL_KEY);
  }

  public setLanguage(language: string): Promise<void> {
    return this.storage.set(Constants.LANGUAGE_LOCAL_KEY, language);
  }

  public getAvoidOnboardingPages(): Promise<boolean> {
    return this.storage.get(Constants.JUMP_TO_LOGIN_KEY);
  }

  public setAvoidOnboardingPages(login: boolean): Promise<void> {
    return this.storage.set(Constants.JUMP_TO_LOGIN_KEY, login);
  }

  public getRememberUser(): Promise<boolean> {
    return this.storage.get(Constants.REMEMBER_USER_KEY);
  }

  public setRememberUser(value: boolean): Promise<void> {
    return this.storage.set(Constants.REMEMBER_USER_KEY, value);
  }

  public getSavedUser(): Promise<string> {
    return this.storage.get(Constants.EMAIL_KEY);
  }

  public setUser(email: string): Promise<void> {
    return this.storage.set(Constants.EMAIL_KEY, email);
  }

  private async getSavedPass(): Promise<string> {
    const encodedValue = await this.storage.get(Constants.PASS_KEY);
    return this.encoder.getEncrypt(encodedValue);
  }

  public async getCredentials(): Promise<CredentialsModel> {
    const email = await this.getSavedUser();
    const pass = await this.getSavedPass();
    return email && pass ? new CredentialsModel(email, pass) : null;
  }

  public async setCredentials(credentials: CredentialsModel): Promise<void> {
    if (credentials) {
      await this.storage.set(Constants.EMAIL_KEY, credentials.email);
      await this.storage.set(Constants.PASS_KEY, this.encoder.setEncrypt(credentials.password));
    } else {
      await this.storage.remove(Constants.EMAIL_KEY);
      await this.storage.remove(Constants.PASS_KEY);
    }
  }

  public async setCompleteSignupPage(pageName: string) {
    await this.storage.set(Constants.LAST_SIGNUP_PAGE, pageName);
  }

  public async removeSignupInProgress() {
    await this.storage.remove(Constants.LAST_SIGNUP_PAGE);
  }

  public async getLastCompleteSignupPage() {
    return this.storage.get(Constants.LAST_SIGNUP_PAGE);
  }

  public async removeSigupInProgessUser() {
    await this.storage.remove(Constants.SIGNUP_INFO);
  }

  public async getSigupInProgessUser() {
    const retrievedObject = await this.storage.get(Constants.SIGNUP_INFO);
    if (retrievedObject) {
      return (JSON.parse(retrievedObject) as SignupInfoModel).email;
    }
    return null;
  }

  public setNewOrganizationData(object: OrganizationInput) {
    return this.storage.set(Constants.NEW_ORGANIZATION_INFO, JSON.stringify(object));
  }

  public setSignUpData(object: SignupInfoModel) {
    return this.storage.set(Constants.SIGNUP_INFO, JSON.stringify(object));
  }

  public async getSignupData(): Promise<SignupInfoModel> {
    const retrievedObject = await this.storage.get(Constants.SIGNUP_INFO);
    if (retrievedObject) {
      return JSON.parse(retrievedObject) as SignupInfoModel;
    }
    return {} as SignupInfoModel;
  }

  public async removeSignupDataField(fieldName: string): Promise<void> {
    const retrievedObject = await this.storage.get(Constants.SIGNUP_INFO);
    if (retrievedObject) {
      const data = JSON.parse(retrievedObject) as SignupInfoModel;
      delete data[fieldName];

      await this.setSignUpData(data);
    }
  }

  public setCookiesData(object: ICookies) {
    return this.storage.set(Constants.COOKIES, JSON.stringify(object));
  }

  public async getCookiesData(): Promise<ICookies> {
    const retrievedObject = await this.storage.get(Constants.COOKIES);
    if (retrievedObject) {
      return JSON.parse(retrievedObject) as ICookies;
    }
    return null;
  }
}
