import { Injectable } from '@angular/core';
import { Analytics } from 'aws-amplify';
import { Store } from '@ngxs/store';
import { CommonState } from '../@store/common/common.state';

@Injectable({
  providedIn: 'root',
})
export class AnalyticsService {
  constructor(private store: Store) {}

  public record(name: string, attributes?: any): Promise<any> {
    if (this.store.selectSnapshot(CommonState.getAnalyticsCookies)) {
      return Analytics.record({ name, attributes }).then(
        () => {
          console.log('[AnalyticsService] record success');
          console.log(name, attributes || '');
        },
        (error) => {
          console.log('[AnalyticsService] record error');
          console.log(error);
        }
      );
    }
  }
}
