import { Injectable } from '@angular/core';
import { Auth, Storage } from 'aws-amplify';
import { Observable } from 'rxjs';
import * as AWS from 'aws-sdk';
import { ICredentials } from '@aws-amplify/core';
import { CognitoUserSession } from 'amazon-cognito-identity-js';
import { Body } from 'aws-sdk/clients/s3';
import { environment } from '@env/environment';
import { Constants } from '../@global/constants';

AWS.config.region = environment.awsRegion;
AWS.config.apiVersions = {
  cognitoidentity: '2014-06-30',
};

export type confirmSignUpPayload = { username: string; code: string };
export type changePasswordPayload = { user: any; oldPassword: string; newPassword: string };

export type signInPayload = { username: string; password: string };

export type uploadFileToS3Payload = {
  filename: string;
  body: Body;
  type?: string;
  level?: string;
  bucket?: string;
  metadata?: any;
  credentials?: { accessKeyId: string; secretAccessKey: string; sessionToken: string };
};

export type forgotPasswordPayload = { username: string };
export type forgotPasswordSubmitPayload = { username: string; code: string; newPassword: string };

@Injectable({
  providedIn: 'root',
})
export class AwsService {
  constructor() {}

  public signIn({ username, password }: signInPayload): Observable<{ userId: string; token: string }> {
    return new Observable((subscriber) => {
      Auth.signIn(username, password).then(
        (response) => {
          subscriber.next({ userId: response.attributes.sub, token: response.signInUserSession.idToken.jwtToken });
          subscriber.complete();
        },
        (error) => {
          subscriber.error(error);
        }
      );
    });
  }

  public getCurrentUserCredentials(): Observable<ICredentials> {
    return new Observable((subscriber) => {
      Auth.currentCredentials().then(
        (credentials) => {
          subscriber.next(credentials);
          subscriber.complete();
        },
        (error) => {
          subscriber.error(error);
        }
      );
    });
  }

  public getCurrentUserInfo(): Observable<any> {
    return new Observable((subscriber) => {
      Auth.currentUserInfo().then(
        (userInfo) => {
          if (!userInfo.id) {
            subscriber.error('User does not exist');
            return;
          }
          subscriber.next(userInfo);
          subscriber.complete();
        },
        (error) => {
          subscriber.error(error);
        }
      );
    });
  }

  public getCurrentSession(): Observable<CognitoUserSession> {
    return new Observable((subscriber) => {
      Auth.currentSession().then(
        (session) => {
          subscriber.next(session);
          subscriber.complete();
        },
        (error) => {
          subscriber.error(error);
        }
      );
    });
  }

  public getCurrentAuthenticatedUser(): Observable<any> {
    return new Observable((subscriber) => {
      Auth.currentAuthenticatedUser().then(
        (user) => {
          subscriber.next(user);
          subscriber.complete();
        },
        (error) => {
          subscriber.error(error);
        }
      );
    });
  }

  public signUp({ username, password }: signInPayload): Observable<{ user: any; userConfirmed: any; userSub: any }> {
    return new Observable((subscriber) => {
      Auth.signUp(username, password).then(
        (response) => {
          subscriber.next({
            user: response.user,
            userConfirmed: response.userConfirmed,
            userSub: response.userSub,
          });
          subscriber.complete();
        },
        (error) => {
          subscriber.error(error);
        }
      );
    });
  }

  public confirmSignUp({
    username,
    code,
  }: confirmSignUpPayload): Observable<{ user: any; userConfirmed: any; userSub: any }> {
    return new Observable((subscriber) => {
      Auth.confirmSignUp(username, code).then(
        (response) => {
          subscriber.next({
            user: response.user,
            userConfirmed: response.userConfirmed,
            userSub: response.userSub,
          });
          subscriber.complete();
        },
        (error) => {
          subscriber.error(error);
        }
      );
    });
  }

  public signOut(): Observable<any> {
    return new Observable((subscriber) => {
      Auth.signOut().then(
        () => {
          subscriber.next();
          subscriber.complete();
        },
        (error) => {
          subscriber.error(error);
        }
      );
    });
  }

  public changePassword({ user, oldPassword, newPassword }: changePasswordPayload): Observable<any> {
    return new Observable((subscriber) => {
      Auth.changePassword(user, oldPassword, newPassword).then(
        (result) => {
          subscriber.next(result);
          subscriber.complete();
        },
        (error) => {
          subscriber.error(error);
        }
      );
    });
  }

  public forgotPassword({ username }: forgotPasswordPayload): Observable<any> {
    return new Observable((subscriber) => {
      Auth.forgotPassword(username).then(
        () => {
          subscriber.next();
          subscriber.complete();
        },
        (error) => {
          subscriber.error(error);
        }
      );
    });
  }

  public forgotPasswordSubmit({ username, code, newPassword }: forgotPasswordSubmitPayload): Observable<any> {
    return new Observable((subscriber) => {
      Auth.forgotPasswordSubmit(username, code, newPassword).then(
        (result) => {
          subscriber.next(result);
          subscriber.complete();
        },
        (error) => {
          subscriber.error(error);
        }
      );
    });
  }

  public resendVerificationCode({ username }: forgotPasswordPayload): Observable<any> {
    return new Observable((subscriber) => {
      Auth.resendSignUp(username).then(
        (result) => {
          subscriber.next(result);
          subscriber.complete();
        },
        (error) => {
          subscriber.error(error);
        }
      );
    });
  }

  public async uploadFileToS3({ filename, body, type, level, bucket, metadata }: uploadFileToS3Payload): Promise<any> {
    const config = { contentType: type, level: level || Constants.PUBLIC_LEVEL };

    if (bucket) {
      config['bucket'] = bucket;
    }

    if (metadata) {
      config['metadata'] = metadata;
    }

    return await Storage.put(filename, body, config);
  }

  public async uploadFileToS3Manual({
    filename,
    body,
    type,
    credentials,
    bucket,
    metadata,
  }: uploadFileToS3Payload): Promise<any> {
    const params = {
      Body: body,
      Bucket: bucket,
      Key: filename,
      ContentType: type,
      Metadata: metadata,
    };

    return await this.putObjectToS3(params, credentials);
  }

  private putObjectToS3(params: any, credentials: any): Promise<any> {
    return new Promise<any>((resolve, reject) => {
      AWS.config.update({
        region: environment.awsRegion,
        credentials: new AWS.Credentials(
          credentials.accessKeyId,
          credentials.secretAccessKey,
          credentials.sessionToken
        ),
      });

      const s3 = new AWS.S3({ apiVersion: '2006-03-01' });

      s3.putObject(params, function (err, data) {
        if (err) {
          console.log(err, err.stack);
          reject(err);
        } else {
          console.log(data);
          resolve(data);
        }
      });
    });
  }
}
