import { AfterViewInit, Component, OnDestroy, OnInit } from '@angular/core';
import { IElement } from '../../../@store/global-info/global-info.model';
import { Select, Store } from '@ngxs/store';
import { GlobalInfoState } from '../../../@store/global-info/global-info.state';
import { Observable, Subscription } from 'rxjs';
import { GetSportsInfo } from '../../../@store/global-info/global-info.actions';
import { ModalController } from '@ionic/angular';

interface ISportModalData {
  id: string;
  name: string;
  selected: boolean;
}

@Component({
  selector: 'app-sports-modal',
  templateUrl: './sports-modal.component.html',
  styleUrls: ['./sports-modal.component.scss'],
})
export class SportsModalComponent implements OnInit, OnDestroy, AfterViewInit {
  @Select(GlobalInfoState.getSports) sports$: Observable<IElement[]>;
  private subscription$: Subscription;
  public sportsList: ISportModalData[];
  public sportsSelected: IElement[] = [];

  constructor(private store: Store, private modalController: ModalController) {}

  ngAfterViewInit() {
    setTimeout(() => {
      if (!this.sportsList || this.sportsList?.length === 0) {
        this.store.dispatch(new GetSportsInfo());
      }
    });
  }

  ngOnInit() {
    this.subscribeToSports();
  }

  private subscribeToSports(): void {
    this.subscription$ = this.sports$.subscribe((data) => {
      if (!data || this.sportsList?.length > 0) {
        return;
      }
      this.sportsList = this.generateModalData(data);
    });
  }

  ngOnDestroy(): void {
    this.disconnectObservers();
  }

  private disconnectObservers(): void {
    this.subscription$.unsubscribe();
  }

  public async goBack() {
    await this.modalController.dismiss();
  }

  public generateModalData(sportList: IElement[]): ISportModalData[] {
    const data: ISportModalData[] = [];

    for (let i = 0; i < sportList.length; i++) {
      data.push({
        ...sportList[i],
        selected: this.isSelected(sportList[i].id),
      });
    }

    return data;
  }

  public isSelected(sportId: string): boolean {
    return !!this.sportsSelected.find((element) => element.id === sportId);
  }

  public async save() {
    const data = this.sportsList.filter((element) => {
      if (element.selected) {
        return { id: element.id, name: element.name };
      }
    });
    await this.modalController.dismiss(data);
  }
}
