import { Selector, State, StateContext, Action } from '@ngxs/store';
import { Injectable } from '@angular/core';
import { catchError, mergeMap, tap } from 'rxjs/operators';
import { throwError } from 'rxjs';

import { AuthStateModel } from './auth.model';
import {
  ChangePassword,
  ConfirmSignUp,
  ForgotPassword,
  ForgotPasswordSubmit,
  ChangeUserPassword,
  SignIn,
  SignOut,
  SignUp,
  ResendVerificationCode,
} from './auth.actions';

import { AWSError } from '../../@core/errors/aws-error';
import { AuthService } from '../../@core/auth.service';
import { AsyncCallStart, AsyncCallSuccess, AsyncCallFail } from '../common/common.actions';
import { SetAutoLoginData, GetIdentityId, ResetAuthState } from './auth.actions';
import { ResetNetworkState } from '../network/network.actions';
import { ResetPagesState } from '../pages/pages.actions';
import { ResetPostsState } from '../posts/posts.actions';
import { ResetPreferencesState } from '../preferences/preferences.actions';
import { ResetProfilesState } from '../profiles/profiles.actions';
import { ResetTimelineState } from '../timeline/timeline.actions';
import { ResetUserState } from '../user/user.actions';
import { ResetNotificationsState } from '../notifications/notifications.actions';
import { ResetJobOffersState } from '../job-offers/job-offers.actions';
import { ResetAcademicProgramsState } from '../academic-programs/academic-programs.actions';
import { ResetChatsState } from '../chats/chats.actions';

const initialState = {
  userId: '',
  token: '',
  identityId: '',
};

@State({
  name: 'auth',
  defaults: initialState,
})
@Injectable()
export class AuthState {
  constructor(private authService: AuthService) {}

  @Selector()
  static getUserId(state: AuthStateModel): string {
    return state.userId;
  }

  @Selector()
  static getToken(state: AuthStateModel): string {
    return state.token;
  }

  @Selector()
  static getIdentityId(state: AuthStateModel): string {
    return state.identityId;
  }

  @Action(SignIn)
  signIn({ getState, patchState, dispatch }: StateContext<AuthStateModel>, { payload }: SignIn) {
    dispatch(new AsyncCallStart());

    return this.authService.signIn(payload).pipe(
      tap((data: { token: string; userId: string }) => {
        dispatch(new AsyncCallSuccess());

        const state = getState();

        patchState({
          ...state,
          userId: data.userId,
          token: data.token,
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new AWSError(err.message, err.code, 'signIn'));
      })
    );
  }

  @Action(SignUp)
  signUp({ getState, patchState, dispatch }: StateContext<AuthStateModel>, { payload }: SignUp) {
    dispatch(new AsyncCallStart());

    return this.authService.signUp(payload).pipe(
      tap(() => {
        dispatch(new AsyncCallSuccess());

        const state = getState();

        patchState({
          ...state,
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new AWSError(err.message, err.code, 'signUp'));
      })
    );
  }

  @Action(ConfirmSignUp)
  confirmSignUp({ getState, patchState, dispatch }: StateContext<AuthStateModel>, { payload }: ConfirmSignUp) {
    dispatch(new AsyncCallStart());

    return this.authService.confirmSignUp(payload).pipe(
      tap(() => {
        dispatch(new AsyncCallSuccess());

        const state = getState();

        patchState({
          ...state,
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new AWSError(err.message, err.code, 'confirmSignUp'));
      })
    );
  }

  @Action(SignOut)
  signOut({ dispatch }: StateContext<AuthStateModel>, {}: SignOut) {
    dispatch(new AsyncCallStart());

    return this.authService.signOut().pipe(
      tap(() => {
        dispatch(new AsyncCallSuccess());
        dispatch(new ResetAuthState());
        dispatch(new ResetNetworkState());
        dispatch(new ResetPagesState());
        dispatch(new ResetPostsState());
        dispatch(new ResetPreferencesState());
        dispatch(new ResetProfilesState());
        dispatch(new ResetTimelineState());
        dispatch(new ResetUserState());
        dispatch(new ResetNotificationsState());
        dispatch(new ResetJobOffersState());
        dispatch(new ResetAcademicProgramsState());
        dispatch(new ResetChatsState());
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new AWSError(err.message, err.code, 'signOut'));
      })
    );
  }

  @Action(ChangeUserPassword)
  changeUserPassword({ dispatch, getState, patchState }: StateContext<AuthStateModel>, { payload }: ChangePassword) {
    dispatch(new AsyncCallStart());

    return this.authService.getCurrentAuthenticatedUser().pipe(
      mergeMap((user) => {
        dispatch(new AsyncCallSuccess());
        return dispatch(
          new ChangePassword({ user, oldPassword: payload.oldPassword, newPassword: payload.newPassword })
        );
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new AWSError(err.message, err.code, 'changeUserPassword'));
      })
    );
  }

  @Action(ChangePassword)
  changePassword({ getState, patchState, dispatch }: StateContext<AuthStateModel>, { payload }: ChangePassword) {
    dispatch(new AsyncCallStart());

    return this.authService.changePassword(payload).pipe(
      tap(() => {
        dispatch(new AsyncCallSuccess());

        const state = getState();

        patchState({
          ...state,
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new AWSError(err.message, err.code, 'changePassword'));
      })
    );
  }

  @Action(ForgotPassword)
  forgotPassword({ patchState, getState, dispatch }: StateContext<AuthStateModel>, { payload }: ForgotPassword) {
    dispatch(new AsyncCallStart());

    return this.authService.forgotPassword(payload).pipe(
      tap(() => {
        dispatch(new AsyncCallSuccess());

        const state = getState();

        patchState({
          ...state,
          token: '',
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new AWSError(err.message, err.code, 'forgotPassword'));
      })
    );
  }

  @Action(ForgotPasswordSubmit)
  forgotPasswordSubmit(
    { getState, patchState, dispatch }: StateContext<AuthStateModel>,
    { payload }: ForgotPasswordSubmit
  ) {
    dispatch(new AsyncCallStart());

    return this.authService.forgotPasswordSubmit(payload).pipe(
      tap(() => {
        dispatch(new AsyncCallSuccess());

        const state = getState();

        patchState({
          ...state,
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new AWSError(err.message, err.code, 'forgotPasswordSubmit'));
      })
    );
  }

  @Action(ResendVerificationCode)
  resendVerificationCode(
    { getState, patchState, dispatch }: StateContext<AuthStateModel>,
    { payload }: ResendVerificationCode
  ) {
    dispatch(new AsyncCallStart());

    return this.authService.resendVerificationCode(payload).pipe(
      tap(() => {
        dispatch(new AsyncCallSuccess());

        const state = getState();

        patchState({
          ...state,
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new AWSError(err.message, err.code, 'resendVerificationCode'));
      })
    );
  }

  @Action(GetIdentityId)
  getIdentityId({ getState, patchState, dispatch }: StateContext<AuthStateModel>) {
    dispatch(new AsyncCallStart());

    return this.authService.getCurrentUserCredentials().pipe(
      tap((credentials) => {
        dispatch(new AsyncCallSuccess());

        const state = getState();

        patchState({
          ...state,
          identityId: credentials.identityId,
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new AWSError(err.message, err.code, 'getIdentityId'));
      })
    );
  }

  @Action(SetAutoLoginData)
  setAutoLoginData({ getState, patchState }: StateContext<AuthStateModel>, { payload }: SetAutoLoginData) {
    const state = getState();

    patchState({
      ...state,
      ...payload,
    });
  }

  @Action(ResetAuthState)
  resetAuthState({ setState }: StateContext<AuthStateModel>): void {
    setState({
      ...initialState,
    });
  }
}
