import {
  ChangeDetectionStrategy,
  Component,
  EventEmitter,
  Input,
  OnChanges,
  OnInit,
  Output,
  SimpleChanges,
} from '@angular/core';
import { Utils } from '../../../@global/utils';

export interface IActionIcon {
  name: string;
  badge?: any;
}

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class HeaderComponent implements OnInit, OnChanges {
  @Input() showBackButton: boolean;
  @Input() menuImage: string;
  @Input() title: string;
  @Input() titleImage: string;
  @Input() actionButton: string;
  @Input() actionIconList: IActionIcon[] = [];

  @Output() actionButtonClick = new EventEmitter<void>();
  @Output() actionIconListClick = new EventEmitter<any>();
  @Output() backButtonClick = new EventEmitter<void>();

  public userImage: string;

  constructor() {}

  ngOnInit() {}

  ngOnChanges(changes: SimpleChanges) {
    if (changes.menuImage) {
      this.userImage = Utils.getPhoto(changes.menuImage.currentValue);
    }
  }

  public onActionButtonClick(): void {
    this.actionButtonClick.emit();
  }

  public onActionIconListClick($event, index: number): void {
    this.actionIconListClick.emit({
      event: $event,
      index,
    });
  }

  public onBackButtonClick(): void {
    this.backButtonClick.emit();
  }
}
