import { Component, Input, Output, EventEmitter, OnInit, SimpleChanges, OnChanges } from '@angular/core';

@Component({
  selector: 'app-icon-clicked',
  templateUrl: './icon-clicked.component.html',
  styleUrls: ['./icon-clicked.component.scss'],
})
export class IconClickedComponent implements OnInit, OnChanges {
  @Input() iconNameInitial: string;
  @Input() iconNameTapped: string;
  @Output() buttonClick = new EventEmitter<string>();

  public iconToShow: string;

  constructor() {}

  ngOnInit() {
    this.iconToShow = this.iconNameInitial;
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes['iconNameInitial']?.currentValue) {
      this.iconToShow = this.iconNameInitial;
    }
  }

  eventTap(event: any) {
    event.stopPropagation();
    this.buttonClick.emit(this.iconToShow);
    this.iconToShow = this.iconToShow === this.iconNameInitial ? this.iconNameTapped : this.iconNameInitial;
  }
}
