import { Action, Selector, State, StateContext, Store } from '@ngxs/store';
import { Injectable } from '@angular/core';
import { Utils } from '../../@global/utils';
import { IJobOffer, JobOffersStateModel } from './job-offers.model';
import {
  AddJobOfferFavourite,
  CreateJobOfferAction,
  DeleteJobOfferAction,
  DeleteJobOfferFavourite,
  GetJobOffersFavourites,
  GetJobOffersRequested,
  GetJobOffersSuggestions,
  GetOrganizationJobOffersAction,
  RequestEmailJobOffer,
  SetCurrentJobOffer,
} from './job-offers.actions';
import { AsyncCallFail, AsyncCallStart, AsyncCallSuccess } from '../common/common.actions';
import { catchError, tap, mergeMap } from 'rxjs/operators';
import { EMPTY, from, throwError, Observable, of } from 'rxjs';
import { UtilsService } from 'src/app/@core/utils.service';
import { JobOffersService } from '../../@core/job-offers.service';
import { GraphqlError } from '../../@core/errors/graphql-error';
import { ResetJobOffersState } from './job-offers.actions';
import { Constants } from 'src/app/@global/constants';
import { AwsService } from '../../@core/aws.service';
import { ChooserResult } from '@ionic-native/chooser/ngx';
import { environment } from '../../../environments/environment';
import { AuthState } from '../auth/auth.state';

const initialState: JobOffersStateModel = {
  jobOffersSuggestions: [],
  jobOffersFavourites: [],
  jobOffersRequested: [],
  jobOffersOrganization: [],
  currentJobOffer: null,
};

@State({
  name: 'jobOffers',
  defaults: initialState,
})
@Injectable()
export class JobOffersState {
  constructor(
    private jobOffersService: JobOffersService,
    private utils: UtilsService,
    private awsService: AwsService,
    private store: Store
  ) {}

  @Selector()
  public static getJobOffers(state: JobOffersStateModel): IJobOffer[] {
    return state.jobOffersOrganization;
  }

  @Selector()
  public static getJobOffersSuggestions(state: JobOffersStateModel): IJobOffer[] {
    return state.jobOffersSuggestions;
  }

  @Selector()
  public static getJobOffersFavourites(state: JobOffersStateModel): IJobOffer[] {
    return state.jobOffersFavourites;
  }

  @Selector()
  public static getJobOffersRequested(state: JobOffersStateModel): IJobOffer[] {
    return state.jobOffersRequested;
  }

  @Selector()
  public static getCurrentJobOffer(state: JobOffersStateModel): IJobOffer {
    return state.currentJobOffer;
  }

  @Action(GetOrganizationJobOffersAction)
  public getJobOffers(
    { getState, patchState, dispatch }: StateContext<JobOffersStateModel>,
    { id }: GetOrganizationJobOffersAction
  ) {
    dispatch(new AsyncCallStart());

    return from(this.jobOffersService.getOrganizationJobOffers(id)).pipe(
      tap((response: any) => {
        dispatch(new AsyncCallSuccess());

        const state = getState();

        patchState({
          ...state,
          jobOffersOrganization: this.mapJobOffers(response.data.getOrganizationJobOffers),
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new GraphqlError(err));
      })
    );
  }

  @Action(CreateJobOfferAction)
  public createJobOffer(
    { getState, patchState, dispatch }: StateContext<JobOffersStateModel>,
    { organizationId, input }: CreateJobOfferAction
  ) {
    dispatch(new AsyncCallStart());

    return from(this.jobOffersService.createJobOffer(organizationId, input)).pipe(
      tap((response: any) => {
        dispatch(new AsyncCallSuccess());

        const state = getState();

        patchState({
          ...state,
          jobOffersOrganization: [this.mapOfferItem(response.data.createJobOffer), ...state.jobOffersOrganization],
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new GraphqlError(err));
      })
    );
  }

  @Action(DeleteJobOfferAction)
  public deleteJobOffer(
    { getState, patchState, dispatch }: StateContext<JobOffersStateModel>,
    { id }: DeleteJobOfferAction
  ) {
    dispatch(new AsyncCallStart());

    return from(this.jobOffersService.deleteJobOffer(id)).pipe(
      tap(() => {
        dispatch(new AsyncCallSuccess());

        const state = getState();

        const objIndex = state.jobOffersOrganization.findIndex((item) => item.id === id);

        const updatedObjects = [
          ...state.jobOffersOrganization.slice(0, objIndex),
          ...state.jobOffersOrganization.slice(objIndex + 1),
        ];

        patchState({
          ...state,
          jobOffersOrganization: updatedObjects,
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new GraphqlError(err));
      })
    );
  }

  @Action(GetJobOffersSuggestions)
  public getJobOffersSuggestions({ getState, patchState, dispatch }: StateContext<JobOffersStateModel>) {
    dispatch(new AsyncCallStart());

    return from(this.jobOffersService.getJobOffersSuggestions()).pipe(
      tap((response: any) => {
        dispatch(new AsyncCallSuccess());

        const suggestions = this.mapJobOffers(response.data.getJobOfferSuggestions);

        const state = getState();

        patchState({
          ...state,
          jobOffersSuggestions: suggestions,
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new GraphqlError(err));
      })
    );
  }

  @Action(GetJobOffersFavourites)
  public getJobOffersFavourites({ getState, patchState, dispatch }: StateContext<JobOffersStateModel>) {
    dispatch(new AsyncCallStart());
    return from(this.jobOffersService.getJobOffersFavourites()).pipe(
      tap((response: any) => {
        dispatch(new AsyncCallSuccess());

        const favourites = this.mapJobOffers(response.data.getUserFavouriteJobOffers);

        const state = getState();

        patchState({
          ...state,
          jobOffersFavourites: favourites,
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new GraphqlError(err));
      })
    );
  }

  @Action(GetJobOffersRequested)
  public getJobOffersRequested({ getState, patchState, dispatch }: StateContext<JobOffersStateModel>) {
    dispatch(new AsyncCallStart());
    return from(this.jobOffersService.getJobOffersRequested()).pipe(
      tap((response: any) => {
        dispatch(new AsyncCallSuccess());

        const requested = this.mapJobOffers(response.data.getUserRequestedJobOffers);

        const state = getState();

        patchState({
          ...state,
          jobOffersRequested: requested,
        });
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError(new GraphqlError(err));
      })
    );
  }

  @Action(DeleteJobOfferFavourite)
  public deleteJobOfferFavourite(
    { getState, patchState, dispatch }: StateContext<JobOffersStateModel>,
    { payload }: DeleteJobOfferFavourite
  ) {
    const state = getState();
    const favourites = state.jobOffersFavourites.filter((job) => job.id !== payload);

    patchState({
      ...state,
      jobOffersFavourites: favourites,
    });
    return from(this.jobOffersService.deleteJobOfferFavourite(payload)).pipe(
      tap(() => {
        // do nothing
      }),
      catchError((err) => {
        console.log('[DeleteJobOfferFavourite]: ' + err.errors[0].message);
        return EMPTY;
      })
    );
  }

  @Action(AddJobOfferFavourite)
  public addJobOfferFavourite(
    { getState, patchState, dispatch }: StateContext<JobOffersStateModel>,
    { payload }: AddJobOfferFavourite
  ) {
    const state = getState();

    patchState({
      ...state,
      jobOffersFavourites: [payload, ...state.jobOffersFavourites],
    });
    return from(this.jobOffersService.createJobOfferFavourite(payload.id)).pipe(
      tap(() => {
        // do nothing
      }),
      catchError((err) => {
        console.log('[AddJobOfferFavourite]: ' + err.errors[0].message);
        return EMPTY;
      })
    );
  }

  @Action(SetCurrentJobOffer)
  public setCurrentJobOffer(
    { getState, patchState }: StateContext<JobOffersStateModel>,
    { payload }: SetCurrentJobOffer
  ) {
    const state = getState();

    patchState({
      ...state,
      currentJobOffer: payload,
    });
  }

  @Action(RequestEmailJobOffer)
  public requestEmailJobOffer(
    { getState, patchState, dispatch }: StateContext<JobOffersStateModel>,
    { payload }: RequestEmailJobOffer
  ) {
    dispatch(new AsyncCallStart());

    let resumeObservable: Observable<any>;

    if (payload.resume) {
      const filenameToUpload =
        Constants.OPPORTUNITY_REQUEST_RESUMES_FOLDER + '/' + Utils.generateFileUniqueName(payload.resume);
      payload.input.resume =
        Constants.PROTECTED_FOLDER + '/' + this.store.selectSnapshot(AuthState.getIdentityId) + '/' + filenameToUpload;

      resumeObservable = this.uploadResumeFile(payload.resume, filenameToUpload);
    } else {
      resumeObservable = of({});
    }

    return resumeObservable.pipe(
      mergeMap((result) => {
        return from(this.jobOffersService.createJobOfferEmailRequest(payload.id, payload.input)).pipe(
          tap(() => {
            dispatch(new AsyncCallSuccess());
            const state = getState();

            patchState({
              ...state,
              jobOffersRequested: [...state.jobOffersRequested, payload.jobOffer],
            });
          })
        );
      }),
      tap(() => {
        dispatch(new AsyncCallSuccess());
      }),
      catchError((err) => {
        dispatch(new AsyncCallFail());
        return throwError('[RequestEmailJobOffer]: ' + err.message);
      })
    );
  }

  @Action(ResetJobOffersState)
  resetJobOffersState({ setState }: StateContext<ResetJobOffersState>): void {
    setState({
      ...initialState,
    });
  }

  private mapJobOffers(jobOffers: any): IJobOffer[] {
    if (jobOffers) {
      return jobOffers.map((offer: any) => {
        return this.mapOfferItem(offer);
      });
    }
    return [];
  }

  private mapOfferItem(offer) {
    return {
      ...offer,
      photo: Utils.mapCloudfrontProfilePhoto(offer.photo),
      location: Utils.generateUserFriendlyLocation(
        offer.country,
        offer.region,
        offer.city,
        offer.province,
        offer.region_name
      ),
      creation_date_literal: this.utils.calculateCreatedLiteral(Date.now(), offer.creation_date),
    };
  }

  private uploadResumeFile(file: ChooserResult, filenameToUpload: string) {
    return from(
      this.awsService.uploadFileToS3({
        filename: filenameToUpload,
        body: file.data,
        type: file.mediaType,
        level: Constants.PROTECTED_LEVEL,
        bucket: environment.mediaBucket,
      })
    );
  }
}
