import { Injectable } from '@angular/core';
import { API, graphqlOperation } from 'aws-amplify';
import {
  getAcademicProgramsSuggestions,
  getOrganizationAcademicPrograms,
  getUserFavouriteAcademicPrograms,
  getUserRequestedAcademicPrograms,
} from 'src/graphql/queries';
import { OfferRequestInput, AcademicProgramInput } from '../../API';
import {
  createAcademicProgram,
  createAcademicProgramEmailRequest,
  createAcademicProgramFavourite,
  deleteAcademicProgram,
  updateAcademicProgram,
} from '../../graphql/mutations';
import { deleteAcademicProgramFavourite } from 'src/graphql/mutations';

@Injectable({
  providedIn: 'root',
})
export class AcademicProgramsService {
  constructor() {}

  public async createAcademicProgram(organizationId: string, input: AcademicProgramInput) {
    return API.graphql(graphqlOperation(createAcademicProgram, { organizationId, input }));
  }

  public async getAcademicProgramsSuggestions() {
    return API.graphql(graphqlOperation(getAcademicProgramsSuggestions, {}));
  }

  public async getAcademicProgramsFavourites() {
    return API.graphql(graphqlOperation(getUserFavouriteAcademicPrograms, {}));
  }

  public async getAcademicProgramsRequested() {
    return API.graphql(graphqlOperation(getUserRequestedAcademicPrograms, {}));
  }

  public async deleteAcademicProgramFavourite(id: string) {
    return API.graphql(graphqlOperation(deleteAcademicProgramFavourite, { id }));
  }

  public async createAcademicProgramFavourite(id: string) {
    return API.graphql(graphqlOperation(createAcademicProgramFavourite, { id }));
  }

  public async getOrganizationAcademicPrograms(id: string) {
    return API.graphql(graphqlOperation(getOrganizationAcademicPrograms, { id }));
  }

  public async deleteAcademicProgram(id: string) {
    return API.graphql(graphqlOperation(deleteAcademicProgram, { id }));
  }

  public async createAcademicProgramEmailRequest(id: string, input: OfferRequestInput) {
    return API.graphql(graphqlOperation(createAcademicProgramEmailRequest, { id, input }));
  }

  public async updateAcademicProgram(id: string, input: AcademicProgramInput) {
    return API.graphql(graphqlOperation(updateAcademicProgram, { id, input }));
  }
}
