import { HttpClient, HttpParams, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

import { environment } from '@env/environment';

@Injectable({
  providedIn: 'root',
})
export class ApiService {
  constructor(private http: HttpClient) {}

  private getUrl(endpoint: string): string {
    return environment.baseUrl + endpoint;
  }

  public get(endpoint: string, headers?: HttpHeaders, params?: HttpParams): Observable<any> {
    return this.http.get(this.getUrl(endpoint), { headers, params });
  }

  public post(endpoint: string, body: any, headers?: HttpHeaders): Observable<any> {
    return this.http.post(this.getUrl(endpoint), body, { headers });
  }

  public put(endpoint: string, body: any, headers?: HttpHeaders): Observable<any> {
    return this.http.put(this.getUrl(endpoint), body, { headers });
  }

  public delete(endpoint: string, headers?: HttpHeaders): Observable<any> {
    return this.http.delete(this.getUrl(endpoint), { headers });
  }

  public patch(endpoint: string, body: any, headers?: HttpHeaders): Observable<any> {
    return this.http.patch(this.getUrl(endpoint), body, { headers });
  }
}
