import { Injectable } from '@angular/core';

/**
 * Use this service before navigation to pass data between pages
 */
@Injectable({
  providedIn: 'root',
})
export class NavigationExtrasService {
  private extras: any;

  constructor() {}

  public setExtras(extras: any) {
    this.extras = extras;
  }

  public getExtras() {
    return this.extras;
  }
}
