import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { IJobOffer } from '../../../@store/job-offers/job-offers.model';

@Component({
  selector: 'app-opportunity-item',
  templateUrl: './opportunity-item.component.html',
  styleUrls: ['./opportunity-item.component.scss'],
})
export class OpportunityItemComponent implements OnInit {
  @Input() info: IJobOffer;
  @Input() hasPhoto: boolean;
  @Input() editable: boolean;
  @Input() linkText: string;
  @Input() iconNameInitial: string;
  @Input() iconNameTapped: string;
  @Output() itemClick = new EventEmitter<string>();
  @Output() iconClick = new EventEmitter<string>();
  constructor() {}

  ngOnInit() {}

  public onItemClick(): void {
    this.itemClick.emit(this.info.id);
  }

  onButtonClick() {
    this.iconClick.emit(this.info.id);
  }
}
