import { AcademicExperienceInput, ProfessionalExperienceInput, SkillInput } from '../../../API';
import { IFile } from '@ionic-native/file/ngx';

export enum EUserActions {
  GetUserInfo = '[User] Get User Info',
  SetUserInfo = '[User] Set User Info',
  UploadProfileImage = '[User] Upload Profile Image',
  createProfessionalExperience = '[User] Create Professional Experience',
  updateProfessionalExperience = '[User] Update Professional Experience',
  deleteProfessionalExperience = '[User] Delete Professional Experience',
  createAcademicExperience = '[User] Create Academic Experience',
  updateAcademicExperience = '[User] Update Academic Experience',
  deleteAcademicExperience = '[User] Delete Academic Experience',
  createSkillExperience = '[User] Create Skill Experience',
  deleteSkillExperience = '[User] Delete Skill Experience',
  getUserProfile = '[User] Get User Profile',
  UpdatePushNotificationsStatus = '[User] Update push notifications status',
  UpdateEmailNotificationsStatus = '[User] Update email notifications status',
  ResetUserState = '[User] Reset User State',
}

export class GetUserProfile {
  static readonly type = EUserActions.getUserProfile;
  constructor(public payload: { id: string }) {}
}

export class CreateProfessionalExperience {
  static readonly type = EUserActions.createProfessionalExperience;
  constructor(public payload: { input: ProfessionalExperienceInput }) {}
}

export class UpdateProfessionalExperience {
  static readonly type = EUserActions.updateProfessionalExperience;
  constructor(public payload: { id: string; input: ProfessionalExperienceInput }) {}
}

export class DeleteProfessionalExperience {
  static readonly type = EUserActions.deleteProfessionalExperience;
  constructor(public payload: { id: string }) {}
}

export class CreateAcademicExperience {
  static readonly type = EUserActions.createAcademicExperience;
  constructor(public payload: { input: AcademicExperienceInput }) {}
}

export class UpdateAcademicExperience {
  static readonly type = EUserActions.updateAcademicExperience;
  constructor(public payload: { id: string; input: AcademicExperienceInput }) {}
}

export class DeleteAcademicExperience {
  static readonly type = EUserActions.deleteAcademicExperience;
  constructor(public payload: { id: string }) {}
}

export class CreateSkillExperience {
  static readonly type = EUserActions.createSkillExperience;
  constructor(public payload: { input: SkillInput }) {}
}

export class DeleteSkillExperience {
  static readonly type = EUserActions.deleteSkillExperience;
  constructor(public payload: { id: string }) {}
}

export class GetUserInfo {
  static readonly type = EUserActions.GetUserInfo;
  constructor() {}
}

export class SetUserInfo {
  static readonly type = EUserActions.SetUserInfo;
  constructor(public payload: { userInfo: any }) {}
}

export class UploadProfileImage {
  static readonly type = EUserActions.UploadProfileImage;
  constructor(public payload: { filename: string; file: IFile; type?: string }) {}
}

export class UpdatePushNotificationsStatus {
  static readonly type = EUserActions.UpdatePushNotificationsStatus;
  constructor(public payload: boolean) {}
}

export class UpdateEmailNotificationsStatus {
  static readonly type = EUserActions.UpdateEmailNotificationsStatus;
  constructor(public payload: boolean) {}
}

export class ResetUserState {
  static readonly type = EUserActions.ResetUserState;
  constructor() {}
}
