import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { IAcademicProgram } from '../../../@store/academic-programs/academic-programs.model';

@Component({
  selector: 'app-academic-item',
  templateUrl: './academic-item.component.html',
  styleUrls: ['./academic-item.component.scss'],
})
export class AcademicItemComponent implements OnInit {
  @Input() program: IAcademicProgram;
  @Input() iconNameInitial: string;
  @Input() iconNameTapped: string;
  @Input() linkText: string;
  @Input() hasPhoto: boolean;
  @Input() editable: boolean;
  @Output() itemClick = new EventEmitter<string>();
  @Output() iconClick = new EventEmitter<string>();

  constructor() {}

  ngOnInit() {}

  public onItemClick(): void {
    this.itemClick.emit(this.program.id);
  }

  onButtonClick() {
    this.iconClick.emit(this.program.id);
  }
}
